<template>
    <div class="point">
        
    </div>
</template>

<script>
	export default {
	    data() {
	        return {
                topPopup: false,
                marker:null
            }
        },
	    props: {
	        map: {
	            type: Object,
                default() {
	                return null
                }
            },
            isRed: {
	            type: Boolean,
                default: false
            },
            id: {
	            type: String
            },
            position: {
	            type: Object,
                default() {
	                return {}
                }
            }
        },
        mounted() {
            this._initPoint()
        },
        methods: {
	        _initPoint() {
                let that = this;
                that.marker = new AMap.Marker({
                    position: new AMap.LngLat(this.position.lng,this.position.lat) ,
                    content: that.isRed ? '<img class="red-icon" ' +
                        'src="../static/red-icon.png"/>' : null,
//                    offset: that.isRed ? new AMap.Pixel(-12,-12) : new AMap.Pixel(-12,-12) ,
                    map: this.map
                });
                that.marker.on('click',() => {
                    that.$emit('pointclick',{
                        isRed: that.isRed,
                        id: that.id,
                    })
                })
            },
            removeMarker(){
                this.marker.setMap(null)
            }
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    .red-icon
        width: 24px
        height: 24px
</style>



