<template>
    <div class="create-asset-wrapper">
        <v-header :path="fromUrl" title="添加资产" style="z-index: 9999999"></v-header>
        <div class="get" @click="Sub">提交</div>
        <scroller style="position: absolute;top: 50px;margin-bottom: 20px">
        <mt-field class="border-1px" label="物品名" placeholder="请输入物品名" v-model="name"></mt-field>
        <mt-field class="border-1px" label="价格" placeholder="请输入价格" v-model="price"></mt-field>
        <mt-field class="border-1px" label="添加人" placeholder="请输入添加人" v-model="people"></mt-field>
        <mt-field class="border-1px" label="编号" placeholder="请输入编号" v-model="num"></mt-field>
        <mt-field class="border-1px" label="数量" placeholder="请输入数量" v-model="quantity"></mt-field>

        <mt-cell title="单位" :value="leaveValueNum" @click.native="popupVisibleNum=true" is-link></mt-cell>

        <mt-cell title="物品使用状态" :value="leaveValue" @click.native="popupVisible=true" is-link></mt-cell>
        <mt-cell title="公司资产类型" :value="leaveValueType" @click.native="popupVisibleType=true" is-link></mt-cell>
        <mt-cell title="公司店铺" :value="leaveValueShop" @click.native="popupVisibleShop=true" is-link></mt-cell>
        <mt-cell title="物品状态" :value="leaveValueGood" @click.native="popupVisibleGood=true" is-link></mt-cell>

            <img-upload title="上传物品照片" ref="imgs"></img-upload>
        </scroller>

        <mt-popup
                v-model="popupVisibleNum"
                position="bottom">
                <mu-picker :slots="slotsNum" :visible-item-count="3" @change="onValuesChangeNum" :values="valuesNum"/>
            </mt-popup>

        <mt-popup
            v-model="popupVisible"
            position="bottom">
            <mu-picker :slots="slots" :visible-item-count="3" @change="onValuesChange" :values="values"/>
        </mt-popup>

        <mt-popup
            v-model="popupVisibleType"
            position="bottom">
            <mu-picker :slots="slotsType" :visible-item-count="3" @change="onValuesChangeType" :values="valuesType"/>
        </mt-popup>

        <mt-popup
            v-model="popupVisibleShop"
            position="bottom">
            <mu-picker :slots="slotsShop" :visible-item-count="3" @change="onValuesChangeShop" :values="valuesShop"/>
        </mt-popup>

        <mt-popup
            v-model="popupVisibleGood"
            position="bottom">
            <mu-picker :slots="slotsGood" :visible-item-count="3" @change="onValuesChangeGood" :values="valuesGood"/>
        </mt-popup>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import ImgUpload from 'components/img-upload/img-upload';
    import {API_ASSET_ADD,API_COM_STATE,API_COM_TYPE,API_ALL_COM,API_COMP_LIST} from 'common/js/api';
    import {mapMutations} from 'vuex';
    import qs from 'qs';
    import axios from 'axios';
    import { Indicator, Toast } from 'mint-ui';

    export default {
        data() {
            return {
                fromUrl: '',
                token:'',
                id:'',
                name:'',
                price:'',
                people:'',
                assetPicPath:[],
                num:'',
                quantity:'',

                dictType:'',
                Nums:[],
                leaveValueNum:'请选择（必选）',
                popupVisibleNum:false,
                valuesNum:[],
                slotsNum:[
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],

                assetStatus:'',
                valuesGood: [],
                leaveValueGood:'请选择（必选）',
                popupVisibleGood:false,
                slotsGood:[
                    {
                        flex: 1,
                        values: ['上架','下架'],
                        textAlign: 'center'
                    }
                ],

                idShop:[],
                officeId:'',
                valuesShop: [],
                leaveValueShop:'请选择（必选）',
                popupVisibleShop:false,
                slotsShop:[
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],

                idType:[],
                assetTypeId:'',
                valuesType: [],
                leaveValueType:'请选择（必选）',
                popupVisibleType:false,
                slotsType:[
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],

                assetUseStatus:'',
                ids:[],
                leaveValue:'请选择（必选）',
                popupVisible: false,
                values: [],
                slots: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
	    components: {
	        VHeader,
            ImgUpload
        },
        created(){
            this.token=this.$login.getToken();
            this.id=this.$route.params.id;
            this._request();
            this._requestType();
            this._requestAll();
            this._requestNum();
        },
        methods:{
            async _requestNum(){
                let res=await axios.post(API_COMP_LIST,qs.stringify({
                    utoken:this.token,
                    dictType:'unit'
                }));
                let data=res.data;
                console.log(data);
                this.Nums=data.list;
                let arr=[];
                for(var i=0;i<this.Nums.length;i++){
                    arr.push(this.Nums[i].name);
                }
                this.slotsNum[0].values=arr;
            },
            async _request(){
                let res=await axios.post(API_COM_STATE,qs.stringify({
                    utoken:this.token,
                    dictType:'use_status'
                }));
                let data=res.data;
                console.log(data.list);
                this.ids=data.list;
                var arr=[];
                for(var i=0;i<data.list.length;i++){
                    arr.push(data.list[i].name);
                }
                this.slots[0].values=arr;
            },
            async _requestType(){
                let res=await axios.post(API_COM_TYPE,qs.stringify({
                    utoken:this.token,
                    officeId:this.id
                }));
                let data=res.data;
                console.log(data);
                this.idType=data.list;
                var arrType=[];
                for(var i=0;i<data.list.length;i++){
                    arrType.push(data.list[i].name);
                }
                this.slotsType[0].values=arrType;
            },
            async _requestAll(){
                let res=await axios.post(API_ALL_COM,qs.stringify({
                    utoken:this.token,
                    officeId:this.id
                }));
                let data=res.data;
                console.log(data);
                this.idShop=data.list;
                this.setShopList(data.list);
                var arrAll=[];
                for(var i=0;i<data.list.length;i++){
                    arrAll.push(data.list[i].name);
                }
                this.slotsShop[0].values=arrAll;
            },
            ...mapMutations({
                'setShopList':"SET_SHOP_LIST"
            }),
            onValuesChange(a) {
                this.leaveValue = a;
                this.values = [a];
                for(var i=0;i<this.ids.length;i++){
                    if(this.values[0]===this.ids[i].name){
                        this.assetUseStatus=this.ids[i].id;
                    }
                }
            },
            onValuesChangeType(b){
                this.leaveValueType = b;
                this.valuesType = [b];
                for(var i=0;i<this.idType.length;i++){
                    if(this.valuesType[0]===this.idType[i].name){
                        this.assetTypeId=this.idType[i].id;
                    }
                }
            },
            onValuesChangeShop(c){
                this.leaveValueShop = c;
                this.valuesShop = [c];
                for(var i=0;i<this.idShop.length;i++){
                    if(this.valuesShop[0]===this.idShop[i].name){
                        this.officeId=this.idShop[i].id;
                    }
                }
            },
            onValuesChangeGood(d){
                this.leaveValueGood = d;
                this.valuesGood = [d];
                if(this.valuesGood[0]==='上架'){
                    this.assetStatus='1'
                }else {
                    this.assetStatus='2'
                }
            },
            onValuesChangeNum(e){
                this.leaveValueNum = e;
                this.valuesNum = [e];
                for(var i=0;i<this.Nums.length;i++){
                    if(this.valuesNum[0]===this.Nums[i].name){
                        this.dictType=this.Nums[i].id;
                    }
                }
            },
            Sub:function () {
                this.assetPicPath=this.$refs.imgs.$data.imgArr;
                // console.log(this.$refs.imgs.$data.imgArr);
                if(this.token===''||this.id===''||this.assetUseStatus===''||this.assetTypeId===''||this.officeId===''||this.assetStatus===''||this.name===''||this.price===''||this.assetPicPath===''){
                    alert('请把信息补充完整')
                }else {
                axios.post(API_ASSET_ADD,qs.stringify({
                    utoken: this.token,
                    companyId:this.id,
                    assetUseStatus:this.assetUseStatus,
                    assetTypeId:this.assetTypeId,
                    officeId:this.officeId,
                    assetStatus:this.assetStatus,
                    assetName:this.name,
                    assetPrice:this.price,
                    assetPicPath:this.assetPicPath,
                    assetCode:this.num,
                    unit:this.dictType,
                    quantity:this.quantity

                })).then(res=>{
                    console.log(res);
                    let data=res.data;
                    if(data.result===0){
                        Toast({
                            message:'添加成功',
                            duration: 1000
                        });
                        this.$router.go(-1)
                    }else if(data.result===2){
                        Toast({
                            message: '用户已失效，请重新登陆',
                            duration: 2000
                        });
                        window.localStorage.clear();
                        this.$router.push('/login');
                    }
                }).catch(err=>{
                    console.log(err);
                });
                }
            }
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"

    .create-asset-wrapper
        position: absolute
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        z-index:1
        .border-1px
            border-b(#eee)
        .get
            width: 50px
            height: 50px
            position: absolute
            right: 0
            top: 0
            text-align: center
            line-height: 50px
            color #fff
</style>



