<template>
    <div class="details-wrapper">
        <v-header :path="fromUrl" title="详情"></v-header>
        <div>
            <img :src="ObjList.pics" alt="">
            <mt-cell title="姓名">{{ObjList.name}}</mt-cell>
            <mt-cell title="电话"><a :href="'tel:' + ObjList.phone"></a>{{ObjList.phone}}</mt-cell>
            <mt-cell title="性别">{{ObjList.sex}}</mt-cell>
            <mt-cell title="年龄">{{ObjList.age}}</mt-cell>
            <mt-cell title="职位">{{ObjList.chargePerson}}</mt-cell>
            <mt-cell title="客户意向">{{ObjList.intention}}</mt-cell>
        </div>
    </div>
</template>
<script>
    import VHeader from 'components/header/header';
    import { mapGetters } from 'vuex';

    export default {
        name:'Details',
        data() {
            return {
                fromUrl:'',
                CusList:[],
                id:'',
                ObjList:{},
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        created(){
            this.CusList=this.getMyCus;
            this.id=this.$route.params.id;
            console.log(this.CusList);
            for(var i=0;i<this.CusList.length;i++){
                if(this.id===this.CusList[i].id){
                    this.ObjList=this.CusList[i];
                }
            }
            console.log(this.ObjList);
        },
        computed: {
            ...mapGetters([
                'getMyCus',
            ])
        },
        components: {VHeader},
        methods: {}
    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"
        .details-wrapper{
            position: fixed
            left: 0
            right: 0
            top: 0
            bottom: 0
            background-color: $color-bg
            z-index 1
        }
    img
        width 55px
        height 55px
        border-radius 50%
        margin-left 10px
</style>



