<template>
    <div class="notice-wrapper">
        <v-header :path="fromUrl" title="公告" style="position: relative; z-index: 2"></v-header>
        <mt-navbar v-model="selected" style="position: relative; z-index: 2">
            <mt-tab-item id="1">未读</mt-tab-item>
            <mt-tab-item id="2">已读</mt-tab-item>
        </mt-navbar>
        <scroller style="position: relative; z-index: 1">

        <mt-tab-container v-model="selected">
            <mt-tab-container-item id="1">
                <router-link tag="div" append to="" class="approve-item">
                    <Noticelists ref="ToNoticelists" :child-list="list"/>
                </router-link>
            </mt-tab-container-item>

            <mt-tab-container-item id="2">
                <scroller style="position: relative; z-index: 99999">
                <router-link tag="div" append to="" class="approve-item">
                    <NoticelistsNo :child-list="list"/>
                </router-link>
                </scroller>
            </mt-tab-container-item>
        </mt-tab-container>
        </scroller>
        <router-link tag="div" append to="newnotive" class="create-people" style="z-index: 3333">发公告</router-link>
        <transition name="slide">
            <router-view></router-view>
        </transition>


    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import Noticelists from '../noticelists/noticelists'
    import NoticelistsNo from '../noticelists-no/noticelists-no'
    import {API_NOTICE} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';

    export default {
        data() {
            return {
                list:[],
                fromUrl:'',
                selected:'1',
                token:''
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        beforeRouteUpdate(to, from, next){
            if(from.path.substr(from.path.length-17,from.path.length)==='/notice/newnotive'){
                    this._request();
            }
            next();
        },
        created(){
            this.token=this.$login.getToken();
            this._request();
        },
        computed: {},
        components: {VHeader,Noticelists,NoticelistsNo},
        methods: {
            async _request(){
                let res=await axios.post(API_NOTICE,qs.stringify({
                    utoken:this.token,
                }));
                let data=res.data;
                console.log(data);
                this.list=data.list;
            },
            /*ToNotive:function (id) {
                console.log(1);
                console.log(id);
                this.$refs.ToNotiverange.NotiveDetails();

            },*/
            /*Delete:function (id) {
                this.refs.ToNoticelists.Delete(id)
            }*/
        }
    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"

    .notice-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        z-index 99
    .create-people
        width: 80px
        padding: 0 5px
        text-align: center
        color #fff
        position: absolute
        right: 0
        top: 0
        line-height: 50px



</style>



