<template>
    <div class="create-wrapper">
        <v-header path="/team" title="创建团队"></v-header>
        <mt-navbar v-model="selected">
            <mt-tab-item id="1">企业</mt-tab-item>
            <mt-tab-item id="2">团队</mt-tab-item>
            <!--<mt-tab-item id="3">群</mt-tab-item>-->
        </mt-navbar>

        <!-- tab-container -->
        <mt-tab-container v-model="selected">
            <mt-tab-container-item id="1">
                <div class="tab-item">
                    <div class="bg">
                        <img src="./company.png" alt="">
                    </div>
                    <p>权限说明</p>
                    <ul class="list">
                        <li>人数上限500</li>
                        <li>可享XXXX等20种功能</li>
                        <li>其它功能</li>
                    </ul>
                </div>
                <div class="footer">
                    asd
                    <button class="create-btn" @click="CreateCompany">创建企业</button>
                </div>
            </mt-tab-container-item>
            <mt-tab-container-item id="2">
                <div class="tab-item">
                    <div class="bg">
                        <img src="./team.png" alt="">
                    </div>
                    <p>权限说明</p>
                    <ul class="list">
                        <li>人数上限50</li>
                        <li>可享XXXX等10种功能</li>
                        <li>其它功能</li>
                    </ul>
                </div>
                <div class="footer">
                    asd
                    <button class="create-btn" @click="CreateTeam">创建团队</button>
                </div>
            </mt-tab-container-item>
           <!-- <mt-tab-container-item id="3">
                <div class="tab-item">
                    <div class="bg">
                        <img src="./family.png" alt="">
                    </div>
                    <p>权限说明</p>
                    <ul class="list">
                        <li>人数上限50</li>
                        <li>可享XXXX等2种功能</li>
                        <li>其它功能</li>
                    </ul>
                </div>
                <div class="footer">
                    asd
                    <button style="width: 96px;" class="create-btn" @click="CreateGroup">创建群</button>
                </div>
            </mt-tab-container-item>-->
        </mt-tab-container>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';

	export default {
	    data() {
	        return {
                selected: '1'
            }
        },
	    components: {
	        VHeader
        },
        methods:{
            CreateCompany:function () {
                this.$router.push('createcompany')
            },
            CreateTeam:function () {
                this.$router.push('createteam')
            },
            /*CreateGroup:function () {
                this.$router.push('creataegroup')
            }*/
    }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .create-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        z-index: 1000
        .mint-navbar
            background-color $color-black
            .mint-tab-item-label
                font-weight: bold
                color $color-orange
        .is-selected
            border-bottom: 3px solid $color-orange!important
            margin: 0!important
        .tab-item
            padding: 0 10px
            .bg
                height: 180px
                margin: 20px auto 0
                text-align: center
                background none
                img
                    height: 100%
            .list
                padding-left: 20px
                list-style disc
        .footer
            height: 50px
            display: flex
            justify-content space-between
            position: fixed
            left: 0
            right: 0
            bottom: 0
            background-color $color-black
            .create-btn
                height: 100%;
                color: #fff;
                font-size: 14px;
                padding: 0 20px;
                outline: 0;
                border: none;
                background-color: #f08619;
</style>



