<template>
    <div class="cart-wrapper">
        <v-header path="/" :goback="true"  title="购物车"></v-header>
        <div class="cart-main">
            <div class="cart-item">
                <div class="seller-name border-1px">
                    <mu-avatar :size="32" slot="left" src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=138253999,3830018517&fm=27&gp=0.jpg"/>
                    xxx汽车服务中心
                </div>
                <div class="goods-item">
                    <div class="img-wrap">
                        <img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1513334696645&di=5925270b1e64f2901b082cf67a27e019&imgtype=0&src=http%3A%2F%2Fimgsrc.baidu.com%2Fimgad%2Fpic%2Fitem%2F37d12f2eb9389b5014d8ca5b8f35e5dde7116e97.jpg" alt="">
                    </div>
                    <div class="text-content">
                        <div class="good-name">汽车保养</div>
                        <div class="abstract">介绍</div>
                        <div class="money">
                            <div class="num">￥100.00</div>
                            <div class="con">
                                <div class="prev">-</div>
                                <div class="d-num">0</div>
                                <div class="add">+</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>



        <div class="bot-bar">
            <div class="text">
                已选购<span class="orange">￥</span><span class="fz18 orange">100.00</span>
            </div>
            <div class="yes-btn">结算</div>
        </div>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';

	export default {
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"

    .cart-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color $color-bg
        .cart-main
            position: absolute
            top: 50px
            bottom: 45px
            left: 0
            right: 0
            font-size: 12px
            .cart-item
                margin-top: 10px
                padding: 0 10px
                background-color #fff
                .seller-name
                    padding: 5px 0
                    line-height: 32px
                    border-b(#ccc)
                .mu-avatar
                    margin-right: 5px
                    vertical-align: top
                .goods-item
                    padding: 10px 0
                    display: flex
                    .img-wrap
                        flex 0 0 76px
                        height: 76px
                        border 1px solid #ccc
                        border-radius 5px
                        overflow: hidden
                        box-sizing content-box
                        img
                            width: 100%
                    .text-content
                        box-sizing content-box
                        padding: 0 10px
                        flex 1
                        .good-name
                            font-size: 14px
                        .abstract
                            color #989292
                            height: 39px
                        .money
                            display: flex
                            color $color-orange
                            .num,.con
                                flex 1
                            .con
                                text-align: right
                                font-size: 0
                                div
                                    display: inline-block
                                    vertical-align: top
                                .prev,.add
                                    width: 25px
                                    height: 100%
                                    text-align: center
                                    line-height: 17px
                                    font-size: 18px
                                    border 1px solid #ccc
                                .d-num
                                    width: 35px
                                    font-size: 12px
                                    line-height: 17px
                                    height: 100%
                                    border 1px solid #ccc
                                    border-left none
                                    border-right none
                                    text-align: center

        .bot-bar
            position: absolute
            left: 0
            right: 0
            bottom: 0
            height: 45px
            display: flex
            line-height: 45px
            box-sizing border-box
            padding-left: 28px
            color #b4b4b4
            background-color $color-black
            .orange
                color $color-orange
            .fz18
                font-size: 18px
            .text
                flex 1
            .yes-btn
                height: 100%
                background-color $color-orange
                color #fff
                text-align: center
                line-height: 45px
                flex 0 0 112px
</style>



