<template>
    <div class="create-job-wrapper">
        <v-header :path="fromPath" title="添加任务"></v-header>
        <mt-field label="任务名称" placeholder="请输入任务名称" v-model="name"></mt-field>

        <mt-cell title="完成人" :value="leaveValue" @click.native="popupVisible=true" is-link></mt-cell>
        <mt-cell title="截至时间" :value="chooseDate" @click.native="showDatePicker" isLink></mt-cell>
        <mt-field label="完成进度" placeholder="请输入完成进度(数字)" v-model="speed">%</mt-field>
        <mt-field label="任务描述" placeholder="任务描述" type="textarea" rows="4" v-model="describe"></mt-field>
        <mt-datetime-picker
            ref="picker"
            type="date"
            @confirm="handleConfirm"
            v-model="pickerValue">
        </mt-datetime-picker>
        <mt-popup
            v-model="popupVisible"
            position="bottom">
            <mu-picker :slots="slots" :visible-item-count="3" @change="onValuesChange" :values="values"/>
        </mt-popup>
        <span class="add" @click.once="add">添加</span>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import {API_COMPLETE,API_ADD_WORK} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Indicator, Toast } from 'mint-ui';

    export default {
	    data() {
	        return {
	            fromPath: '',
                pickerValue: new Date(),
                chooseDate: '请选择截至时间',
                token:'',
                comid:'',
                speed:'0',
                name:'',
                describe:'',

                officeId:'',
                people:[],
                leaveValue:'请选择完成人(必选)',
                popupVisible: false,
                values: [],
                slots: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],
            }
        },
        beforeRouteEnter (to, from, next) {
            next( vm => {
                vm.fromPath = from.path;
            });
        },
        created(){
	        this.token=this.$login.getToken();
	        this.comid=this.$route.params.id;
            this._request();
        },
        methods: {
            handleConfirm() {
                console.log(this.pickerValue.toLocaleDateString());
                console.log(typeof (this.pickerValue.getDate()));
                var month;var num;var date;var num2;
                if(this.pickerValue.getMonth()+1<10&&this.pickerValue.getDate()>9){
                    num=this.pickerValue.getMonth()+1;
                    month='0'+num;
                    this.chooseDate = `${this.pickerValue.getFullYear()}-${month}-${this.pickerValue.getDate()}`;
                }else if(this.pickerValue.getMonth()+1>=10&&this.pickerValue.getDate()<10){
                    num2=this.pickerValue.getDate();
                    date='0'+num2;
                    this.chooseDate = `${this.pickerValue.getFullYear()}-${this.pickerValue.getMonth()+1}-${date}`;
                }
                else if(this.pickerValue.getMonth()+1<10&&this.pickerValue.getDate()<10){
                    num=this.pickerValue.getMonth()+1;
                    month='0'+num;
                    num2=this.pickerValue.getDate();
                    date='0'+num2;
                    this.chooseDate = `${this.pickerValue.getFullYear()}-${month}-${date}`;
                }else if(this.pickerValue.getMonth()+1>=10&&this.pickerValue.getDate()>9){

                    this.chooseDate = `${this.pickerValue.getFullYear()}-${this.pickerValue.getMonth()+1}-${this.pickerValue.getDate()}`;
                }
                this.$refs.picker.close();
                console.log(this.chooseDate);
            },
            showDatePicker() {
                this.$refs.picker.open()
            },
            onValuesChange(a) {
                this.leaveValue = a;
                this.values = [a];
                for(var i=0;i<this.people.length;i++){
                    if(this.values[0]===this.people[i].name){
                        this.officeId=this.people[i].id;
                    }
                }
                console.log(this.officeId);
            },
            async _request(token){
                let res=await axios.post(API_COMPLETE,qs.stringify({
                    utoken:this.token,
                    companyId:this.comid
                }));
                let data=res.data;
                console.log(data);
                this.people=data.list;
                var arr=[];
                for(var i=0;i<this.people.length;i++){
                    arr.push(this.people[i].name);
                }
                this.slots[0].values=arr;
            },
            add(){
                if(this.name===''||this.describe===''||this.officeId===''||this.chooseDate===''||this.speed===''){
                    alert('请补充完整信息')
                }else{
                    axios.post(API_ADD_WORK,qs.stringify({
                        utoken:this.token,
                        name:this.name,
                        synopsis:this.describe,
                        personChargeId:this.officeId,
                        estimateTime:this.chooseDate,
                        endTime:this.speed
                    })).then(res=>{
                        console.log(res);
                        let data=res.data;
                        if(data.result===0){
                            Toast({
                                message: '添加成功',
                                duration: 2000
                            });
                            this.$router.go(-1);
                        }else if(data.result===2){
                            window.localStorage.clear();
                            this.$router.push('/login');
                            Toast({
                                message: '用户已失效，请重新登陆',
                                duration: 2000
                            });
                        }
                    }).catch(err=>{
                        console.log(err);
                    })
                }
            }
        },
	    components: {
            VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"

    .create-job-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        .mint-cell
            border-b(#eee)

    .add
        position absolute
        top:18px
        right 10px
        color #FFF
</style>



