<template>
    <div class="more-wrapper">
        <v-header :path="fromUrl" title="更多" style="z-index: 9999"></v-header>
        <scroller class="con">
            <ul>
                <li v-for="item in allThigh">
                    <span class="comp">{{item.name}}：</span>
                    <span>{{item.count}}</span>
                </li>
            </ul>
        </scroller>

    </div>
</template>
<script>
    import VHeader from 'components/header/header';
    import  axios from 'axios'
    import qs from 'qs';
    import { mapGetters } from 'vuex';
    import {API_GET_SHARES} from 'common/js/api'
    export default {
        data() {
            return {
                fromUrl:'',
                allThigh:[]
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        created(){
            let token=this.$login.getToken();
            let id=this.getCompanyMsg.id;
            this._request(token,id);
        },
        computed:{
            ...mapGetters([
                'getCompanyMsg'
            ]),
        },
        components: {VHeader},
        methods: {
            async _request(token,id){
                let res=await axios.post(API_GET_SHARES,qs.stringify({
                    utoken: token,
                    officeId:id
                }));
                let data=res.data;
                console.log(data);
                this.allThigh=data.list;
            }
        }
    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"
    .more-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        z-index 22
        .con
            margin-top 50px
        li
            width 100%
            height 40px;
            line-height 40px;
            background-color #FFFFFF
            border-bottom 1px solid #CCCCCC
            span
                padding 0 10px
            .comp
                color #000000
                font-size 16px
</style>



