<template>
    <div class="s-wrapper">
        <!-- 头部 -->
        <div class="s-header">
            <!-- 头部底图 -->
            <img class="bg blur" src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1512135479865&di=8f40c0b046fef7bda228da4c0d923f00&imgtype=0&src=http%3A%2F%2Fimg1.touxiang.cn%2Fuploads%2F20140416%2F16-055612_614.jpg" alt="">
            <!-- 底图遮罩 -->
            <div class="zz"></div>
            <!-- 搜索栏、返回 -->
            <div class="s-search">
                <div class="left" @click="goBack">
                    <i class="icon-fanhui"></i>
                </div>
                <div class="s-input">
                    <div class="icon">
                        <i class="icon-magnifier"></i>
                    </div>
                    <input class="input" placeholder="搜索" type="text" />
                </div>
                <div class="right"></div>
            </div>
            <!-- 商家头像、名称 -->
            <div class="s-msg">
                <img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1512135479865&di=8f40c0b046fef7bda228da4c0d923f00&imgtype=0&src=http%3A%2F%2Fimg1.touxiang.cn%2Fuploads%2F20140416%2F16-055612_614.jpg" alt="">
                <div class="text">
                    <span class="name">{{getCompanyMsg.name}}</span><br>
                    <span class="bot">
                        <span class="assess">评价</span>
                        <star :star="Number(getCompanyMsg.totalScore)"></star>
                    </span>
                    <!-- <div @click="doSomething" v-if="this.classtaggle=='1'" style="position: absolute;width: 25px;height: 25px;background: rebeccapurple;right: 40px;top: 5px;"></div> -->
                    
                </div>
            </div>
            
            <!-- 商家地址、电话 -->
            <div class="s-add">
                <div class="item" style="width: 30%">
                    <div class="icon">
                        <i class="icon-dianhua"></i>
                    </div>
                    <span class="text">{{getCompanyMsg.phone?getCompanyMsg.phone: '该商铺没写联系方式'}}</span>
                </div>
                <div class="item" style="width: 70%;margin: 0;">
                    <div class="icon z">
                        <i class="icon-dianhua">x</i>
                    </div>
                    <span class="text">{{getCompanyMsg.address}}</span>
                </div>
            </div>
            <div @click="doSomething" v-if="this.boxshow=='1'" class="calss_b">
                <img width="50%" height="100%" src="./shoucang.png" alt="">
                <span></span>
            </div>
            <div @click="doSomething" v-else class="calss_b">
                <img width="50%" height="100%" src="./shoucang1.png" alt="">
                <span></span>
            </div>
        </div>
        
        <!-- tab -->
        <div class="s-tab border-1px">
            <router-link tag="div" :to="'/index/seller'+id+'/goods'">
                <span>商品/服务</span>
            </router-link>
            <router-link tag="div" :to="'/index/seller'+id+'/comments'">
                <span>评价</span>
            </router-link>
            <router-link tag="div" :to="'/index/seller'+id+'/msg'">
                <span>店铺详情</span>
            </router-link>
        </div>
        <div class="list-wrapper">
            <keep-alive>
                <router-view></router-view>
            </keep-alive>
        </div>
    </div>
</template>

<script>
    import {API_TAGGLELLECTION} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import Star from '../comp/star/star';
    import { mapGetters } from 'vuex';

	export default {
        data() {
            return {
                id: '',
                boxshow:''
            }
        },
        created() {
            this.id = this.$route.params.id;
            this.boxshow = 1
        },
        computed: {
            ...mapGetters(['getCompanyMsg'])
        },
	    methods: {
            goBack() {
                this.$router.push({
                    path: '/index'
                })
            },
            doSomething(){
                this.boxshow = !this.boxshow;
                axios.post(API_TAGGLELLECTION,qs.stringify({
                utoken:this.$login.getToken(),
                collectionType:'1',
                officeId:this.id
                })).then(response=>{
                    let res = response.data
                    
                }).catch(err=>{
                    console.log(err)                
                });
            }
        },
        components: {
	        Star
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/mixin"
    @import "~common/stylus/variable"
    .s-wrapper
        position: absolute
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index 20141290
        background-color #fff
        .s-header
            width: 100%
            height: 138px
            overflow: hidden
            position: relative
            .bg
                width: 100%
            .zz
                position: absolute
                left: 0
                right: 0
                top: 0
                bottom: 0
                background-color #000
                opacity: .6
            .s-search
                position: absolute
                left: 0
                right: 0
                top: 5px
                height: 28px
                display: flex
                font-size: 12px
                .s-input
                    flex 1
                    background-color #fff
                    opacity: .6
                    border-radius 5px
                    color #888
                    display: flex
                    .icon
                        flex 0 0 42px
                        text-align: center
                    .input
                        flex 1
                        border hidden
                        outline none
                        border-radius 5px
                .left,.right
                    width: 39px
                    display: flex
                    align-items center
                    justify-content center
                    color #fff
            .s-msg
                position: absolute
                top: 47px
                width: 100%
                height: 45px
                box-sizing border-box
                padding: 0 17px
                font-size: 0
                color #fff
                .text
                    display: inline-block
                    vertical-align: top
                    height: 100%
                    .assess
                        font-size: 12px
                    .bot
                        display: inline-block
                        vertical-align: top
                        margin-top: 5px
                        height: 12px
                        line-height: 12px
                span
                    font-size: 14px
                img
                    width: 45px
                    height: 45px
                    border-radius 5px
                    margin-right: 13px
                    vertical-align: top
            .s-add
                position: absolute
                left: 0
                right: 0
                top: 98px
                height: 34px
                display: flex
                align-items center
                box-sizing border-box
                padding: 0 17px
                font-size: 0
                .item
                    display: flex
                    align-items center
                    font-size: 0
                    margin-right: 15px
                    .icon
                        width: 15px
                        height: 15px
                        background-color #ffd867
                        text-align: center
                        border-radius 50%
                        line-height: 15px
                        color #504432
                        display: inline-block
                        vertical-align: middle
                        margin-right: 4px
                        &.z
                            background-color #c781f6
                        i
                           font-size: 12px
                    .text
                        max-width 90%
                        overflow: hidden
                        text-overflow ellipsis
                        white-space nowrap
                        font-size: 12px
                        color #fff
        .s-tab
            width: 100%
            height: 35px
            border-b(#e5e5e5)
            display: flex
            div
                height: 100%
                flex 1px
                line-height: 35px
                text-align: center
                color #555554
                &.router-link-active
                    color $color-orange
                    position: relative
                    span
                        display: inline-block
                        vertical-align: top
                        height: 100%
                        border-bottom: 1px solid $color-orange

        .list-wrapper
            position: absolute
            top: 173px
            bottom: 0
            left: 0
            right: 0
    .class_a 
        position: absolute;
        width: 25px;
        height: 25px;
        background url('./shoucang.png')
        right: 40px;
        top: 5px;
        text-align center
    .calss_b
        position: absolute;
        width: 50px;
        height: 25px;
        right: 40px;
        top: 90px;
</style>



