<template>
    <div class="comment-wrapper">
        <v-header path="/mine/record" title="评论"></v-header>
        <!--<div class="comment-seller-name">
            &lt;!&ndash;<img alt="商家logo" class="index-18wSG_0" src="https://fuss10.elemecdn.com/4/2b/77b246abf67bd8929ee941c9aea94png.png?imageMogr/format/webp/thumbnail/!70x70r/gravity/Center/crop/70x70/">&ndash;&gt;
            &lt;!&ndash;<span>xxxxxx汽车服务中心</span>&ndash;&gt;
        </div>-->
        <div class="comment-body">
            <span class="title">评价商品</span>
            <div class="comment-block">
                <div class="product">
                    星级
                    <Rate v-model="value"></Rate>
                </div>
                <div class="comment-input">
                    <textarea maxlength="140" v-model="text" placeholder="说说哪里满意，帮大家选择"></textarea>
                    <span v-show="num" class="count">{{num}}/140个字</span>
                </div>
            </div>
        </div>
        <footer class="comment-submit">
            您的评价对其他用户会有帮助
            <button class="comment-btn" @click="OrderComment">提交评价</button>
        </footer>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import {API_ORDER_COMMENT} from 'common/js/api';
    import qs from 'qs';
    import { Toast, Indicator } from 'mint-ui';
    import axios from 'axios';
	export default {
	    data() {
	        return {
	            value: 1,
                text: '',
                token:'',
                id:''
            }
        },
        computed: {
	        num() {
	            return this.text.length
            }
        },
        created() {
            this.token = this.$login.getToken();
            this.id=this.$route.params.id ;
        },
        methods:{
            OrderComment(){
                console.log(this.value);
                if(this.value===''||this.text===''){
                    Toast({
                            message: '请把数据补充完整',
                            duration: 2000
                        });
                }else {
                    axios.post(API_ORDER_COMMENT,qs.stringify({
                        orderId:this.id,
                        starNum:this.value,
                        comments:this.text,
                        utoken:this.token
                    })).then(res=>{
                        console.log(res);
                        let data=res.data;
                        if(data.result===0){
                            this.$router.push('/mine/record');
                            Toast({
                                message: '提交成功',
                                duration: 2000
                            });
                        }else if(data.result===2){
                            window.localStorage.clear();
                            this.$router.push('/login');
                            Toast({
                                message: '用户已失效，请重新登陆',
                                duration: 2000
                            });
                        }
                    }).catch(err=>{
                        console.log(err);
                    })
                }

            }
        },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .comment-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        .comment-seller-name
            padding: 15px
            margin-bottom: 10px
            display: flex
            align-items center
            font-size: 16px
            background-color: #fff
            img
                width: 35px
                height: 35px
                border-radius 2px
                margin-right: 10px
        .comment-body
            padding: 15px
            background-color: #fff
            .title
                display: inline-block
                font-size: 14px
                margin: 0 4px 10px 0
            .comment-block
                .product
                    display: flex
                    justify-content space-between
                    align-items center
                    font-size: 14px
                .comment-input
                    position: relative
                    margin-top: 10px
                    padding-bottom: 15px
                    border: 1px solid #eee
                    background-color: #f9f9f9
                    textarea
                        width: 100%
                        padding: 5px 10px
                        color #666
                        resize none
                        border none
                        background-color: #f9f9f9
                        outline none
                        appearance: none
                    .count
                        position: absolute
                        bottom: 2px
                        right: 5px
        .comment-submit
            width: 100%
            height: 44px
            display: flex
            align-items center
            justify-content space-between
            color #fff
            padding-left: 15px
            position: absolute
            left: 0
            bottom: 0
            background-color: $color-black
            .comment-btn
                height: 100%
                color #fff
                font-size: 14px
                padding: 0 20px
                outline: 0
                border: none
                background-color: $color-orange
</style>



