<template>
    <div class="addfriend-wrapper">
        <v-header :path="fromUrl" title="添加好友"></v-header>
        <div class="friend-detail">
            <img src="https://fuss10.elemecdn.com/c/f5/d0b0f2fc83f3ac3e4a0cfae891256png.png?imageMogr/format/webp/thumbnail/!60x60r/gravity/Center/crop/60x60/" alt="">
            <div class="name">{{name}}</div>
        </div>
        <div class="btn-group">
            <div class="add-msg" @click="AddFriend">添加好友</div>
        </div>
    </div>
</template>
<script>
    import VHeader from 'components/header/header';
    import {API_ADD_FRIEND} from 'common/js/api'
    import  axios from 'axios'
    import { Indicator, Toast } from 'mint-ui';
    import qs from 'qs';

    export default {
        data() {
            return {
                fromUrl: '',
                name: '',
                id:'',
                token:''
            }
        },
        created() {
            this.name=this.$route.params.login;
            this.id=this.$route.params.id;
            this.token=this.$login.getToken();
            console.log(this.$route.params);
            console.log(this.id);

        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        computed: {},
        components: {VHeader},
        methods: {
            AddFriend(){
                /*let res=await axios.post(API_ADD_FRIEND,qs.stringify({
                    utoken:this.token,
                    target:this.id
                }));
               let data=res.data;
               console.log(data);*/
               axios.post(API_ADD_FRIEND,qs.stringify({
                   utoken:this.token,
                   target:this.id
               })).then((res)=> {
                   console.log(res.data.msg);
                   let data=res.data;
                   if(data.result===0){
                       this.$router.push('/message/maillist');
                   }else if(data.result===2){
                       Toast({
                           message: '用户已失效,请重新登陆',
                           duration: 1000
                       });
                       window.localStorage.clear();
                       this.isLogin = false;
                   }
               }).catch((err)=>{
                   console.log(err);
                   // alert(err.data.msg);
               })
            }
        }


    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"
    .addfriend-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index: 1000
        background-color: $color-bg
        .friend-detail
            padding: 20px
            display: flex
            background-color: #fff
            .name
                font-size: 16px
                padding-left: 20px
                font-weight: bold
        .btn-group
            div
                margin: 0 auto
                text-align: center
                width: 90%
                line-height: 40px
                margin-top: 20px
                color #fff
                border-radius 4px
                font-size: 14px
                background-color: $color-orange
            .add-msg
                background-color: rgb(255, 73, 72)



</style>



