<template>
    <div class="item-w" @click="goToSubPage">
        <div class="item-main border-1px">
            <img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1512971154554&di=6eccc0c06a44d1ea11f4a91c4099da6c&imgtype=jpg&src=http%3A%2F%2Fimg1.imgtn.bdimg.com%2Fit%2Fu%3D1984422401%2C3186567680%26fm%3D214%26gp%3D0.jpg" alt="">
            <div class="item-text">
                <div class="item-name">{{msg.name}}</div>
                <div style="margin:9px 0" class="item-job">
                    <div class="icon-e66f icon"></div>
                    <span>部门：{{msg.department}}</span>
                    <div style="margin-left: 20px;" class="icon-zhiwei icon"></div>
                    <span>职务：{{msg.position}}</span>
                </div>
                <div class="item-job">
                    <div class="icon-shijian icon"></div>
                    <span>进入公司时间：{{msg.addDate}}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapMutations } from 'vuex';
	export default {
	    props: {
	        msg: {
	            type: Object,
                default() {
	                return {}
                }
            },
	        needClick: {
	            type: Boolean,
                default: false
            }
        },
	    methods: {
            ...mapMutations({
                setMsg: 'SET_COMPANY_MSG'
            }),
            goToSubPage() {
                if(!this.needClick) return;
                this.setMsg(this.msg);
                this.$emit('goToSubPage',this.msg.id);
                // console.log(this.msg.id);
                console.log(this.msg.t);
            }
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    .item-w
        width: 100%
        height: 108px
        padding: 0 16px
        box-sizing border-box
        .item-main
            width: 100%
            height: 100%
            padding:18px 0
            box-sizing border-box
            border-b(#ccc)
            font-size: 0
            &:last-child
                &::after
                    display: none
            img
                width: 73px
                height: 73px
                border-radius 5px
                vertical-align: top
                margin-right: 16px
            .item-text
                display: inline-block
                vertical-align: top
                height: 73px
                font-size: 12px
                color #464545
                .item-job
                    .icon
                        display: inline-block
                        vertical-align: top
                        width: 18px
                        height: 18px
                        background-color #dcdcdc
                        border-radius 50%
                        font-size: 12px
                        color #575757
                        text-align: center
                        line-height: 18px
</style>



