<template>
    <div class="data-tab-wrapper">
        <v-header path="/mine" title="首页数据切换"></v-header>
        <div v-on:click.capture="ToIndex($event)">
            <mt-radio
                title="选择需要首页展示的单位"
                v-model="value"
                class="border-1px"
                :options="options"
            >
            </mt-radio>
        </div>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import {API_INDEX_LIST,API_INDEX_AFTER} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Toast } from 'mint-ui';


    export default {
	    data() {
	        return {
	            value: '',
                tarDel:'',
                options: [
                    /*{
                        label: '北京睿信毅城科技有限公司',
                        value: '1'
                    },
                    {
                        label: 'xxxxxxx有限公司',
                        value: '2'
                    },
                    {
                        label: '个人账户',
                        value: '3'
                    }*/
                ],
                token:''
            }
        },
        created(){
            this.token = this.$login.getToken();
            this._request();

        },
        methods:{
            async _request() {
                let res=await axios.post(API_INDEX_LIST,qs.stringify({
                    utoken:this.token
                }));
                let data=res.data;
                console.log(data);
                var dataList=data.list;
                console.log(dataList)
                var arr=[];
                for(var i=0;i<dataList.length;i++){
                    var obj=dataList[i];
                    arr.push({
                        label:obj.name,
                        value:obj.id,
                        t:obj.t
                    })
                }
                console.log(arr);
                this.options=arr;
            },
            ToIndex(ev){
                var event = ev || ev.target;
                console.log(event.target.value);
                for(var i=0;i<this.options.length;i++){
                    if(this.options[i].value===event.target.value){
                        console.log(this.options[i].t);//传给后台的t
                        console.log(this.options[i].value);
                        var t=this.options[i].t;
                        var name=this.options[i].label;
                        var personId=this.options[i].value;
                        console.log(name);
                        axios.post(API_INDEX_AFTER,qs.stringify({
                            utoken:this.token,
                            personId:personId,
                            indexType:t
                        })).then(res=>{
                            console.log(res);
                            let data=res.data;
                            if(data.result===0){
                                localStorage.setItem("indexT", t);
                                localStorage.setItem("indexID", personId);
                                localStorage.setItem("name", name);
                            }else if(data.result===2){
                                Toast({
                                    message: '用户已失效，请重新登陆',
                                    duration: 2000
                                });
                                window.localStorage.clear();
                                this.$router.push('/login');
                            }
                        }).catch(err=>{
                            console.log(err);
                        });
                    }
                }
                console.log(this.options);
                // var T = localStorage.getItem("indexT");
                // console.log(T);
            }
            },

	    components: {
	        VHeader
        }

    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    .data-tab-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index: 1000
        background-color: $color-bg
        .mint-cell
            &:after
                height: 0

</style>



