import Team from 'page/team/team';
import TeamItem from 'page/team/subpage/team-item/team-item';
import Create from 'page/team/subpage/create/create';
import Join from 'page/team/subpage/join/join';
import JoinField from 'page/team/subpage/join-field/join-field';
import CompanyJoin from 'page/team/subpage/company-join/company-join';
import Finance from 'page/team/subpage/finance/finance';
import Asset from 'page/team/subpage/asset/asset';
import AssetDetail from 'page/team/subpage/asset-detail/asset-detail';
import CreateAsset from 'page/team/subpage/create-asset/create-asset';
import Leave from 'page/team/subpage/leave/leave';
import Purchase from 'page/team/subpage/purchase/purchase';
import Approve from 'page/team/subpage/approve/approve';
import ApproveDetail from 'page/team/subpage/approve-detail/approve-detail';
import Files from 'page/team/subpage/files/files';
import Job from 'page/team/subpage/job/job';
import CreateJob from 'page/team/subpage/create-job/create-job';
import Customer from 'page/team/subpage/customer/customer';
import CreatePeople from 'page/team/subpage/createpeople/createpeople';
import Details from 'page/team/subpage/details/details';
import Notice from 'page/team/subpage/notice/notice';
import NewNotive from 'page/team/subpage/newnotive/newnotive';
import NotiveRange from 'page/team/subpage/notiverange/notiverange';
import Tonotiverange from 'page/team/subpage/tonotiverange/tonotiverange';
import Iou from 'page/team/subpage/iou/iou';
import subSuccess from 'page/team/subpage/sub-success/sub-success';
import CreateCompany from 'page/team/subpage/create-company/create-company';
import CreateTeam from 'page/team/subpage/createteam/createteam'
import CreataeGroup from 'page/team/subpage/creataegroup/creataegroup'
import More from 'page/team/subpage/more/more'
import LookPeople from 'page/team/subpage/lookpeople/lookpeople'
import Apply from 'page/team/subpage/apply/apply'
import CustomerAll from 'page/team/subpage/customer-all/customer-all'
import CustomerNo from 'page/team/subpage/customer-no/customer-no'


export default {
    path: '/team',
    component: Team,
    children: [
        {
            path: 'item/:id/',
            component: TeamItem,
            children: [
                {
                    path: 'more',
                    component: More
                },
                {
                    path: 'finance',
                    component: Finance
                },
                {
                    path: 'asset',
                    component: Asset,
                    children: [
                        {
                            path: 'as:assetid',
                            component: AssetDetail
                        },
                        {
                            path: 'create',
                            component: CreateAsset
                        }
                    ]
                },
                {
                    path: 'leave',
                    component: Leave
                },

               /* {
                    path: 'apply',
                    component: Apply,
                    children: [
                        {
                            path: 'leave',
                            component: Leave
                        },
                    ]
                },*/
                {
                    path: 'leave',
                    component: Leave
                },
                {
                    path: 'purchase',
                    component: Purchase
                },
                {
                    path: 'approve',
                    component: Approve,
                    children: [
                        {
                            path: ':approveid',
                            component: ApproveDetail
                        }
                    ]
                },
                {
                    path:'customer',
                    component:Customer,
                    children: [
                        {
                            path: 'createpeople',
                            component: CreatePeople
                        },
                        {
                            path: 'details/:id',
                            component: Details
                        },
                        {
                            path: 'cusall/:id',
                            component: CustomerAll
                        },
                        {
                            path: 'cusno/:id',
                            component: CustomerNo
                        }
                    ]
                },
                {
                    path:'notice',
                    component: Notice,
                    children: [
                        {
                            path: 'newnotive',
                            component: NewNotive,
                            children:[
                                {
                                    path:'notiverange',
                                    component: NotiveRange,
                                },
                                {
                                    path:'lookpeople',
                                    component: LookPeople,
                                }
                                    ]
                        },
                        {
                            path: 'tonotiverange/:id',
                            component:Tonotiverange ,
                        }
                    ]
                },
                {
                    path: 'iou',
                    component: Iou
                },
                {
                    path: 'subSuccess',
                    component:subSuccess},
                {
                    path: 'files',
                    component: Files
                },
                {
                    path: 'job',
                    component: Job,
                    children: [
                        {
                            path: 'createJob',
                            component: CreateJob
                        }
                    ]
                }

            ]
        },
        {
            path: 'create',
            component: Create,
        },
        {
            path: 'createcompany',
            component: CreateCompany,
        },
        {
            path: 'createteam',
            component: CreateTeam,
        },
        {
            path: 'creataegroup',
            component:CreataeGroup ,
        },
        {
            path: 'join',
            component: Join,
            children: [
                {
                    path: ':id/:comid',
                    component: CompanyJoin,
                    children: [
                        {
                            path: 'joinfield',
                            component: JoinField
                        }
                    ]
                }
            ]
        }
    ]
}



