<template>
    <div class="payselect-wrapper">
        <v-header :path="fromUrl" title="支付"></v-header>
        <div  >
            <mt-radio
                title="选择要支付的类型"
                v-model="value"
                class="border-1px"
                :options="options"
            >
            </mt-radio>
            <mt-button  class="btnt"  type="primary" @click="ToIndex">立即支付</mt-button>
        </div>
    </div>
</template>
<script>
    import VHeader from 'components/header/header';
    import axios from 'axios';
    import qs from 'qs';
    import { API_RECEIVE_WECHAT,API_GOODS,API_RECEIVE_PAYMENT,API_RECEIVE_ALIPAY_YWO} from 'common/js/api';
    import { Toast,Indicator } from 'mint-ui';
    export default {
        data() {
            return {
                fromUrl:'',
                value:'',
                allMoney2:'',
                Sjxhs:'',
                options:[
                    {
                        label:'支付宝支付',
                        value:'1'
                    },
                    {
                        label:'微信支付',
                        value:'2'
                    }
                    ],
                    weChatPayment:'',
                    orderIds:'',
                    weCatchUrl:'',
                    appId:'',
                    mch_id:'',
                    nonce_str :'',      
                    sign:'',
                    prepay_id:'',
                    body:'',
                    out_trade_no:'',
                    total_fee:'',
                    spbill_create_ip:'',
                    notify_url:'',
                    trade_type:'',
                    sign_type:'',
                    mweb_url:'',
                    aaa:''
            }
    
        },

        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        created(){
             this.orderIds = this.$route.query.id;
             this.aaa = this.$route.query.aaa;
             console.log(this.aaa);
             console.log(this.orderIds);
             this.Sjxh()
        },
        computed: {},
        components: {VHeader},
        methods: {
            ToIndex(e){
                // 微信
                if(this.value == 2) {
                    this.weChatPayment = 'weChatPayment';
                    axios.post(API_RECEIVE_WECHAT,qs.stringify({
                        utoken:this.$login.getToken(),
                        paytype:this.weChatPayment,
                        orderIds:this.orderIds
                })).then(response=>{
                    let res = response.data
                    if(res.result===1){
                        Toast({
                            message: res.msg,
                            duration: 1500
                        });
                    }else if(res.result===0){
                        console.log(res.mweb_url);
                        // window.location.href = res.mweb_url;
                    }
                }).catch(err=>{
                    console.log(err);
                });
                }
                //    支付宝
                if(this.value == 1) {
                   
                    // console.log(this.$login.getToken())
                        console.log(API_RECEIVE_ALIPAY_YWO);
                    
                    this.weChatPayment = 'aliPayApi';
                    axios.post(API_RECEIVE_WECHAT,qs.stringify({
                        utoken:this.$login.getToken(),
                        paytype:this.weChatPayment,
                        orderIds:this.orderIds
                })).then(response=>{
                    let res = response.data
                    console.log(res.result);
                    
                    if (res.result===0){
                        console.log(123);
                         window.location.href = API_RECEIVE_ALIPAY_YWO+"?utoken="+ this.$login.getToken()+"&paytype="+this.weChatPayment+"&orderIds="+this.orderIds;
                        // window.location.href = API_RECEIVE_ALIPAY_YWO+"?utoken="+ this.$login.getToken()+"&paytype="+this.weChatPayment+"&orderIds="+this.orderIds;
                    }
                    if(res.result===1){
                        Toast({
                            message: res.msg,
                            duration: 1500
                        });
                        // https://openapi.alipay.com/gateway.do
                    }
                    if(res.result==2){
                        alert('请重新登陆');
                    }
                    
                }).catch(err=>{
                    console.log('访问链接错误！');
                });
                
                }
            },
            Sjxh(){
             let canvas = document.createElement('canvas'),
                gl = canvas.getContext('experimental-webgl'),
                debugInfo = gl.getExtension('WEBGL_debug_renderer_info');
                this.Sjxhs=gl.getParameter(debugInfo.UNMASKED_RENDERER_WEBGL)
                }
            // sub(){
            //     if(!this.selectedShop){
            //         Toast({
            //             message:'请选择商铺'
            //         })
            //         return false;
            //     }
            //     axios.post(API_SUB_REDBAG,qs.stringify({
                    
            //     })).then(response=>{
            //         let res = response.data
                   
                    
            //     }).catch(err=>{
            //         Toast({
            //             message:'发送失败'
            //         })
            //     });
            // },
        },
        
    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"

    .payselect-wrapper
        position: fixed
        left: 0
        top: 0
        bottom: 0
        right: 0
        background-color: $color-bg
    .btnt
        width:100%
</style>



