<template>

    <div class="popularize-wrapper">
        <v-header path="/mine" title="推广"></v-header>
        <div class="back"></div>
        <div class="popularize-btn" 
            @click="Extension">推广给好友
        </div>
        <div id="btn" class="jiathis_style_24x24" v-show="!isShow">
            <span><input id="text" type="text" :value="this.share"><button @click="buTton">复制链接</button></span>
            <div @click="shareTo('qzone')">
                <img src="./30b643c4b609726fbe54ad27c3c39673.png" width="50">
            </div>
            <div @click="shareTo('qq')">
                <img src="./30b643c4b609726fbe54ad27c3c39670.png" width="52">
            </div>
            <div @click="shareTo('sina')">
                <img src="./30b643c4b609726fbe54ad27c3c39671.png" width="56">
            </div>
            <div @click="iSImg()">
                <img src="./30b643c4b609726fbe54ad27c3c39674.png" width="52">
                <div class="imgshow"  v-show="!isImg">
                    <img width="100%" src="https://qr.api.cli.im/qr?data=http%253A%252F%252F192.168.3.228%253A8081%252Fuoter%252Ff%252Ffront%252Fusercon%252FextensionRegister%252FrQ%2525EF%2525BC%2525852FBQJBK5ijO5qt7G22Ib0zhKaEdWfZVfAbW15ZvxMzeuulYTCHBrqTA1UGdYYwN3It5my0s4r%2525EF%2525BC%2525852FD%2525EF%2525BC%2525850D%2525EF%2525BC%2525850AZkCRDw2wIg%2525EF%2525BC%2525853D%2525EF%2525BC%2525853D&level=H&transparent=false&bgcolor=%23ffffff&forecolor=%23000000&blockpixel=12&marginblock=1&logourl=&size=280&kid=cliim&key=d9e74b419f5386c151b74e84da43d5ed" alt="">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
        import VHeader from "components/header/header";
        import axios from 'axios';
        import { Toast } from "mint-ui";
        import {API_EXTENSION} from 'common/js/api';
        import qs from 'qs';

        export default {
        data() {
            return {
            isShow: true,
            isImg: true,
            Url:'',
            share:''
            };
        },
        components: {
            VHeader
        },
        created(){
            axios.post(API_EXTENSION,qs.stringify({
                        utoken:this.$login.getToken(),
                        collectionType:'1'
                        })).then(response=>{
                            let res = response.data
                            console.log(res)
                            this.Url = res.url;
                        }).catch(err=>{
                            console.log(err)                
                        });
                        
        },
        methods: {
            // 生成推广链接
            Extension() {
            this.isShow = !this.isShow;
            this.share = this.Url+this.$login.getToken().replace(/%/g,'％');
        },
        shareTo(stype) {
            var newToken=this.$login.getToken().replace(/%/g,'％');
            var share = this.Url+newToken;
            //qq空间接口的传参
            if (stype == "qzone") {
                window.open(
                "https://sns.qzone.qq.com/cgi-bin/qzshare/cgi_qzshare_onekey?url=" +
                    share +
                    "&title=" +
                    "优生活，快乐生活" +
                    "&summary=" +
                    "&desc=这个很给力了，满10元就可以微信红包提现秒到账，速度注册当元老赚钱"
                );
            }
            //新浪微博接口的传参
            if (stype == "sina") {
                window.open(
                "http://service.weibo.com/share/share.php?appkey" +
                    "583395093" +
                    "&title=" +
                    "分享到新浪微博代码%2C分享到各大网站按钮%7C" +
                    "&url=" +
                    share
                );
            }
            //qq好友接口的传参
            if (stype == "qq") {
                window.open(
                "http://connect.qq.com/widget/shareqq/index.html?url=" +
                    share +
                    "&pics=" +
                    '' +
                    "&site=" 
                );
            }
            },
            iSImg() {
            this.isImg = !this.isImg;
            },
            buTton(){
                var d = document.getElementById("text");  
                d.select();  
                document.execCommand("Copy");  
                alert("复制成功！发送给好友吧");  
            }
        }
        };
</script>

<style rel="stylesheet/stylus" lang="stylus">
@import '~common/stylus/variable';
#app 
    width 100%
    height 100%
    background blue
    
.popularize-wrapper {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 1000;
    background: url('./popularize.png') #fff center 80px no-repeat / 300px;

    .popularize-btn {
        width: 276px;
        height: 45px;
        border-radius: 5px;
        text-align: center;
        line-height: 45px;
        position: absolute;
        top: 65%;
        left: 50%;
        margin-left: -138px;
        background-color: $color-orange;
        color: #fff;
    }
}

.jiathis_style_24x24 {
    position: absolute;
    width: 100%;
    top: 76%;
    text-align: center;

    .fa-facebook {
        color: red;
        position: relative;
    }

    div {
        display: inline-block;
        margin-top: 90px;
        width 20%

        .imgshow {
           position: absolute;
            left: 20%;
            bottom: 190px;
            width: 60%;
            height: 310px;
            text-align: center;
        }
    }

    span {
            width: 80%;
            height: 25px;
            display: inline-block;
            border: 1px solid #ccc;
            position: absolute;
            top: 30%;
            left: 10%;
    }

    input {
            width: 70%;
            height: 100%;
            border: none;
    }

    button {
            width: 30%;
        height: 100%;
        border: 0;
    }
}
</style>



