<template>
    <div class="job-wrapper">
        <v-header :path="fromPath" title="工作任务"></v-header>
        <div class="scroller-wrapper">
            <scroller>
                <div class="job-item" v-for="(item, index) in lists" :key="item.id">
                    <div class="job-name">{{item.name}}</div>
                    <div class="job-detail">
                        <span class="tit">任务描述</span>
                        <span>{{item.synopsis}}</span>
                    </div>
                    <div class="job-detail">
                        <span class="tit">预计完成时间</span>
                        <span>{{item.estimateTime}}</span>
                    </div>
                    <div class="job-detail">
                        <span class="tit">实际完成时间</span>
                        <span>{{item.estimateTime}}</span>
                    </div>
                    <div class="job-detail">
                        <span class="tit">负责人</span>
                        <span>{{item.personChargeName}}</span>
                    </div>

                    <mt-range style="margin: 10px 0;" v-model="value[index]"></mt-range>

                    <div>{{value[index]}}%</div>
                    <div>已完成{{item.endTime}}%</div>
                    <div style="overflow: hidden;" >
                        <div class="job-sub" @click="sub(item.id)">提交进度</div>
                    </div>
                </div>
            </scroller>
        </div>
        <router-link tag="div" append to="createJob" class="create-job">新建任务</router-link>
        <transition name="slide">
            <router-view></router-view>
        </transition>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import { Toast,Indicator} from 'mint-ui';
    import {API_WORK,API_WORK_SPEED} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';

    export default {
	    data() {
	        return {
	            fromPath: '',
                value: [],
                token:[],
                lists:[]
            }
        },
        created() {
	        this.token=this.$login.getToken();
	        this._request();
            this.value = new Array();
        },
        beforeRouteUpdate(to, from, next){
            if(from.path.substr(from.path.length-9,from.path.length)==='createJob'){
                this._request();
            }
            next();
        },
        beforeRouteEnter (to, from, next) {
            next( vm => {
                vm.fromPath = from.path;
            });
        },
        methods:{
            async _request(){
                let res=await axios.post(API_WORK,qs.stringify({
                    utoken:this.token,
                }));
                let data=res.data;
                console.log(data);
                this.lists=data.list;
                // this.value[0]=23;
                for(var i=0;i<this.lists.length;i++){
                    this.value.push(parseInt(this.lists[i].endTime));
                }
            },
            sub(id){
                console.log(this.value[0]);
                axios.post(API_WORK_SPEED,qs.stringify({
                    utoken:this.token,
                    workId:id,
                    speedNumber:this.value[0]
                })).then(res=>{
                    let data=res.data;
                    console.log(res);
                    if(data.result===0){
                        alert('提交成功');
                        Toast({
                            message:'提交成功',
                            duration: 2000
                        });
                    }else if(data.result===2){
                        window.localStorage.clear();
                        this.$router.push('/login');
                        Toast({
                            message: '用户已失效，请重新登陆',
                            duration: 2000
                        });
                    }
                }).catch(err=>{
                    console.log(err);
                })
            }

        },
	    components: {
            VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .job-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        .create-job
            width: 80px
            padding: 0 5px
            text-align: center
            color #fff
            position: absolute
            right: 0
            top: 0
            line-height: 50px
        .scroller-wrapper
            position: absolute
            left: 0
            right: 0
            top: 50px
            bottom: 0
            .job-item
                margin-bottom: 10px
                padding:10px
                background-color: #fff
                .job-name
                    font-size: 16px
                    padding: 0 0 10px
                .job-detail
                    padding: 0 0 10px
                    display: flex
                    .tit
                        flex 0 0 80px
                        color #999
                .job-sub
                    width: 96px
                    line-height: 40px
                    text-align: center
                    color #fff
                    border-radius 2px
                    float: right
                    background-color:$color-orange
</style>



