<template>
    <div class="app-wrapper">
        <!--<v-header path="1" :goback="true" title="功能管理"></v-header>-->
        <v-header :path="fromUrl" title="功能管理"></v-header>
        <!--<mt-search
            v-model="value"
            cancel-text="取消"
            placeholder="搜索">
        </mt-search>-->
        <div class="app-main">
            <scroller>
               <!-- <div class="already">
                    <div class="al-item">
                        <router-link to="ads" tag="div">
                            <i class="icon-gongneng"></i>
                            <span class="text">已购应用</span>
                        </router-link>
                    </div>
                    <div class="al-item">
                        <router-link to="ads" tag="div">
                            <i class="icon-gongnengfuben"></i>
                            <span class="text">购买应用</span>
                        </router-link>
                    </div>
                </div>-->
                <mt-navbar v-model="selected">
                    <mt-tab-item id="1">
                        <i class="icon-gongneng"></i>
                        <span class="text">已购应用</span>
                    </mt-tab-item>
                    <mt-tab-item id="2">
                        <i class="icon-gongnengfuben"></i>
                        <span class="text">购买应用</span>
                    </mt-tab-item>
                </mt-navbar>

                <mt-tab-container v-model="selected">
                    <mt-tab-container-item id="1">
                        <div class="classify">
                            <!--<div class="name border-1px">分类</div>-->
                            <div class="content" v-if="t==='1'">
                                <div class="item" v-for="(item,index) in payedApp" :key="index"  @click="Repurchase(item.id)">
                                    <!--<div class="icon-2 icon orange"></div>-->
                                    <!--<div class="icon orange"><img src="http://thumbs.dreamstime.com/z/-%E5%8A%A1%E5%88%86%E6%9E%90-27720890.jpg" alt="" style="width: 100%;height: 100%"></div>-->

                                    <div class="icon orange"><img :src="item.pic" alt="" style="width: 100%;height: 100%"></div>
                                    {{item.name}}
                                </div>
                               <!-- <div class="item">
                                    <div class="icon-guanliyuan icon pink"></div>
                                    管理
                                </div>
                                <div class="item">
                                    <div class="icon-jilu icon green"></div>
                                    记录
                                </div>
                                <div class="item">
                                    <div class="icon-xiaoshuai icon blue"></div>
                                    效率
                                </div>
                                <div class="item">
                                    <div class="icon-jia icon purple"></div>
                                    公告
                                </div>-->
                            </div>
                        </div>
                    </mt-tab-container-item>
                    <mt-tab-container-item id="2">
                        <div class="classify">
                            <!--<div class="name border-1px">分类</div>-->
                            <div class="content" v-if="t==='1'">
                                <div class="item" v-for="(item,index) in notApp" :key="index" @click="PayApp(item.id)">
                                    <!--<div class="icon-2 icon orange"></div>-->
                                    <div class="icon orange"><img :src="item.pic" alt="" style="width: 100%;height: 100%"></div>

                                    {{item.name}}
                                </div>
                               <!-- <div class="item">
                                    <div class="icon-guanliyuan icon pink"></div>
                                    管理2
                                </div>
                                <div class="item">
                                    <div class="icon-jilu icon green"></div>
                                    记录2
                                </div>-->
                            </div>
                        </div>
                    </mt-tab-container-item>
                </mt-tab-container>


                <div class="hot">
                    <div class="name border-1px">热门</div>
                    <div class="content">
                        <div class="item" v-for="(item,index) in lists" :key="index">
                            <mu-paper class="paper" :zDepth="1" >
                                <div class="img-wrap">
                                    <!--<img :src='img' alt="">-->
                                    <img src="http://192.168.3.41:8081/uoter/userfiles/1/images/menu/photo/2018/01/01142104b7cx.jpg" alt="">
                                    <div class="zz">
                                        {{item.name}}
                                    </div>
                                </div>
                                <div class="text">
                                    ￥<span class="red">{{price}}</span> / 年
                                </div>
                            </mu-paper>
                        </div>
                    </div>
                </div>
            </scroller>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import {API_HOT_LIST,IP_IMG,API_PAYED_APP,API_NOT_APP} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Toast, Indicator } from 'mint-ui';
    import { mapMutations } from 'vuex';


    export default {
	    data() {
	        return {
	            lists:[],
                payedApp:[],
                notApp:[],
                fromUrl:'',
                img:'',
                price:'',
                selected:'1',
                t:'',
                id:'',
                token:''
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        created(){
            this.t = localStorage.getItem("indexT");
            this.id = localStorage.getItem("indexID");
            console.log(this.t);
            let token = this.$login.getToken();
            this.token=token;
            this._request(token);
            this._PayedApp(token);
            this._NotPayApp(token);
        },
        beforeRouteUpdate (to, from, next) {
            let str = '/app/payapp/';
            var path=from.path;
            if(path.slice(0,12)===str||path.slice(0,16)==='/app/repurchase/'){
                // this.t = localStorage.getItem("indexT");
                this._PayedApp(this.token);
                this._NotPayApp(this.token);
            }
            next();
        },
        activated(){
            this.t = localStorage.getItem("indexT");
            this.id = localStorage.getItem("indexID");
            this._PayedApp(this.token);
            this._NotPayApp(this.token);
            console.log(this.t==='1');

        },
        methods:{
            async _NotPayApp(token){
                let res=await axios.post(API_NOT_APP,qs.stringify({
                    utoken:token,
                }));
                let data=res.data;
                console.log(data);
                this.notApp=data.list;
                this.setPayApp(data.list);

            },
            async _PayedApp(token){
                let res=await axios.post(API_PAYED_APP,qs.stringify({
                    utoken:token,
                    officeId:this.id
                }));
                let data=res.data;
                console.log(data);
                this.payedApp=data.list;
                this.setPayedApp(data.list);
                Toast({
                    message: data.msg,
                    duration: 2000
                });
            },
	        async _request(token){
	            let res=await axios.post(API_HOT_LIST,qs.stringify({
                    utoken:token
                }));
                let data=res.data;
                this.lists=data.list;
                this.img=this.lists[0].pathList[0].path;
                this.price=this.lists[0].priceList[0].money;
            },
            PayApp(id){
                console.log(id);
                this.$router.push({path:`app/payapp/${id}`})
            },
            Repurchase(id){
                this.$router.push({path:`app/repurchase/${id}`})
            },
            ...mapMutations({
                'setPayApp':"SET_PAY_APP",
                'setPayedApp':"SET_PAYED_APP"
            })
    },
	    components: {
            VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"
    .app-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index 1000
        background-color $color-bg
        .mint-searchbar
            background-color $color-black
            .mint-searchbar-cancel
                color #fff
        .mint-search
            height: 44px
        .already
            height: 33px
            display: flex
            background-color #fff
            .al-item
                text-align: center
                line-height: 33px
                color #6a6969
                font-size: 12px
                flex 1
                .text
                    display: inline-block
                    vertical-align: top
        .classify
            /*height: 90px*/
            padding: 0 20px
            margin-top: 5px
            background-color: #fff
            .name
                height: 28px
                line-height: 28px
                font-size: 12px
                color #605f5f
                border-b(#ccc)
            .content
                display: flex
                padding-top: 14px
                flex-wrap: wrap
                justify-content:center
                .item
                    text-align: center
                    color #919191
                    font-size: 12px
                    margin 0 4%
                .icon
                    margin: 0 auto 5px
                    text-align: center
                    border-radius 5px
                    background-color: red
                    line-height: 35px
                    color #fff
                    width: 35px
                    height: 35px
                    &.pink
                        background-color: #f29c9f
                    &.orange
                        background-color: $color-orange
                    &.green
                        background-color #dcd24f
                    &.purple
                        background-color: #e2acdc
                    &.blue
                        background-color: #90eae9
        .hot
            margin-top: 5px
            background-color #fff
            padding: 0 20px
            .name
                line-height: 28px
                font-size: 12px
                color #605f5f
                border-b(#ccc)
            .content
                display: flex
                flex-wrap wrap
                padding: 20px 0 0
                .item
                    width: 50%
                    height: 160px
                    margin-bottom: 20px
                    .paper
                        width: 92%
                        height: 100%
                        margin: 0 auto
                        .img-wrap
                            width: 100%
                            height: 130px
                            position: relative
                            overflow: hidden
                            img
                                width: 100%
                            .zz
                                width: 100%
                                height: 30px
                                position: absolute
                                left: 0
                                bottom: 0
                                line-height: 30px
                                padding-left: 10px
                                box-sizing border-box
                                font-size: 12px
                                color #fff
                                background-color: rgba(0, 0, 0, .5)

                        .text
                            line-height: 30px
                            padding: 0 5px
                            font-size: 12px
                            text-align: right
                            .red
                                font-size: 20px
                                color $color-orange
        .app-main
            position: absolute
            left: 0
            right: 0
            top: 50px
            bottom: 0
        .mint-searchbar
            z-index:0
</style>



