<template>
    <div class="people-wrapper">
        <v-header :path="fromPath" title="可以看的人"></v-header>
        <div class="complete" @click="Complete">完成</div>
        <scroller class="scroller">
        <div class="lsits">
            <div class="border" :key="index" v-for="(item,index) in listpeople">
                <mu-checkbox name="group" :nativeValue="item.id" v-model="list" :label="item.name" class="demo-checkbox"/>
                <span class="people">{{item.department}}</span>
            </div>
           <!-- <div class="demo-checkbox">
                你选择的是：{{list}}
            </div>-->
            <!--<mu-checkbox :label="single ? 'true' : 'false'" v-model="single"/>-->
        </div>
        </scroller>
    </div>
</template>
<script>
    import VHeader from 'components/header/header';
    import {API_COMPLETE} from 'common/js/api';
    import { mapMutations } from 'vuex';
    import qs from 'qs';
    import axios from 'axios';
    export default {
    data() {
            return {
                list: [],
                // single: false,
                fromPath:'',
                token:'',
                comId:'',
                listpeople:''
            }
        },
        beforeRouteEnter (to, from, next) {
            next( vm => {
                vm.fromPath = from.path;
            });
        },
        created(){
            this.token=this.$login.getToken();
            this.comId=this.$route.params.id;
            this._repuestPeople()
        },
        computed: {

        },
        components: {VHeader},
        methods: {
            async _repuestPeople(){
                let res=await axios.post(API_COMPLETE,qs.stringify({
                    utoken:this.token,
                    companyId:this.comId
                }));
                let data=res.data;
                console.log(data);
                this.listpeople=data.list;
            },
            Complete(){
                var names=[];
                for(var i=0;i<this.listpeople.length;i++){
                    for(var j=0;j<this.list.length;j++){
                        if(this.list[j]===this.listpeople[i].id){
                            names.push(this.listpeople[i].name);
                        }
                    }
                }
                this.setListPeople(names);
                this.setPeopleId(this.list);
                console.log(names);
                this.$router.go(-1);
            },
            ...mapMutations({
                setListPeople:'SET_LIST_PEOPLE',
                setPeopleId:'SET_PEOPLE_ID'
            })


        }

    }
</script>
<style  rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"
        .people-wrapper
            position: fixed
            left: 0
            right: 0
            top: 0
            bottom: 0
            background-color: $color-bg
            .complete
                position absolute
                top: 20px
                right 5px
                z-index 9999
                color #FFFFFF
        .scroller
            position absolute
            top:50px;
            left 0
            background-color #FFFFFF
            .border
                border-bottom:1px solid #E6E6E6
                font-size 18px
                margin 6px
        .people
            display inline-block
            height 24px;
            line-height 24px
            vertical-align top
            margin-left 15px
            font-size 15px



</style>



