<template>
    <div class="talk-wrap">
        <v-header :path="fromPath" :title="name" class="zindex"></v-header>
        <scroller>
            <div class="other" style="margin-top: 55px">
                <div class="time">1.17 10:23:23</div>
                <div class="content">
                    <img src="https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1364010267,891537106&fm=27&gp=0.jpg" @click="ToDetail">
                    <span class="msg">你你好你你好你你好你你好你你好你你好你你好你你好你你好你你好你你好你你好你你好你你好你
                <div class="Graphical"></div>
            </span>
                </div>
            </div>
            <div class="other">
                <div class="time">1.17 10:23:23</div>
                <div class="content">
                    <img src="https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1364010267,891537106&fm=27&gp=0.jpg">
                    <span class="msg">你你好你你好你你好
                <div class="Graphical"></div>
            </span>
                </div>
            </div>
            <div class="self">
                <div class="time">1.17 10:23:23</div>
                <div class="content">
                    <img src="http://img4.imgtn.bdimg.com/it/u=777874609,1596850197&fm=214&gp=0.jpg">
                    <span class="msg">hello wordhello wordhello word
                <div class="Graphical"></div>
            </span>
                </div>
            </div>
            <div class="self">
                <div class="time">1.17 10:23:23</div>
                <div class="content">
                    <img src="http://img4.imgtn.bdimg.com/it/u=777874609,1596850197&fm=214&gp=0.jpg">
                    <span class="msg">hello wordhello wordhello word
                <div class="Graphical"></div>
            </span>
                </div>
            </div>
            <div class="self">
                <div class="time">1.17 10:23:23</div>
                <div class="content">
                    <img src="http://img4.imgtn.bdimg.com/it/u=777874609,1596850197&fm=214&gp=0.jpg">
                    <span class="msg">hello wordhello wordhello word
                <div class="Graphical"></div>
            </span>
                </div>
            </div>
            <div class="self last">
                <div class="time">1.17 10:23:23</div>
                <div class="content">
                    <img src="http://img4.imgtn.bdimg.com/it/u=777874609,1596850197&fm=214&gp=0.jpg">
                    <span class="msg">hello wordhello wordhello wordhello wordhello wordhello wordhello word
                <div class="Graphical"></div>
            </span>
                </div>
            </div>
        </scroller>
            <div class="footer">
                <span class="icon-xiaolian-copy look"></span>
                <span class="icon-jiahao-my-copy more"></span>
                <input type="text" v-model="value">
                <span class="send" @click="Send">发送</span>
            </div>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import {API_MESSAGE} from 'common/js/api'
    import  axios from 'axios'
    import qs from 'qs';


	export default {
	    data() {
	        return {
	            name: '',
                fromPath: '',
                token:'',
                value:''
            }
        },
        /*beforeRouteEnter (to, from, next) {
            console.log(from.path.substring(0, from.path.lastIndexOf('/')));

            next( vm => {
                if(from.path.substring(0,from.path.lastIndexOf('/')) === '/message/maillist') {
                    vm.fromPath = '/message/maillist';
                    console.log(from.path.substring(0, from.path.lastIndexOf('/')));
                }
            });
        },*/
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromPath = from.path;
            })
        },
	    created() {
	        this.name = this.$route.params.id;
            this.token=this.$login.getToken();
            console.log(this.name);
            console.log(this.token);
        },
        mounted (){},
        methods:{
            ToDetail(){
                this.$router.push('/message/maillist/friend');
            },
            Send(){
                console.log(this.value);
                // console.log(this.name);
                axios.post(API_MESSAGE,qs.stringify({
                    utoken:this.token,
                    targetType:'users',
                    target:[this.name],
                    contentType:'txt',
                    content:this.value
                })).then((res)=> {
                    console.log(res);
                }).then((err)=>{
                    console.log(err);
                })
            }
        },
        components: {
            VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    .zindex
        position relative
        z-index 99999999
    .talk-wrap
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index 1000
        background-color: $color-bg
        .other
            margin 15px 20px
            .time
                text-align center
                color #DADADA
            .content
                display: flex;
                justify-content: column-reverse;
            img
                width 40px
                height 40px
                margin-right 20px
                border-radius 3px
            .msg
                display block
                position relative
                max-width 70%
                background-color #8DFA69
                border 1px solid rgba(170, 170, 170, 0.34)
                border-radius 5px
                padding 5px 5px
                font-size 15px
            .Graphical
                position absolute
                top 12px
                left -14px
                width: 0;
                height: 0;
                border-top: 8px solid transparent;
                border-right: 16px solid #8DFA69;
                border-bottom: 8px solid transparent;
                border-radius 5px
        .self
            margin 15px 20px
            .time
                text-align center
                color #DADADA
            .content
                display: flex;
                flex-direction: row-reverse;
            img
                width 40px
                height 40px
                margin-left 20px
                border-radius 3px
            .msg
                display block
                position relative
                max-width 70%
                background-color #FFFFFF
                border 1px solid rgba(170, 170, 170, 0.34)
                border-radius 5px
                padding 5px 5px
                font-size 15px
            .Graphical
                position absolute
                top 12px
                right  -14px
                width: 0;
                height: 0;
                border-top: 8px solid transparent;
                border-left: 16px solid #FFFFFF;
                border-bottom: 8px solid transparent;
                border-radius 5px
        .footer
            position absolute
            bottom 0
            left 0
            display flex
            align-self flex-end
            padding 5px 5px 0 5px
            width 100%
            border-top 1px solid #BBBBBB
            background-color #EEEFF3
            .look
                display inline-block
                font-size 24px
                margin-right 2px
            .more
                display inline-block
                font-size 24px
                margin-right 2px
            input
                width 70%
                height 30px
                vertical-align middle
                margin-bottom 9px
                border-radius 5px
                border 1px solid #BBBBBB
                outline:none
                margin-right 2px
            .send
                display inline-block
                width 11%
                height 30px
                background-color #42AE3C
                color #FFFFFF
                border-radius 4px
                vertical-align top
                margin-top 3px
                font-size 16px
                text-align center
                line-height 30px
    .last
        padding-bottom 200px

</style>



