<template>
    <div class="finance-wrapper">
        <v-header :path="fromUrl" title="财务"></v-header>
        <mt-navbar v-model="selected">
            <mt-tab-item id="1">收入</mt-tab-item>
            <mt-tab-item id="2">支出</mt-tab-item>
            <mt-tab-item id="3">利润分红</mt-tab-item>
            <mt-tab-item id="4">应收</mt-tab-item>
            <mt-tab-item id="5">应付</mt-tab-item>
        </mt-navbar>

        <!-- tab-container -->
        <mt-tab-container class="finance-content-wrap" v-model="selected">
            <mt-tab-container-item class="finance-item" id="1">
                <detail-msg ref="income"></detail-msg>
            </mt-tab-container-item>
            <mt-tab-container-item id="2">
                <Expend-msg ref="expend"></Expend-msg>
            </mt-tab-container-item>
            <mt-tab-container-item id="3">
                <Profit-msg ref="profit"></Profit-msg>
            </mt-tab-container-item>
            <mt-tab-container-item id="4">
                <Receivable-msg ref="receivable"></Receivable-msg>
            </mt-tab-container-item>
            <mt-tab-container-item id="5">
                <Meet-msg ref="meet"></Meet-msg>
            </mt-tab-container-item>
        </mt-tab-container>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import DetailMsg from './detail-msg';
    import ExpendMsg from './expend-msg';
    import MeetMsg from './meet-msg';
    import ReceivableMsg from './receivable-msg'
    import ProfitMsg from './profit-msg'

	export default {
	    data() {
	        return {
                selected: '1',
                fromUrl: ''
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
	    components: {
	        VHeader,
            DetailMsg,
            ExpendMsg,
            MeetMsg,
            ReceivableMsg,
            ProfitMsg
        },
        mounted(){
            this.$refs.income.judge();
            // this.$refs.expend.expend();
            // this.$refs.profit.income();
        },
        created(){
        },
        watch: {
	        selected(nowValue,lastValue) {
                // console.log(typeof (nowValue), lastValue);
                if(nowValue==='1'){
                    this.$refs.income.judge();
                }else if(nowValue==='2'){
                    this.$refs.expend.expend();
                }else if(nowValue==='3'){
                    this.$refs.profit.income();
                }else if(nowValue==='4'){
                    this.$refs.receivable.expend();
                }else if(nowValue==='5'){
                    this.$refs.meet.expend();
                }
            }
        },
        methods:{

        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .finance-wrapper
        position: fixed
        left: 0
        top: 0
        bottom: 0
        right: 0
        background-color: $color-bg
        .finance-content-wrap
            position: absolute
            left: 0
            right: 0
            top: 99px
            bottom: 0
        .mint-navbar
            background-color: $color-black
            .mint-tab-item
                &.is-selected
                    color $color-orange
                    border-bottom: 3px solid $color-orange
                    margin: 0
                    .mint-tab-item-label
                        color $color-orange
                .mint-tab-item-label
                    color #ccc

        .mint-tab-container-wrap
            height: 100%
</style>



