<template>
    <div class="add-card-wrapper">
        <v-header path="/purse" title="添加银行卡"></v-header>
        <mt-field label="持卡人" placeholder="请输入持卡人" v-model="username"></mt-field>
        <mt-field label="卡号" placeholder="请输入卡号" v-model="num"></mt-field>
        <mt-field label="开户行" placeholder="请输入开户行" v-model="open"></mt-field>
        <mt-field label="开户行地址" placeholder="请输入开户行地址" v-model="address"></mt-field>
        <div class="input-btns" @click="Determine">确定</div>

    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import {API_ADD_ACCOUNT} from 'common/js/api';
    import { Toast, Indicator } from 'mint-ui';
    import qs from 'qs';
    import axios from 'axios';
	export default {
	    data() {
	        return {
                username: '',
                num: '',
                token:'',
                open:'',
                address:''
            }
        },
        created(){
             this.token = this.$login.getToken();
        },
	    components: {
	        VHeader
        },
        methods:{
            Determine(){
                if(this.open===''||this.username===''||this.address===''){
                    alert('请把信息补充完整')
                }else{
                    var reg = /^([1-9]{1})(\d{14}|\d{18})$/;
                    if(reg.test(this.num)){
                        axios.post(API_ADD_ACCOUNT,qs.stringify({
                            utoken:this.token,
                            categoryId:'2',
                            accountType:'银行卡',
                            accountName:this.open,
                            personal:this.username,
                            account:this.num,
                            bankAddress:this.address
                        })).then((res)=>{
                            console.log(res);
                            let data=res.data;
                            if(data.result===0){
                                this.$router.push('/purse');
                            }else if(data.result===2){
                                window.localStorage.clear();
                                this.$router.push('/login');
                                Toast({
                                    message: '用户已失效,请重新登陆',
                                    duration: 2000
                                });
                            }
                        }).catch((err)=>{
                            console.log(err);
                        })
                    }else {
                        alert('请输入正确的银行卡号');
                    }
                }

            }
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    .add-card-wrapper
        position: fixed
        left: 0
        top: 0
        right: 0
        bottom: 0
        background-color $color-bg
    .input-btns
        position fixed
        bottom 50%
        width: 100%
        height: 40px
        border-radius 5px
        color #fff
        text-align: center
        line-height: 40px
        font-size: 14px
        background-color $color-orange
</style>



