import qs from 'qs';
import axios from 'axios';
import { Toast } from 'mint-ui';
import { API_EXPEND_TYPE } from "./api";

async function writeOne(url,json,vue) {
    let res= await axios.post(url, qs.stringify(json));
    let data = res.data;
    console.log(res);
    if(data.result === 2) {
        window.localStorage.clear();
        Toast({
            message: data.msg,
            duration: 2000
        });
        vue.$router.push({
            path: '/login'
        })
    }else if(data.result === 0) {
        Toast({
            message: data.msg,
            duration: 2000
        });
        vue.$router.push({
            path: '/account'
        })
    }else if(data.result === 1) {
        Toast({
            message: data.msg,
            duration: 2000
        });

    }    
}

export default writeOne;



