<template>
    <div class="asset-wrapper">
        <v-header :path="fromUrl" title="资产"></v-header>
        <router-link append to="create" class="icon-jia add"></router-link>
        <form action="#">
            <!--<mt-search v-model="value"></mt-search>-->
        </form>
        <div style="position: absolute;top:50px;bottom: 0;left: 0;right: 0">
        <scroller>
        <div class="content-list">
            <router-link tag="div" append class="content-item" v-for="(item, i) in assetArr" :key="i" :to="`as${item.id}`" >
                <div v-for="(src,index) in item.picPathList" :key="index" v-if="index===1">
                <img :src="src.picPath" >
                </div>
                <div class="asset-msg">
                    <div class="name">{{item.name}}</div>
                    <div class="state">
                        资产状态：
                        <span>{{item.useStatus}}</span>
                    </div>
                    <div class="date">添加日期：{{item.addTime}}</div>
                </div>
            </router-link>
        </div>
        </scroller>
        </div>
        <transition name="slide">
            <router-view></router-view>
        </transition>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import {API_ASSETS_LIST} from 'common/js/api';
    import { mapMutations } from 'vuex';
    import { Toast, Indicator } from 'mint-ui';
    import qs from 'qs';
    import axios from 'axios';
	export default {
	    data() {
	        return {
                value: '',
                assetArr: [],
                fromUrl: '',
                token:'',
                id:''
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },

        beforeRouteUpdate(to, from, next){
            var str=from.path.substring(from.path.length - 13, from.path.length);
             if(str==='/asset/create'){
                 this._request();
             }
	        next();
        },
        created(){
            this.token=this.$login.getToken();
            this.id=this.$route.params.id;
            console.log(this.id);
            this._request();
        },
        methods:{
	        async _request(){
                Indicator.open();
	            let res=await axios.post(API_ASSETS_LIST,qs.stringify({
                    utoken:this.token,
                    officeId:this.id
                }));
                Indicator.close();
                let data=res.data;
                if(data.result===0){
                    console.log(data);
                    this.assetArr=data.list;
                    this.setAssetDetails(data.list);
                }else if(data.result===2){
                    window.localStorage.clear();
                    this.$router.push('/login');
                    Toast({
                        message: '用户已失效，请重新登陆',
                        duration: 2000
                    });
                }

            },
            ...mapMutations({
                    setAssetDetails:'SET_ASSET_DETAILS'
                }
            )
        },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .asset-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        .add
            width: 50px
            height: 50px
            color #fff
            position: absolute
            right: 0
            top: 0
            text-align: center
            line-height: 50px
        .mint-search
            height: 44px
            .mint-searchbar
                background-color $color-black
                .mint-searchbar-cancel
                    color #fff
        .content-list
            padding: 10px 15px 0px 15p
            .content-item
                width: 100%
                height: 100px
                padding: 10px
                margin-bottom: 10px
                background-color: #fff
                border-radius 2px
                display: flex
                justify-content space-between
                img
                    width: 80px
                    height: 100%
                    border-radius 2px
                .asset-msg
                    display: flex
                    flex-direction column
                    justify-content space-between
                    flex 1
                    overflow: hidden
                    padding: 5px 0 5px 10px
                    height: 100%
                    .name
                        width: 100%
                        overflow: hidden
                        text-overflow ellipsis
                        white-space nowrap
                        font-size: 16px

</style>



