const state = {
    msg: []
};

const getters = {
    getPeopleId: state => state.msg
};

const mutations = {
    ['SET_PEOPLE_ID'](state, msg) {
        state.msg = msg;
        console.log(state.msg);
    }
};

export default {
    state,
    getters,
    mutations
}



