 <template>
    <div class="team-sub">
        <v-header path="/team" :title="getCompanyMsg.name"></v-header>
        <div>
            <team-item2 :msg="getCompanyMsg" class="bgf"></team-item2>
            <div class="twrap">
                <div class="title border-1px">
                    <span>股份概况</span>
                    <router-link append to="more" tag="span">更多></router-link>
                </div>
                <div style="display: block" class="content">
                    <div>{{names}}：{{counts}}</div>
                    <div>{{names2}}：{{counts2}}</div>
                </div>
            </div>
            <div class="twrap t2">
                <div class="title border-1px">
                    <span>企业功能</span>
                    <span>更多&gt;</span>
                </div>
                <div class="content pos">
                    <router-link tag="div" append to="files" class="app">
                        <!--<div style="background-color: #f8b551;" class="icon-dangan icon"></div>-->
                        <div class="icon"><img src="http://img.zcool.cn/community/01690955496f930000019ae92f3a4e.jpg@2o.jpg" alt="" style="width: 100%;height: 100%"></div>
                        <span>档案</span>
                    </router-link>
                    <router-link tag="div" append to="approve" class="app">
                        <div style="background-color: #90eae9;" class="icon-shenpi icon"></div>
                        <span>审批</span>
                    </router-link>
                    <router-link tag="div" append to="finance" class="app">
                        <div class="icon-shijian icon"></div>
                        <span>财务</span>
                    </router-link>
                    <router-link tag="div" append to="asset" class="app">
                        <div style="background-color: #f29c9f;" class="icon-iconfontzichan icon"></div>
                        <span>资产</span>
                    </router-link>
                    <router-link tag="div" append to="leave" class="app">
                        <div style="background-color: #7ecef4;" class="icon-qingjia icon"></div>
                        <span>申请</span>
                    </router-link>

                    <router-link tag="div" append to="job" class="app">
                        <div style="background-color: #acd598;" class="icon-gongzuo icon"></div>
                        <span>工作</span>
                    </router-link>
                    <router-link tag="div" append to="purchase" class="app">
                        <div style="background-color: #dcd24f;" class="icon-caigou icon"></div>
                        <span>采购</span>
                    </router-link>
                    <router-link tag="div" append to="customer" class="app">
                        <div style="background-color: #e2acdc;" class="icon-kehu icon"></div>
                        <span>客户</span>
                    </router-link>
                    <router-link tag="div" append to="notice" class="app">
                        <div style="background-color: #ffa61f;" class="icon-gonggao icon"></div>
                        <span>公告</span>
                    </router-link>
                    <router-link tag="div" append to="iou" class="app">
                        <div style="background-color: #97ace6;" class="icon-jietiao icon"></div>
                        <span>借条</span>
                    </router-link>
                </div>
            </div>
        </div>
        <transition name="slide">
            <router-view></router-view>
        </transition>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import TeamItem2 from '../../comp/team-item/team-item-2';
    import { mapGetters } from 'vuex';
    import  axios from 'axios'
    import qs from 'qs';
    import {API_GET_SHARES} from 'common/js/api'
    import {API_GET_GROUP_DETAIL} from 'common/js/api'

    export default {
        data(){
            return{
                isLogin: false,
                id:'',
                count:'',
                names:'',
                names2:'',
                shares:[],
                counts:'',
                counts2:''

            }
        },
        /*activated() {
            this.isLogin = this.$login.isLogin();
            let token = this.$login.getToken();
            console.log(token);
            if(this.isLogin) {
                this._request(token);
            }
        },*/
        created(){
            let id=this.getCompanyMsg.id;
            let t=this.getCompanyMsg.t;
            let token=this.$login.getToken();
            this._request(token,t,id);
            console.log(t);
        },
	    computed: {
            ...mapGetters([
                'getCompanyMsg'
            ]),
        },
        mounted() {
	        // console.log(this.getCompanyMsg);
            // console.log(this.shares);
        },
        methods:{
            async _request(token,t,id){
                let res =await axios.post(API_GET_GROUP_DETAIL,qs.stringify({
                    utoken: token,
                    teamType:t,
                    teamId:id
                }));
                let data=res.data;
                console.log(data);
                console.log(data.data.shares);
                this.shares=data.data.shares;
                this.names=this.shares[0].name;
                this.names2=this.shares[1].name;
                this.counts=this.shares[0].count;
                this.counts2=this.shares[1].count;
            },
        },
        components: {
            VHeader,
            TeamItem2
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"
    .team-sub
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        z-index: 9999
        .bgf
            background-color #f7f7f7
        .twrap
            position: relative
            padding: 0 22px
            background-color #fff
            color #605f5f
            font-size: 12px
            &.t2
                position: absolute
                left: 0
                right: 0
                top: 257px
                bottom: 0
            &:last-child
                margin-top: 6px
            .title
                display: flex
                justify-content space-between
                height: 30px
                line-height: 30px
                border-b(#ccc)
            .content
                padding: 12px 0
                box-sizing border-box
                display: flex
                flex-wrap wrap
                color #8e8d8d
                &.pos
                    padding: 28px 0
                    .app
                        height: 100%
                        width: 25%
                        margin-bottom: 10px
                        text-align: center
                        .icon
                            width: 34px
                            height: 34px
                            margin: 0 auto
                            line-height: 34px
                            color #fff
                            background-color #f08619
                            border-radius 5px
</style>



