<template>
    <div class="create-wrapper">
        <v-header :path="fromUrl" title="创建企业"></v-header>
        <div style="position: absolute;top:50px;left: 0;right: 0;bottom: 34px">
        <scroller>
        <mt-field label="企业名称" placeholder="请输入企业真实名称" v-model="name"></mt-field>
        <mt-field label="可分配利润" placeholder="请输入" v-model="profit">%</mt-field>
        <mt-field label="原股价" placeholder="请输入" v-model="money">元</mt-field>
        <img-upload style="margin-top: 10px;" ref="SrcBase" :title="title"></img-upload>
        <img-upload style="margin-top: 10px;" ref="SrcBasepic" :title="titlepic"></img-upload>
        <mt-cell title="审批流程" :value="scale" @click.native="popupVisible=true" is-link></mt-cell>
        <mt-field label="备注" placeholder="请输入备注" type="textarea" rows="4" v-model="remark"></mt-field>
        </scroller>
        </div>
        <mt-popup
            v-model="popupVisible"
            position="bottom">
            <mu-picker :slots="slots" :visible-item-count="5" @change="onValuesChange" :values="values"/>
        </mt-popup>
<!--
        <mt-cell title="我的职务" :value="zhiwu" @click.native="propleNum=true" is-link></mt-cell>
-->
        <!--<mt-popup
            v-model="propleNum"
            position="bottom">
            <mu-picker :slots="slotsNum" :visible-item-count="5" @change="onNumChange" :values="MyNum"/>
        </mt-popup>-->
        <span class="btn-create" @click="BtnCreate">确定创建</span>
    </div>
</template>
<script>
    import VHeader from 'components/header/header';
    import ImgUpload from 'components/img-upload/img-upload';
    import {API_CREATE_COM} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';

    export default {
        data() {
            return {
                title:'上传logo(只能一张)',
                titlepic:'上传营业执照(必填)',
                name:'',
                profit:'',
                money:'',
                fromUrl:'',
                remark:'',
                imglist:'',
                imglistpic:'',
                popupVisible:false,
                scale:'请选择',
                values: [],
                slots: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],

                /*zhiwu:'请选择',
                propleNum:false,
                MyNum:['负责人'],
                slotsNum:[
                    {
                        flex: 1,
                        values: ['负责人','管理层','人事','财务','行政','销售','技术','员工','技术'],
                        textAlign: 'center'
                    }
                ]*/
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        computed: {},
        components: {VHeader,ImgUpload},
        methods: {
            /*async _requestPro(){
                let res=await axios.post(API_APPROVAL_PRO,qs.stringify({
                    utoken:this.token,
                    type:'',
                    officeId:this.officeId
                }));
                let data=res.data;
                console.log(data.list);

                this.Pros=data.list;
                let arr=[];
                for(var i=0;i<this.Pros.length;i++){
                    arr.push(this.Pros[i].name)
                }
                this.slotsPro[0].values=arr;
            },*/
            onValuesChange(a) {
                this.scale = a;
                this.values = [a]
            },
            /*onNumChange(a){
             this.zhiwu=a;
             this.values=[a];
            },*/
            BtnCreate:function () {
                this.imglist=this.$refs.SrcBase.$data.imgArr;
                this.imglistpic=this.$refs.SrcBasepic.$data.imgArr;
                var file=[];
                var filepic=[];
                for(var i=0;i<this.imglist.length;i++){
                    file.push(this.imglist[i])
                }
                console.log(file);
                for(var i=0;i<1;i++){
                    filepic.push(this.imglist[i])
                }
                console.log(filepic);
                axios.post(API_CREATE_COM,qs.stringify({
                    utoken:this.$login.getToken(),
                    logoUrl:filepic,
                    grade:'1',
                    name:this.name,
                    profit:this.profit,
                    originalStockPrice:this.money,
                    licenseUrl:file,
                    remarks:this.remark,
                    // 流程ID
                    processId:'',
                }));
                this.$router.go(-1);

                // axios.post(API_PROCESS,qs.stringify({
                   
                // })).then(response=>{
                //     let res = response.data
                //     console.log(res);
                // }).catch(err=>{
                //     console.log(res);
                // });
            },
            change:function () {

            }
        }
    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"
    .create-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        .btn-create
            display block
            width 100%
            text-align center
            background-color #F08619
            position fixed
            bottom:0
            left 0
            color #FFFFFF
            font-size 16px
            padding 5px 0


</style>



