<template>
    <div class="join-wrapper">
        <v-header path="/team" title="加入团队"></v-header>
        <form action="#">
            <mt-search @input.native="inputHandler" v-model="value"></mt-search>
        </form>
        <div style="position:absolute;top:90px;left: 0;right: 0;bottom: 0">
        <scroller>
        <div class="join-result">
            <div class="join-item" @click="toCompany(item.teamId,item.companyId)" v-for="item in list" :key="item.teamId">
                <div class="join-name">
                   <!-- <div class="join-avatar">
                        <img :src="item.teamLogoUrl" alt="">
                    </div>-->
                    <div style="font-size: 16px;">部门：{{item.teamName}}</div>
                    <div style="margin-top: 6px">{{item.companyName}}</div>
                </div>
                <div class="join-content">
                    创建时间：{{item.addDate }}
                </div>
            </div>
        </div>
        </scroller>
        </div>
        <transition name="slide">
            <router-view></router-view>
        </transition>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import {API_SEARCH_TEAM} from 'common/js/api';
    import _ from 'lodash';
    import qs from 'qs';
    import axios from 'axios'
    export default {
        data() {
            return {
                value: '',
                token:'',
                list:[]
            }
        },
        methods: {
            toCompany(id,companyId) {
                this.$router.push({
                    path: `join/${id}/${companyId}`
                })
            },
            inputHandler: _.debounce(function () {
                this.token = this.$login.getToken();
                axios.post(API_SEARCH_TEAM, qs.stringify({
                    utoken: this.token,
                    teamType:'2',
                    teamName: this.value
                })).then((res) => {
                    let data = res.data;
                    if(data.result===2){
                        window.localStorage.clear();
                        this.$router.push('/login');
                        Toast({
                            message: '用户已失效,请重新登陆',
                            duration: 2000
                        });
                    }else if(data.result===0){
                        console.log(data);
                        this.list=data.list;
                    }
                }).catch((err) => {
                    console.log(err);
                });
            }, 1000),

        },
        components: {
            VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .join-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        z-index: 1000
        .mint-search
            height: 44px
            .mint-searchbar
                background-color $color-black
                .mint-searchbar-cancel
                    color #fff
        .join-result
            padding: 20px 20px 0
            .join-item
                width: 100%
                height: 90px
                padding: 10px
                background-color: #fff
                margin-bottom 20px
                .join-name
                    position: relative
                    height: 31px
                    line-height: 16px
                    /*padding-left: 40px*/
                    .join-avatar
                        width: 31px
                        height: 31px
                        position: absolute
                        left: 0
                        top: 0
                        border-radius 2px
                        overflow: hidden
                        img
                            width: 100%
                            height: 100%

                .join-content
                    width: 100%
                    padding-top: 12px
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 3;
                    overflow: hidden;
                    word-wrap: break-word
                    height: 68px
                    color #6e6e6e
</style>



