const state = {
    billList: {

    }
};

const getters = {
    getBillList: state => state.billList
};

const mutations = {
    ['SET_BILL_LIST'](state, billList) {
        state.billList = billList;
    }
};

export default {
    state,
    getters,
    mutations
}



