<template>
    <div class="recharge-wrapper">
        <v-header path="/purse" title="充值"></v-header>
        <div class="recharge-main">
            <div class="content">
                <div class="con1 choice-card border-1px" @click="open">
                    <div class="card-name"><!--储蓄卡--></div>
                    <div class="card-num">
                        <div>{{address}}</div>
                        <div>单日交易限额 ￥50000.00</div>
                    </div>
                </div>
                <div class="con1 input-num border-1px">
                    <div class="input-name">充值金额</div>
                    <div class="input">
                        <span>￥</span>
                        <input type="text" @input="controlPrice" class="input-k" v-model="price" />
                    </div>
                </div>
                <div class="con1">
                    <div class="input-btn" @click="Next">下一步</div>
                </div>
            </div>
        </div>
        <mu-popup position="bottom" popupClass="demo-popup-bottom" :open="bottomPopup" @close="close">
            <mu-picker :slots="addressSlots" :visible-item-count="5" @change="addressChange" :values="address"/>
        </mu-popup>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import {API_RECHARGE} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { mapGetters } from 'vuex';
    import { Toast, Indicator } from 'mint-ui';


    export default {
        data() {
            return {
                bottomPopup: false,
                addressSlots: [
                    {
                        width: '100%',
                        textAlign: 'center',
                        values: [],
                        token:'',
                    }
                ],
                address: [],
                price: '',
                BankCard:'',
                accountId:''
            }
        },
        created(){
            this.token = this.$login.getToken();
            let BankCard=this.getBankCard;
            console.log(BankCard);
            this.BankCard=this.getBankCard;
            let bankcard=[];
            let id=[];
            for(let i=0;i<BankCard.length;i++){
                console.log(BankCard[i]);
                bankcard.push(BankCard[i].name);
                id.push(BankCard[i].id);
            }
            console.log(bankcard,id);
            this.addressSlots[0].values=bankcard;
            this.address=Array.of(bankcard[0]);
            console.log(this.getBankCard);
            this.accountId=id[0];
        },
        computed:{
            ...mapGetters([
                    'getBankCard'
                ])
        },
        methods: {
            open() {
                this.bottomPopup = true;
            },
            close() {
                this.bottomPopup = false;
            },
            addressChange(a,i) {
                this.address = [a];
                console.log(this.address);
                for(let i=0;i<this.BankCard.length;i++){
                    if(this.BankCard[i].name==this.address){
                        console.log(2);
                        console.log(this.BankCard[i].id);
                        this.accountId=this.BankCard[i].id;
                    }
                }
            },
            controlPrice() {
                if( ! /^-?\d+\.?\d{0,2}$/.test(this.price)){
                    var s = this.price;
                    this.price=s.substring(0,s.length-1);
                }
            },

            Next(){
                axios.post(API_RECHARGE,qs.stringify({
                     utoken:this.token,
                     accountId:this.accountId,
                     money:this.price
                })).then((res)=>{
                    let data=res.data;
                    if(data.result===0){
                        // window.location.href='/purse#/purse';
                        this.$router.go(-1);
                    }else if(data.result===2){
                        window.localStorage.clear();
                        this.$router.push('/login');
                        Toast({
                            message: '用户已失效,请重新登陆',
                            duration: 2000
                        });
                    }
                    // console.log(window.location.href);
                    // this.$router.push('/purse');


                }).catch(err=>{
                    console.log(err);
                })
            }
        },
        components: {
            VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"

    .recharge-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color $color-bg
        .recharge-main
            padding: 10px 10px 0
            .content
                width: 100%
                padding: 0 20px
                box-sizing border-box
                background-color #fff
            .con1
                padding: 10px 0
                border-b(#ccc)
                font-size: 0
                &:last-child
                    padding-top: 15px
                    &:after
                        height: 0
                [class^=card-]
                    display: inline-block
                    vertical-align: top
                    font-size: 12px
                .card-name
                    width: 80px
                .input-name
                    font-size: 12px
                .input
                    font-size: 24px
                    .input-k
                        border none
                        outline none
                .input-btn
                    width: 100%
                    height: 40px
                    border-radius 5px
                    color #fff
                    text-align: center
                    line-height: 40px
                    font-size: 14px
                    background-color $color-orange

    .demo-popup-bottom
        width: 100%
</style>



