<template>
    <div class="company-join-wrapper">
        <v-header path="/team/join" title="公司详情"></v-header>
        <!--<team-item3 class="top-msg"></team-item3>-->

        <div class="item-w-3">
            <div class="item-main">
                <img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1512971154554&di=6eccc0c06a44d1ea11f4a91c4099da6c&imgtype=jpg&src=http%3A%2F%2Fimg1.imgtn.bdimg.com%2Fit%2Fu%3D1984422401%2C3186567680%26fm%3D214%26gp%3D0.jpg" alt="">
                <div class="item-text">
                    <div class="item-name">{{info.name}}</div>
                    <div style="margin:9px 0" class="item-job">
                        <div class="icon-e66f icon"></div>
                        <span>创建时间：{{info.addDate}}</span>
                    </div>
                    <div class="item-job">
                        <div class="icon-shijian icon"></div>
                        <span>负责人：{{info.master}}</span>
                    </div>
                </div>
            </div>
        </div>

        <div class="abstract">
            <div class="name">公司简介</div>
            <div class="content">
                <div class="base-msg border-1px">
                    <div class="border-1px my">
                        <div class="msg-item">机构编码：{{info.code}}</div>
                        <div class="msg-item">联系方式：{{info.phone}}</div>
                        <div class="msg-item" style="width: 100%;">地址：{{info.address}}</div>
                    </div>
                </div>
                <div class="msg-content">
                    <div>
                        图文介绍
                    </div>
                    <img :src="info.licenseUrl" alt="" class="img-pic">
                </div>
            </div>
        </div>
        <router-link append tag="div" to="joinfield" class="submit-btn">申请加入</router-link>
        <transition name="slide">
            <router-view></router-view>
        </transition>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import {API_GET_GROUP_DETAIL} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';

	export default {
	    data(){
	        return{
	            comId:'',
                info:{},
            }
        },
	    computed:{

        },
        created(){
            this.comId=this.$route.params.comid;
            console.log(this.comId);
            this._request();
        },
        methods:{
            async _request(){
                let res =await axios.post(API_GET_GROUP_DETAIL,qs.stringify({
                    utoken: this.$login.getToken(),
                    teamType:'1',
                    teamId:this.comId
                }));
                let data=res.data;
                if(data.result===0){
                    console.log(data);
                    this.info=data.data.teamInfo;
                    console.log(data.teamInfo);
                }else if(data.result===2){
                    window.localStorage.clear();
                    this.$router.push('/login');
                    Toast({
                        message: '用户已失效,请重新登陆',
                        duration: 2000
                    });
                }
            },
        },
	    components: {
	        VHeader,
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"

    .company-join-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index: 1000
        background-color: $color-bg
        .top-msg
            background-color: $color-black
        .abstract
            padding: 0 16px
            position: absolute
            top: 158px
            bottom: 0
            left: 0
            right: 0
            .name
                padding: 10px 0
            .content
                .base-msg
                    background-color: #fff
                    padding: 8px
                    border-b(#eee)
                    .my
                        display: flex
                        flex-wrap wrap
                        .msg-item
                            width: 50%
                            line-height: 30px
                .msg-content
                    color #666
                    padding: 8px
                    background-color: #fff
        .submit-btn
            position: absolute
            left: 0
            bottom: 0
            right: 0
            color #fff
            text-align: center
            line-height: 40px
            background-color: $color-orange

    .item-w-3
        width: 100%
        height: 108px
        padding: 0 16px
        box-sizing border-box
        background #000000
        .item-main
            width: 100%
            height: 100%
            padding:18px 0
            box-sizing border-box
            font-size: 0
            &:last-child
                &::after
                    display: none
            img
                width: 73px
                height: 73px
                border-radius 5px
                vertical-align: top
                margin-right: 16px
            .item-text
                display: inline-block
                vertical-align: top
                height: 73px
                font-size: 12px
                color #fff
                .item-job
                    .icon
                        display: inline-block
                        vertical-align: top
                        width: 18px
                        height: 18px
                        background-color #fff
                        border-radius 50%
                        font-size: 12px
                        color $color-orange
                        text-align: center
                        line-height: 18px
    .img-pic
        width: 70px;
        height 70px;

</style>



