const state = {
    msg: []
};

const getters = {
    getPayApp: state => state.msg
};

const mutations = {
    ['SET_PAY_APP'](state, msg) {
        state.msg = msg;
        console.log(state.msg);
    }
};

export default {
    state,
    getters,
    mutations
}



