<template>
    <div class="msg-wrapper">
        <v-header title="消息" class="con" style="z-index: 22"></v-header>
        <div v-if="isLogin">
        <nav-bar class="con-nav" style="z-index: 9"></nav-bar>
        <div class="mail" tag="div" @click="Tongxun" style="z-index: 23">
            <i class="icon-tongxunlu con-pro"></i>
        </div>
        <router-view></router-view>
        </div>
        <not-login v-else></not-login>
    </div>
</template>
<script>
    import Vue from 'vue';
    import VHeader from 'components/header/header';
    import NavBar from './comp/nav-bar/nav-bar';
    import NotLogin from 'components/not-login/not-login';
    import { Toast } from 'mint-ui';
    import { mapMutations } from 'vuex';
	export default {
	    data() {
	        return {
                isLogin: false,
                mobiletype:''
            }
        },
	    created() {
            this.isLogin = this.$login.isLogin();
	        let self = this;
	        let encryptUsername = 'uoter';
            Vue.prototype.$im = new WebIM.connection({
                isMultiLoginSessions: WebIM.config.isMultiLoginSessions,
                https: typeof WebIM.config.https === 'boolean' ? WebIM.config.https : location.protocol === 'https:',
                url: WebIM.config.xmppURL,
                heartBeatWait: WebIM.config.heartBeatWait,
                autoReconnectNumMax: WebIM.config.autoReconnectNumMax,
                autoReconnectInterval: WebIM.config.autoReconnectInterval,
                apiUrl: WebIM.config.apiURL,
                isAutoLogin: true
            });
            this.$im.listen({
                onOpened(msg) {
                    console.log('已连接');
                   self.$store.dispatch('getFriendList');
                    self.setConnState(true);
                    self.$store.dispatch('getFriendList')
                        .then(() => {
                            self.setConnState(Math.random())
                        })
                },
                onTextMessage(msg) {
                    console.log(msg)
                },
                onRoster: function ( message ) {
                    console.log(message);
                    self.$store.dispatch('getFriendList')
                        .then(() => {
                            self.setConnState(Math.random())
                        })
                }
            });

            let option = {
                user: 'admin',
                pwd: 'admin',
                apiUrl: WebIM.config.apiURL,
                appKey: WebIM.config.appkey,
                success(token) {
                    let tokenStr = token.access_token;
                    WebIM.utils.setCookie(`webim_${encryptUsername}`, tokenStr, 1);
                }
            };
            this.$im.open(option);
            // this._Sjxh();
        },
        activated() {
            this.isLogin = this.$login.isLogin();
        },
	 /* created(){
          var conn = new WebIM.connection({
              isMultiLoginSessions: WebIM.config.isMultiLoginSessions,
              https: typeof WebIM.config.https === 'boolean' ? WebIM.config.https : location.protocol === 'https:',
              url: WebIM.config.xmppURL,
              heartBeatWait: WebIM.config.heartBeatWait,
              autoReconnectNumMax: WebIM.config.autoReconnectNumMax,
              autoReconnectInterval: WebIM.config.autoReconnectInterval,
              apiUrl: WebIM.config.apiURL,
              isAutoLogin: true
          });
          conn.listen({
              onOpened:function (message) {
                  console.log('已连接');

              }
          })
      },*/
        methods: {
            ...mapMutations({
                setFriendList: 'SET_FRIEND_LIST',
                setConnState: 'SET_CONN_STATE'
            }),
            // Tongxun(){
            //     if(this.mobiletype=='android'){
            //         window.android.addressBookAndroid()
            //     }else if(this.mobiletype=='iphone') {
            //         this.addressBookIphone()
            //     }
            // },
            Tongxun(){
                 var u = navigator.userAgent;
                if (u.indexOf('Android') > -1 || u.indexOf('Linux') > -1) {
                    window.android.addressBookAndroid()
                } else if (u.indexOf('iPhone') > -1) {
                    addressBookIphone();
                }
            },
            // addressBookIphone(){
            //      window.location.href="secondViewController"  
            //     }
        },
	    components: {
            VHeader,
            NavBar,
            NotLogin
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .con
        position fixed
        top 0
        left 0
    .con-nav
        position fixed
        top 50px
        left 0
    .con-pro
        position fixed
        top 0
        right  5
    .msg-wrapper
        position: absolute
        left: 0
        top: 0
        right: 0
        bottom: 55px
        background-color $color-bg
        .mail
            position: absolute
            right: 0
            top: 0
            width: 50px
            text-align: center
            line-height: 50px
            color #fff
</style>



