<template>
    <div class="team-item-wrapper">
        <div class="item-header">我的{{title}}</div>
        <team-item2 v-for="(item, index) in list" :msg="item" :key="index" @goToSubPage="goToSubPage" :needClick="true"></team-item2>
    </div>
</template>

<script>
    import TeamItem2 from './team-item-2'
	export default {
	    props: {
	        list: {
	            type: Array,
                default() {
	                return []
                }
            },
	        title: {
	            type: String,
                default: '企业'
            },
            subUrl: {
	            type: String,
                default: ''
            }
        },
        methods: {
            goToSubPage(id) {
                this.$emit('goToSubPage',id)
            }
        },
        components: {
            TeamItem2
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/mixin"

    .team-item-wrapper
        color #6e6e6e
        font-size: 12px
        background-color #fff
    .item-header
        height: 35px
        background-color: #eee
        line-height: 35px
        padding-left: 16px
        box-sizing border-box
        font-size: 12px
    .item-w
        width: 100%
        height: 108px
        padding: 0 16px
        box-sizing border-box
        .item-main
            width: 100%
            height: 100%
            padding:18px 0
            box-sizing border-box
            border-b(#ccc)
            font-size: 0
            img
                width: 73px
                height: 73px
                border-radius 5px
                vertical-align: top
                margin-right: 16px
            .item-text
                display: inline-block
                vertical-align: top
                height: 73px
                font-size: 12px
                color #464545
                .item-job
                    .icon
                        display: inline-block
                        vertical-align: top
                        width: 18px
                        height: 18px
                        background-color #dcdcdc
                        border-radius 50%
                        font-size: 12px
                        color #575757
                        text-align: center
                        line-height: 18px
</style>



