<template>
    <div class="comment-wrapper">
        <v-header path="/mine" title="我的收藏"></v-header>
        <!-- navbar -->
        <mt-navbar class="page-part" v-model="selected">
            <mt-tab-item style="color:#000;" id="1">物品收藏</mt-tab-item>
            <mt-tab-item style="color:#000"  id="2">店铺收藏</mt-tab-item>
        </mt-navbar>
        <mt-tab-container v-model="selected">
            <mt-tab-container-item id="1">
                <div v-for="(item,index) in list" :key="index" class="colle">
                    <div @click="collectiondetails" class="colle-left">
                        <img src="http://www.qqzhi.com/uploadpic/2014-09-23/000247589.jpg" alt="">
                    </div>
                    <div @click="collectiondetails(item.assetId,item.assetId)" class="details">{{item.assetName}}</div>
                    <span class="money">￥{{item.assetPrice}}</span>
                    <span class="outcolle" @click="outcolle(item.collectionId)">
                        <a href="javascript:;">取消收藏</a>
                    </span>
                </div>
                <p v-if="list==''" class="text">暂无收藏，快去店铺收藏吧！</p>
            </mt-tab-container-item>
            <mt-tab-container-item id="2">
                <div v-for="(item,index) in listasset" :key="index" class="colle">
                    <div @click="detailsdp(item.assetId)" class="colle-left">
                        <img src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=16550438,2220103346&fm=27&gp=0.jpg" alt="">
                    </div>
                    <div @click="detailsdp(item.assetId)" class="details">{{item.officeName}}</div>
                    <!-- <span class="money">￥{{item.assetPrice}}</span> -->
                    <span class="outcolle" @click="array(item.collectionId)">
                        <a href="javascript:;">取消收藏</a>
                    </span>
                </div>
                <p v-if="listasset==''" class="text">暂无收藏，快去店铺收藏吧！</p>
            </mt-tab-container-item>
        </mt-tab-container>
    </div>
    
</template>

<script>
    import VHeader from 'components/header/header';
    import {API_COLLECTION,API_OUTCOLLECTION} from 'common/js/api';
    import qs from 'qs';
    import { Toast, Indicator,MessageBox  } from 'mint-ui';
    import axios from 'axios';
	export default {
     name: 'page-navbar',  
	    data() {
	        return {
            selected: '1'  ,
             active: 'tab-container2',
             list:[],
             listasset:[],
            }
        },
        created(){
          this.taggle()
        },
        updated(){
          
        },
      methods:{
        async taggle(){
          axios.post(API_COLLECTION,qs.stringify({
            utoken:this.$login.getToken(),
            })).then(response=>{
                let res = response.data
                console.log(res)
                this.list = res.listasset;
                this.listasset = res.listoffice;
            }).catch(err=>{
                console.log(err)                
            });
        },

        async outcolle(id){
          console.log(id)
          axios.post(API_OUTCOLLECTION,qs.stringify({
            utoken:this.$login.getToken(),
            collectionId:id
            })).then(response=>{
                let res = response.data
                console.log(res)
                this.taggle()
                // Toast({
                //       message:res.msg
                //     })
            }).catch(err=>{
                console.log(err)                
            });
        },
        async array(id){
          console.log(id)
          axios.post(API_OUTCOLLECTION,qs.stringify({
            utoken:this.$login.getToken(),
            collectionId:id
            })).then(response=>{
                let res = response.data
                 this.taggle()
                // Toast({
                //       message:res.msg
                //     })
            }).catch(err=>{
                console.log(err)                
            });
        },
        collectiondetails(id,a){
          console.log(id)
          this.$router.push({
            path: `/index/seller${id}/goods`
          })
        },
        detailsdp(id){
          console.log(id)
          this.$router.push({
            path: `/index/seller${id}/goods`
          })
        }
      },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    .mint-tab-item-label
      font-size 16px
    .mint-navbar .mint-tab-item.is-selected {
        border-bottom: 3px solid #f08619 ;
        color: #f08619 ;
        margin-bottom: 0px;
    }
    .comment-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        .nav 
          width 100%
          height 7%
        .left
          width 50%
          float left
          height 100%
        .right
          width 50%
          float right
          height 100% 
        .record-btn
          width: 100px
          padding: 5px 10px
          border-radius 2px
          border: 1px solid $color-orange
          background-color: transparent
          color $color-orange
          font-size: 12px
          height 30px
        .colle
          width: 100%;
          height: 100px;
          position relative
          border-top 1px solid #ccc
          border-bottom  2px solid #eee
          background #fff
        .colle-left
            display inline-block
            width: 80px;
            height: 100px;
            margin 10px
            img 
              width 100%
        .details
          display: inline-block;
          width: 255px;
          height: 40px;
          margin-bottom: 40px;
          overflow: ellipsis;
          overflow: hidden;
          font: 600 16px '微软雅黑';
        .money
          position absolute
          left: 100px;
          bottom: 10px;
        .outcolle
          position absolute
          bottom: 5px;
          right: 20px;
          text-decoration: underline
        .text
            text-align: center;
            margin-top: 60%;
            
</style>



