<template>
    <div class="hairred-wrapper">
        <v-header :path="fromUrl" title="发红包"></v-header>
        <div class="redbag">
            <mt-cell title="选择商铺" :value="selectedShopValue" @click.native="shopPopupVisible=true" is-link></mt-cell>
            <mt-cell title="红包类型" :value="leaveValue" @click.native="popupVisible=true" is-link></mt-cell>
            <mt-cell title="红包发生区域" :value="DiquValueType" @click.native="DiquupVisible=true" is-link></mt-cell>
            <mt-cell title="可领取的用户类型" :value="leaveValueType" @click.native="popupVisibleType=true" is-link></mt-cell>

            <mt-field label="金额" type="number" placeholder="请输入金额" v-model="money"></mt-field>
            <mt-field label="红包个数" type="number" placeholder="请输入红包个数" v-model="num"></mt-field>

            <mt-cell title="结束时间" :value="endTime" isLink @click.native="showDatePicker(false)"></mt-cell>

            <mt-field label="备注" placeholder="恭喜发财,大吉大利" v-model="Remarks"></mt-field>
        </div>
        <mt-popup
            v-model="shopPopupVisible"
            position="bottom">
            <mu-picker :slots="shopSlots" :visible-item-count="3" @change="onShopChange" :values="selectedShopArr"/>
        </mt-popup>
        <mt-popup
            v-model="popupVisible"
            position="bottom">
            <mu-picker :slots="slots" :visible-item-count="3" @change="onValuesChange" :values="values"/>
        </mt-popup>
        <mt-popup
            v-model="DiquupVisible"
            position="bottom">
            <mu-picker :slots="slots" :visible-item-count="5" @change="onValuesChange" :values="values"/>
        </mt-popup>
        <mt-popup
            v-model="popupVisibleType"
            position="bottom">
            <mu-picker :slots="slotsType" :visible-item-count="3" @change="onValuesChangeType" :values="valuesType"/>
        </mt-popup>

        <mt-datetime-picker
            ref="picker"
            type="date"
            @confirm="confirmHandler"
            v-model="pickerValue">
        </mt-datetime-picker>
        <span class="sub" @click="sub">发送</span>
    </div>
</template>
<script>
    import VHeader from 'components/header/header';
    import { mapGetters } from 'vuex';
    import {API_WELFARE,API_SURE_TYPE,API_SUB_REDBAG, API_GET_USER_MERCHANT, API_APPROVAL_PRO} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Toast } from 'mint-ui';

    export default {
        data() {
            return {
                fromUrl:'',
                money:'',
                num:'',
                Remarks:'',
                surePeopleType:[],

                endTime: '请选择结束时间',
                pickerValue: new Date(),

                applyUser:'',
                bonusType:'',
                peopleTypeType:[],
                leaveValueType:'请选择（必选）',
                popupVisibleType: false,
                valuesType: [],
                slotsType: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],

                peopleType:[],
                leaveValue:'请选择（必选）',
                popupVisible: false,
                values: [],
                slots: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],
                shopList:[],
                selectedShop:null,
                selectedShopValue:'请选择商铺',
                shopPopupVisible:false,
                selectedShopArr:[],
                // processDefId:'',
                lng:'',
                lat:'',
                DiquValueType:'请选择（必选）',
                DiquupVisible:false
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        components: {VHeader},
        created(){
            this._request();
            this._requestPeople();
            this.getUserMerchant()
        },
        mounted(){
            this.lng = this.$route.query.lng
            this.lat = this.$route.query.lat
            
        },
        computed: {
            shopSlots () {
                let shopNames = []
                for(var i=0;i<this.shopList.length;i++){
                    shopNames.push(this.shopList[i].name)
                }
                return [{
                    flex: 1,
                    values: shopNames,
                    textAlign: 'center'
                }]
            }
        },
        methods: {
            //获取用户商家
            getUserMerchant(){
                axios.post(API_GET_USER_MERCHANT, qs.stringify({
                    utoken:this.$login.getToken()
                })).then((response) => {
                    let res = response.data
                    this.shopList = res.list
                }, function(error){
                    console.log('error')
                    console.log(error)
                })
            },

            onShopChange(val){
                this.selectedShopValue = val;
                this.selectedShopArr = [val];
                let selected = this.shopList.filter((item,index) => {
                    return item.name == val;
                });
                this.selectedShop = selected[0]
                // axios.post(API_APPROVAL_PRO, qs.stringify({
                //     type:'8',
                //     officeId:this.selectedShop.id,
                //     utoken:this.$login.getToken()
                // })).then((response) => {
                //     let res = response.data
                //     this.processDefId = res.list[0].value
                // }, function(error){
                //     console.log(error)
                // })
            },
            onValuesChange(a) {
                console.log(a);
                this.leaveValue = a;
                this.values = [a];
                for(var i=0;i<this.peopleType.length;i++){
                    if(this.values[0]===this.peopleType[i].label){
                        this.bonusType=this.peopleType[i].value;
                    }
                }
                // 红包类型
                console.log(this.bonusType);
            },
            onValuesChangeType(b) {
                this.leaveValueType = b;
                this.valuesType = [b];
                for(var i=0;i<this.surePeopleType.length;i++){
                    if(this.valuesType[0]===this.surePeopleType[i].label){
                        this.applyUser=this.surePeopleType[i].value;
                    }
                }
                // 可领取的用户类型
                console.log(this.applyUser);
            },

            sub(){
                if(!this.selectedShop){
                    Toast({
                        message:'请选择商铺'
                    })
                    return false;
                }
                axios.post(API_SUB_REDBAG,qs.stringify({
                    officeId:this.selectedShop.id,
                    amount:this.num,
                    locationX:this.lng,
                    locationY:this.lat,
                    money:this.money,
                    address:this.selectedShop.name,
                    type:this.bonusType,
                    applyDate:this.endTime,
                    applyUser:this.applyUser,
                    remarks:this.Remarks,
                    utoken:this.$login.getToken(),
                    // processDefId:this.processDefId
                })).then(response=>{
                    let res = response.data
                    if(res.result == 0){
                        this.$emit('sendBonus')
                        Toast({
                            message:'发送成功'
                        })
                        this.$router.push({path: '/index'});
                    }else{
                        Toast({
                            message:res.msg
                        })
                    }
                    
                    
                }).catch(err=>{
                    Toast({
                        message:'发送失败'
                    })
                });
            },
            async _request(){
                let res=await axios.post(API_WELFARE,qs.stringify({
                    utoken:this.$login.getToken()
                }));
                let data=res.data;
                this.peopleType=data.list;
                var arr=[];
                for(var i=0;i<data.list.length;i++){
                    arr.push(data.list[i].label);
                }
                this.slots[0].values=arr;
            },
            async _requestPeople(){
                let res=await axios.post(API_SURE_TYPE,qs.stringify({
                    utoken:this.$login.getToken()
                }));
                let data=res.data;
                console.log('surePeopleType')
                console.log(data);
                this.surePeopleType=data.list;
                let arr=[];
                for(var i=0;i<this.surePeopleType.length;i++){
                    //arr.push(this.surePeopleType[i].label);
                    this.slotsType[0].values.push(this.surePeopleType[i].label)
                }
                //this.slotsType[0].values=arr;
            },
            confirmHandler(value) {
                    this.endTime = this._dateFormat(value);
                    // 截至时间
                    console.log(this.endTime);
            },
            showDatePicker(bOk) {
                this.$refs.picker.open();
                this.bOk = bOk
            },
            _dateFormat(date) {
                var month;var num;var date2;var num2;
                if(date.getMonth()+1<10&&date.getDate()>9){
                    num=date.getMonth()+1;
                    month='0'+num;
                    return `${date.getFullYear()}-${month}-${date.getDate()}`;
                }else if(date.getMonth()+1>=10&&date.getDate()<10){
                    num2=date.getDate();
                    date2='0'+num2;
                    return `${date.getFullYear()}-${date.getMonth()+1}-${date2}`;
                }
                else if(date.getMonth()+1<10&&date.getDate()<10){
                    num=date.getMonth()+1;
                    month='0'+num;
                    num2=date.getDate();
                    date2='0'+num2;
                    return `${date.getFullYear()}-${month}-${date2}`;
                }else if(date.getMonth()+1>=10&&date.getDate()>9){
                    return `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
                }
                // return `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
            },
        }
    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"
    .hairred-wrapper
        width 100%
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        z-index:889
        .redbag
            background-color red
        .sub
            color #ffffff
            position absolute
            top:15px
            right 5px
</style>



