<template>
    <div class="team-wrapper">
        <v-header :td="isLogin" title="团队"></v-header>
        <div class="sw" v-if="isLogin">
            <scroller>
                <team-item v-if="listCompany.length" :list="listCompany" @goToSubPage="goToSubPage" subUrl="asd"></team-item>

                <!--<team-item v-if="listTeam.length" :list="listTeam" title="团队" @goToSubPage="goToSubPage" subUrl="as"></team-item>-->
                <team-item v-if="listTeam.length" :list="listTeam" title="团队" subUrl="as"></team-item>

                <!--<team-item v-if="listGroup.length" :list="listGroup" title="群" @goToSubPage="goToSubPage" subUrl="asd"></team-item>-->
                <team-item v-if="listGroup.length" :list="listGroup" title="群" subUrl="asd"></team-item>
            </scroller>
        </div>
        <not-login v-else></not-login>
        <transition name="slide">
            <router-view></router-view>
        </transition>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import TeamItem from './comp/team-item/team-item';
    import NotLogin from 'components/not-login/not-login';
    import { API_TEAMLIST } from 'common/js/api';
    import { Toast, Indicator } from 'mint-ui';
    import qs from 'qs';
    import axios from 'axios';

	export default {
	    data() {
	        return {
                isLogin: false,
                listCompany: [],
                listTeam: [],
                listGroup: []
            }
        },
        activated() {
	        this.isLogin = this.$login.isLogin();
            let token = this.$login.getToken();
            if(this.isLogin) {
                this._request(token);
            }
        },
	    methods: {
	        async _request(token) {
                Indicator.open();
                let res = await axios.post(API_TEAMLIST, qs.stringify({
                    utoken: token
                }));
                Indicator.close();
                let data = res.data;
                console.log(data);
                switch (data.result) {
                    case 0:
                        console.log(data);
                        this.listCompany = data.listCompany;
                        this.listTeam = data.listTeam;
                        this.listGroup = data.listGroup;
                        break;
                    case 2:
                        window.localStorage.clear();
                        this.isLogin = false;
                        Toast({
                            message: data.msg,
                            duration: 2000
                        });
                        break;
                }
            },
            goToSubPage(url) {
                this.$router.push({
                    path: `team/item/${url}`
                })
            }
        },
	    components: {
	        VHeader,
            TeamItem,
            NotLogin
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    .team-wrapper
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 55px;
        background-color: $color-bg
    .sw
        position: absolute
        left: 0
        right: 0
        top: 50px
        bottom: 0
        background-color  #eeeff3
</style>



