        <router-link tag="div" to="/team">
            <i class="icon-tuandui"></i>
            <span>团队</span>
        </router-link>
        <router-link class="index" tag="div" to="/index"></router-link>
        <router-link tag="div" to="/message">
            <i class="icon-xiaoxi"></i>
            <span>消息</span>
        </router-link>
        <router-link tag="div" to="/mine">
            <i class="icon-wode"></i>
            <span>我的</span>
        </router-link>
    </div>
</template>

<script>
	export default {}
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/mixin"
    @import "~common/stylus/variable"
    .tab-wrapper
        width: 100%
        height: 55px
        background #fff
        display: flex
        justify-content space-around
        font-size: 12px
        color $f-color-gray
        border-t($color-orange)
        .router-link-active
            color $color-orange
        i[class^=icon-],span
            display: block
        i
            margin-bottom: 2px
        div
            flex 1
            text-align: center
            display: flex
            flex-direction column
            align-items center
            justify-content center
        .index
            flex 0 0 59px
            height: 59px
            margin-top: -13px
            background $color-orange url("../../../static/uoter.png") no-repeat center / 50%
            border-radius 50%
            z-index:888
</style>



