<template>
    <div class="header">
        <div class="go-back lit">
            <i class="material-icons" @click="goBack" v-if="path">arrow_back_ios</i>
        </div>
        <h2 class="title">{{title}}</h2>
        <div class="z lit">
            <i class="material-icons" v-if="zm" @click="rightPop">filter_1</i>
            <mu-icon-menu
                icon="more_vert"
                menuClass="kkkkkk"
                class="more"
                v-if="td"
            >
                <mu-menu-item title="创建团队" to="/team/create" />
                <mu-menu-item title="加入团队" to="/team/join"/>
            </mu-icon-menu>
        </div>
    </div>
</template>

<script>
	export default {
	    props: {
	        title: String,
            path: String,
            zm: {
	            type: Boolean,
                default: false
            },
            td: {
	            type: Boolean,
                default: false
            },
            goback: {
	            type: Boolean,
                default: false
            }
        },
        data() {
	        return {
                leftTop: {horizontal: 'right', vertical: 'top'}
            }
        },
        methods: {
	        rightPop() {
	            this.$emit('rightPop')
            },
            goBack() {
	            if(this.goback) {
	                this.$router.go(-1);
	                return
                }
	            this.$router.push({
                    path: this.path
                })
            }
        },
        beforeRouteEnter() {
	        console.log(1)
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    .header
        width: 100% !important
        height: 50px
        color #fff
        background-color $color-black
        display: flex !important
        .lit
            flex 0 0 50px
            line-height: 50px
            text-align: center 
            i
                vertical-align middle
                font-size: 16px
        .title
            flex 1
            text-align: center
            line-height: 50px
            font-size: 14px

    .mu-popover
        top: 50px!important
</style>



