<template>
    <div class="img-wrapper">
        <div class="title border-1px">{{title}}</div>
        <div class="content">
            <div class="item-wrapper" v-for="(item,index) in imgArr" :key="index">
                <div class="item" @click="delImg(index)">
                    <img :src="item" alt="" type="files">
                </div>
            </div>
            <div class="item-wrapper">
                <div class="icon-jia item add" @click="addImg"></div>
            </div>
            <input type="file" ref="upfile" @change="changeHandler" style="display: none;">
        </div>
    </div>
</template>

<script>
    import { MessageBox } from 'mint-ui';

	export default {
	    props: {
	        title: {
	            type: String,
                default: '上传图片'
            }
        },
        data() {
	        return {
	            imgArr: []
            }
        },
        methods: {
            addImg() {
                this.$refs.upfile.click();
            },
            changeHandler() {
                let fileObj = this.$refs.upfile.files[0];
                let img = new FileReader();
                img.readAsDataURL(fileObj);
                img.onloadend = (a) => {
                    this.imgArr.push(a.target.result)
                }
                this.$emit('imgupload', this.imgArr)
            },
            delImg(index) {
                MessageBox.confirm('是否删除该图片?')
                    .then(() => {
                        this.imgArr.splice(index,1);
                    })
            }
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/mixin"
    .img-wrapper
        width: 100%
        background-color #fff
        padding: 0 10px
        .title
            height: 30px
            line-height: 30px
            border-b(#ccc)
            font-size: 12px
            color #8e8d8d
        .content
            width: 100%
            padding: 15px 0 5px
            font-size: 0
            display: flex
            flex-wrap wrap
            .item-wrapper
                width: 25%
                margin-bottom: 10px
            .item
                width: 80px
                height: 80px
                margin: 0 auto
                border-radius 5px
                text-align: center
                line-height: 80px
                font-size: 26px
                color #ccc
                border 1px solid #ccc
                overflow: hidden
                position: relative
                img
                    width: 100%
</style>



