<template>
    <div class="notive-wrapper">
        <v-header :path="fromUrl" title="发公告"></v-header>
        <!--<router-link tag="div" append to="notiverange" class="cb">发布</router-link>-->
        <span @click='ToTeam' class="cb">发布</span>
        <mt-field label="公告标题" v-model="title"></mt-field>
        <mt-cell title="公告类型" :value="leaveValue" @click.native="popupVisible=true" is-link></mt-cell>
        <router-link  tag="div" append to="lookpeople">
        <mt-field label="可以看的人" v-model="people"></mt-field>
        </router-link>
        <mt-field label="公告内容" placeholder="公告内容" type="textarea" rows="4" v-model="content"></mt-field>
        <transition name="slide">
            <router-view></router-view>
        </transition>

        <mt-popup
            v-model="popupVisible"
            position="bottom">
            <mu-picker :slots="slots" :visible-item-count="3" @change="onValuesChange" :values="values"/>
        </mt-popup>
    </div>
</template>
<script>
    import VHeader from 'components/header/header';
    import {API_NOTICE_TYPE,API_NOTICE_ADD} from 'common/js/api';
    import { Toast, Indicator } from 'mint-ui';
    import { mapGetters } from 'vuex';

    import qs from 'qs';
    import axios from 'axios';

    export default {
        data() {
            return {
                recordIds:[],
                people:[],
                comId:'',
                id:'',
                content:'',
                title:'',
                fromUrl:'',
                leaveValue:'请选择（必选）',
                popupVisible: false,
                values: [],
                token:'',
                dataList:[],
                length:'',
                slots: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        created(){
            this.token=this.$login.getToken();
            this.comId=this.$route.params.id;
            this._request();
            // this._repuestPeople();
        },
        computed: {
            ...mapGetters([
                'getListPeople',
                'getPeopleId'
            ]),
           /* people:function () {
                console.log(1);
                console.log(getListPeople);
                return this.people=this.getListPeople;
            }*/
        },
        components: {VHeader},
        methods: {
            async _request(){
                let res =await axios.post(API_NOTICE_TYPE,qs.stringify({
                    utoken:this.token,
                    dictType:'oa_notify_type'
                }));
                let data=res.data;
                console.log(data);
                var dataList=data.list;
                this.dataList=dataList;
                var arr=[];
                for(var i=0;i<dataList.length;i++){
                    console.log(dataList[i].name);
                    arr.push(dataList[i].name)
                }
                this.slots[0].values=arr;
                console.log(this.slots[0].values);
                this.length=arr.length;
            },
            onValuesChange(a) {
                this.leaveValue = a;
                this.values = [a];
                console.log(this.values[0]);
                for(var i=0;i<this.dataList.length;i++){
                    if(this.values[0]===this.dataList[i].name){
                        this.id=this.dataList[i].id;
                    }
                }
            },
            ToTeam:function () {
                this.recordIds=this.getPeopleId;
                if(this.id===''){
                    alert('公告类型不能未空')
                }else if(this.title==='') {
                    alert('标题不能为空')
                }else if(this.recordIds===''){
                    alert('请选择能看的人')
                }else if(this.content===''){
                    alert('请填写内容')
                }else {
                    axios.post(API_NOTICE_ADD,qs.stringify({
                        utoken:this.token,
                        title:this.title,
                        type:this.id,
                        content:this.content,
                        recordIds:this.recordIds,
                        companyId:this.comId
                    })).then(res=>{
                        console.log(res);
                        let data=res.data;
                        if(data.result===0){
                            Toast({
                                message: '发布成功',
                                duration: 2000
                            });
                            window.history.back(-1);
                        }else if(data.result===2){
                            window.localStorage.clear();
                            this.$router.push('/login');
                            Toast({
                                message: '用户已失效，请重新登陆',
                                duration: 2000
                            });
                        }
                    }).catch(err=>{
                        console.log(err);
                    });
                }

            }

        }
    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"

    .notive-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        z-index 9999999999
        .cb
            width: 80px
            padding: 0 5px
            text-align: center
            color #fff
            line-height: 50px
            position absolute
            right 0
            top:0


</style>



