<template>
    <div class="cart-wrapper">
        <v-header :path="fromPath" title="购物车"></v-header>
        <div style="position: absolute;top:50px;left: 0;right: 0;bottom: 0">
        <scroller>
        <div class="good-wrap" v-for="item in cartList" :key="item.id" v-if="isLogin">
            <div class="good">
                <div class="seller-avatar">
                    <img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1512475529260&di=55bfd2e930b547d77a999eff2c910b9f&imgtype=0&src=http%3A%2F%2Ff.hiphotos.baidu.com%2Fzhidao%2Fpic%2Fitem%2Fb3b7d0a20cf431adeef1310e4d36acaf2edd98e1.jpg" alt="">
                </div>
                <div class="good-content">
                    <div class="seller-name border-1px">
                        <div class="name">{{item.comAssetName}}</div>
                    </div>
                    <div class="goods-list border-1px">
                        <div class="money">￥{{item.cartPrice}}</div>
                        <div class="good-contral">
                            <div class="ctrl-btn ctrl-jian">
                                <i class="icon-jian"></i>
                            </div>
                            <div class="ctrl-num">{{item.cartNum}}</div>
                            <div class="ctrl-btn ctrl-jia">
                                <i class="icon-jia"></i>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
            <div class="textarea">
                <mt-field label="备注" placeholder="请输入备注" type="textarea" rows="4" v-model="things"></mt-field>
            </div>
        </scroller>
        </div>
        <div class="cart-foot">
            <div class="cart-sum-money">合计：￥{{allMoney2}}</div>
            <div class="cart-sub-btn" @click.once="Settlement">下单</div>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import { mapGetters } from 'vuex';
    import {API_SETTLEMENT_goods,API_CAR_LIST} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Indicator, Toast } from 'mint-ui'; 

    export default {
	    data() {
	        return {
                fromPath: '',
                cartList:[],
                allMoney:0,
                isLogin: false,
                things:'',
                cartInfo:[],
                allMoney2:0
            }
        },

        computed:{
            ...mapGetters([
                'getShopCart'
            ])
        },

        activated() {
            this.isLogin = this.$login.isLogin();
        },
        methods:{
            /*async _getCompanyMsg(){
                // this.$store.dispatch('getShopCart');
                // console.log(this.getCompanyMsg);
                console.log(this.getShopCart);

                this.cartList=this.getShopCart;
                for(var i=0;i<this.cartList.length;i++){
                    this.allMoney+=parseInt(this.cartList[i].sumPrice);
                    this.cartInfo.push({
                        cartId:this.cartList[i].id,
                        goodsNum:this.cartList[i].count
                    })
                }
                console.log(this.cartInfo);
                console.log(this.allMoney);
            },*/
            async _getCarList(){
                let res=await axios.post(API_CAR_LIST,qs.stringify({
                    utoken:this.$login.getToken()
                }));
                let data=res.data;
                console.log(data);
                this.cartList=data.list;

                for(let i=0;i<data.list.length;i++){
                    this.allMoney2+=parseInt(data.list[i].cartPrice*parseInt(data.list[i].cartNum));
                    this.cartInfo.push({
                        cartId:data.list[i].id,
                        goodsNum:data.list[i].cartNum
                    })
                }
                // 购买总金额
                console.log(this.allMoney2);
            },
            Settlement(){
                var cartInfo=JSON.stringify(this.cartInfo);
                axios.post(API_SETTLEMENT_goods,qs.stringify({
                    totalMoney:this.allMoney2,
                    comments:this.things,
                    utoken:this.$login.getToken(),
                    cartInfo:cartInfo
                })).then(res=>{
                    console.log(res);
                    let data=res.data;
                    if(data.result === 2) {
                        Toast({
                            message: '用户已失效，请重新登陆',
                            duration: 2000
                        });
                        this.$router.push('/login');
                    }else if(data.result === 0){
                        this.$router.push({path:'cart/payselect',query:{
                            id:res.data.orderId,
                        }})
                        console.log(res.data.orderId);
                    }
                }).catch(err=>{
                    console.log(err);
                })
                
            },
        },

        created() {
            this.isLogin = this.$login.isLogin();
	        let fullPath = this.$route.fullPath;
	        this.fromPath = fullPath.substring(0, fullPath.lastIndexOf('/'));
            // this._getCompanyMsg();
            this._getCarList();
        },
	    components: {
            VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"

    .cart-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        .good-wrap
            padding: 14px 0 0 15px
            background-color: #fff
            .good
                width: 100%
                display: flex
                position: relative
                .good-content
                    flex 1
                    .seller-name
                        padding: 0 13px 10px 0
                        border-b(#eee)
                        .name
                            line-height: 24px
                    .goods-list
                        display: flex
                        justify-content: space-between
                        padding: 15px 13px 15px 0
                        border-b(#eee)
                        .money
                            color red
                        .good-contral
                            display: flex
                            align-items center
                            justify-content space-between
                            .ctrl-btn
                                width: 20px
                                height: 20px
                                display: flex
                                align-items center
                                justify-content center
                                border: 1px solid #eee
                                i
                                    font-size: 10px
                            .ctrl-jia
                                margin-left: -1px
                            .ctrl-jian
                                margin-right: -1px
                            .ctrl-num
                                width: 30px
                                height: 20px
                                border 1px solid #eee
                                text-align: center
                .seller-avatar
                    padding-right: 10px
                    img
                        width: 31px

        .cart-foot
            position: fixed
            left: 0
            right: 0
            bottom: 0
            z-index: 0
            height: 45px
            background-color: $color-black
            display: flex
            align-items center
            justify-content space-between
            padding-left: 15px
            .cart-sum-money
                font-size: 14px
                color #fff
            .cart-sub-btn
                height: 45px;
                line-height: 45px;
                width: 45px
                color #fff
                text-align: center;
                background-color: $color-orange
</style>



