<template>
    <div class="en-wrapper">
        <div class="choice" @click="choiceAccount">
            <i class="icon-gongneng"></i>
            <span>{{account[0]}}</span>
        </div>
        <div class="number">
            <input class="inp" v-model="money" type="number" @input="inputHandler" @focus="focusHandler" />
            <span>元</span>
        </div>
        <mu-popup position="bottom" popupClass="demo-popup-bottom" :open="bottomPopup" @close="close">
            <mu-picker :slots="accountSlots" @change="accountChange" :values="account"/>
        </mu-popup>
    </div>
</template>

<script>
    import { API_GET_ACCOUNT } from 'common/js/api';
    import axios from 'axios';
    import qs from 'qs';
    import resetLogin from 'common/js/resetLogin';
    import { Toast } from 'mint-ui';

	export default {
	    props: {
	        msg: {
	            type: String
            }
        },
        data() {
	        return {
	            money: 0,
                bottomPopup: false,
                accountSlots: [
                    {
                        width: '100%',
                        textAlign: 'center',
                        values: []
                    }
                ],
                account: ['请选择账户'],
                msgArr: []
            }
        },
        async created() {
	        let res = await axios.post(API_GET_ACCOUNT, qs.stringify({
                utoken: this.$login.getToken(),
                noteType: 2,
                indexType: 1
            }));
	        let data = res.data;
	        if(data.result === 2) {
                resetLogin(this, () => {
                    Toast({
                        message: data.msg,
                        duration: 20000
                    })
                })
            }
	        this.msgArr = data.list;
	        for (let i = 0; i < data.list.length; i++) {
	            this.accountSlots[0].values.push(data.list[i].name)
            }
        },
        methods: {
            focusHandler() {
                if(this.money === 0) {
                    this.money = ''
                }
            },
            inputHandler() {
                this.$emit('on-input', this.money)
            },
            choiceAccount() {
                this.bottomPopup = true
            },
            close() {
                this.bottomPopup = false
            },
            accountChange(value) {
                this.account = [value];
                let nowItem = {};
                for(let i = 0; i < this.msgArr.length; i++) {
                    if(value === this.msgArr[i].name) {
                        nowItem = this.msgArr[i];
                        break;
                    }
                }
                this.$emit('on-accountChange', nowItem);
            }
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    .en-wrapper
        width: 100%
        height: 43px
        background-color #fff
        display: flex
        .choice
            font-size: 0
            box-sizing border-box
            padding-left: 36px
            display: flex
            align-items center
            flex 0 0 147px
            span
                margin-left: 5px
                font-size: 12px
                width: 87px
                overflow: hidden
                text-overflow ellipsis
                white-space nowrap
        .number
            padding-right: 36px
            box-sizing border-box
            text-align: right
            flex 1
            color #e36464
            .inp
                color #e36464
                width: 168px
                font-size: 24px
                border none
                outline none
                text-align: right
                height: 100%
    .demo-popup-bottom
        width: 100%
</style>



