<template>
    <div class="sub-wrapper">
        <v-header title="提交成功"></v-header>
        <span class="icon-tishichenggong success"></span>
        <div class="font-success">提交成功!!</div>
        <span class="back" @click="Goback">回到首页</span>
    </div>
</template>
<script>
    import VHeader from 'components/header/header';
    export default {
        data() {
            return {}
        },
        computed: {},
        components: {VHeader},
        methods: {
            Goback:function () {
                this.$router.go(-2)
            }
        }
    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"

    .sub-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        text-align center
        .success{
            display inline-block
            color #05B07C
            font-size 120px
            text-align center
            margin-top 30%
        }
        .font-success
            font-size 38px
            color black
        .back
            margin-top 60px
            color #FFFFFF
            display inline-block
            border-radius 4px
            background-color #0076FE
            padding 10px 55px
            font-size 18px

</style>



