<template>
    <div class="con-my">
        <scroller>
        <div class="group" v-for="(item,index) in groupList" :key="index" @click="ToTall(item.groupId,item.groupName)">
            <div class="num-msg">
                <img src="http://img5.duitang.com/uploads/item/201506/09/20150609224048_RCQPf.jpeg">
                <div class="reading">
                    <span>34</span>
                </div>
            </div>
            <p>{{item.groupName}}</p>
        </div>
        </scroller>
    </div>
</template>

<script>
    import {API_GROUP_LIST} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Indicator, Toast } from 'mint-ui';

    export default {
        data(){
            return{
               groupList:[]
            }
        },
        created:function () {
            let token = this.$login.getToken();
            this._request(token)
        },
        methods: {
            async _request(token){
                Indicator.open();
                let res=await axios.post(API_GROUP_LIST,qs.stringify({
                    utoken:token,
                    targetType:'chatgroups'
                }));
                Indicator.close();
                let data=res.data;
                console.log(data);
                this.groupList=data.list;
                console.log(this.groupList)
            },
            ToTall(id,name){
                this.$router.push({
                    path:`grouptalk/${id}/${name}`
                })
            }
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"
    .con-my
        position absolute
        top 90px
        left 0
        right 0
        bottom 0
        padding-bottom 50px
    .group
        z-index 2
        height 66px
        width 100%
        border-bottom 1px solid #CCCCCC
        background-color #FFFFFF
        padding 8px 5px
        display flex
        align-items center
        p
            font-size 16px
            color #000000
            margin-left 10px
    .num-msg 
        position relative;
    
        img
            width 50px
            height 50px
            border-radius 5px
        
        .reading 
            width 18px
            height 18px
            background red
            position absolute
            top -5px
            left 40px
            border-radius 50%
            line-height 18px
            text-align center
            span 
                color #fff
                
        
</style>



