<template>
    <div class="purchase-wrapper">
        <v-header :path="fromUrl" title="采购"></v-header>
        <div class="pi-wrapper">
            <scroller>
                <!--<dd>
                    <mt-cell title="店铺" :value="leaveValue" @click.native="popupVisible=true" is-link></mt-cell>
                </dd>-->
                <dd>
                    <mt-cell title="采购类型" :value="leaveValueType" @click.native="popupVisibleType=true" is-link></mt-cell>
                </dd>
                <dd>
                <mt-cell title="开始时间" :value="startTime" isLink @click.native="showDatePicker(true)"></mt-cell>
                </dd>
                <dd>
                    <mt-cell title="部门" :value="leaveValuedep" @click.native="popupVisibledep=true" is-link></mt-cell>
                </dd>
                <dd>
                    <mt-cell title="流程" :value="leaveValuethc" @click.native="popupVisiblethc=true" is-link></mt-cell>
                </dd>
                <dl v-for="(item, index) in several" :key="index">
                    <dt class="title">
                        <span>采购明细({{index+1}})</span>
                        <span class="del" v-show="several.length>1" @click="delSeveral(index)">删除</span>
                    </dt>
                    <dd>
                        <mt-field label="名称" placeholder="请输入名称" v-model="item.purName"></mt-field>
                    </dd>
                    <dd>
                        <mt-field label="规格" placeholder="请输入规格" v-model="item.specification"></mt-field>
                    </dd>
                    <dd>
                        <mt-field label="数量" placeholder="请输入数量" v-model="item.quantity"></mt-field>
                    </dd>
                    <!--<dd>
                        <mt-cell title="单位" :value="leaveValueUnit" @click.native="popupVisibleUnit=true" is-link></mt-cell>
                    </dd>-->
                    <dd>
                        <mt-field label="单位" placeholder="请输入单位" v-model="item.unit"></mt-field>
                    </dd>
                    <dd>
                        <mt-field label="资产编号" placeholder="请输入资产编号" v-model="item.purCode"></mt-field>
                    </dd>
                    <dd>
                        <mt-field label="价格" placeholder="请输入价格" v-model="item.price"></mt-field>
                    </dd>


                </dl>
                <div class="add-several">
                    <div class="title">如采购多种商品，请点击“增加明细”</div>
                    <div class="add-several-btn" @click="addSeveral">增加明细</div>
                </div>
                <mt-field label="备注" placeholder="请输入备注" type="textarea" rows="4" v-model="remark"></mt-field>
                <img-upload style="margin-top: 10px;" ref="SrcBase"></img-upload>
            </scroller>
        </div>
       <!-- <mt-popup
            v-model="popupVisible"
            position="bottom">
            <mu-picker :slots="slots" :visible-item-count="3" @change="onValuesChange" :values="values"/>
        </mt-popup>-->

        <mt-popup
            v-model="popupVisibleType"
            position="bottom">
            <mu-picker :slots="slotsType" :visible-item-count="3" @change="onValuesChangeType" :values="valuesType"/>
        </mt-popup>

        <mt-popup
            v-model="popupVisibledep"
            position="bottom">
            <mu-picker :slots="slotsdep" :visible-item-count="3" @change="onValuesChangedep" :values="valuesdep"/>
        </mt-popup>

        <mt-popup
            v-model="popupVisiblethc"
            position="bottom">
            <mu-picker :slots="slotsthc" :visible-item-count="3" @change="onValuesChangethc" :values="valuesthc"/>
        </mt-popup>

        <mt-datetime-picker
            ref="picker"
            type="date"
            @confirm="confirmHandler"
            v-model="pickerValue">
        </mt-datetime-picker>
        <!--<mt-popup
            v-model="popupVisibleUnit"
            position="bottom">
            <mu-picker :slots="slotsUnit" :visible-item-count="3" @change="onValuesChangeUnit" :values="valuesUnit"/>
        </mt-popup>-->

        <div class="purchase-btn" @click="Sub">提交</div>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import { MessageBox } from 'mint-ui';
    import ImgUpload from 'components/img-upload/img-upload';
    import {API_PURCHASE_ADD,API_ALL_COM,API_COM_TYPE,API_COMP_LIST,API_ALL_DEP,API_APPROVAL_PRO} from 'common/js/api';
    import { Toast } from 'mint-ui';
    import {mapGetters} from 'vuex'
    import qs from 'qs';
    import axios from 'axios';

	export default {
	    data() {
	        return {
                startTime: '请选择时间(必填)',
                pickerValue: new Date(),
                purTypeId:'',
                remark:'',
                token:'',
                imglist:[],
                companyId:'',
                office:[],
                shops:[],
                officeId:'',
                /*leaveValue:'请选择（必选）',
                popupVisible: false,
                values: [],
                slots: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],*/

                money:[],
                leaveValueType:'请选择（必选）',
                popupVisibleType: false,
                valuesType: [],
                slotsType: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],

                officeIdDep:'',
                leaveValuedep:'请选择（必选）',
                popupVisibledep: false,
                valuesdep: [],
                slotsdep: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],

                thclistId:'',
                thclist:[],
                leaveValuethc:'请选择（必选）',
                popupVisiblethc: false,
                valuesthc: [],
                slotsthc: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],

                /*units:[],
                leaveValueUnit:'请选择（必选）',
                popupVisibleUnit: false,
                valuesUnit: [],
                slotsUnit: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],*/

                several: [
                    {
                        unit:'',
                        // id: 1,
                        purName: '',
                        specification: '',
                        quantity: '',
                        purCode: '',
                        price: '',
                    }
                ],

                fromUrl: ''
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        created(){
            this.token=this.$login.getToken();
            this.companyId=this.$route.params.id;
            // this._request();
            this._requestType();
            this._requestdep();
            this._requestthc();
            // this._requestUnit();
        },
        methods: {
            /*async _request(){
                    let res=await axios.post(API_ALL_COM,qs.stringify({
                        utoken:this.token,
                        officeId:this.companyId
                    }));
                    let data=res.data;
                    this.shops=data.list;
                    console.log(data);
                    var arr=[];
                    for(var i=0;i<data.list.length;i++){
                        arr.push(data.list[i].name)
                    }
                    console.log(arr);
                    this.slots[0].values=arr;
                },*/
            async _requestType(){
                let res=await axios.post(API_COM_TYPE,qs.stringify({
                    utoken:this.token,
                    officeId:this.companyId
                }));
                let data=res.data;
                console.log(data);
                this.money=data.list;
                var arr=[];
                for(var i=0;i<this.money.length;i++){
                    arr.push(this.money[i].name);
                }
                this.slotsType[0].values=arr;
            },
            async _requestdep(){
                let res=await axios.post(API_ALL_DEP,qs.stringify({
                    utoken:this.token,
                    type:'1',
                    officeId:this.companyId
                }));
                let data=res.data;
                console.log(data);
                this.office=data.list;
                let arr=[];
                for(let i=0;i<this.office.length;i++){
                    arr.push(this.office[i].name);
                }
                this.slotsdep[0].values=arr;
            },
            async _requestthc(){
                let res=await axios.post(API_APPROVAL_PRO,qs.stringify({
                    utoken:this.token,
                    type:'12',
                    officeId:this.companyId
                }));
                let data=res.data;
                console.log(data);
                this.thclist=data.list;
                let arr=[];
                for(let i=0;i<this.thclist.length;i++){
                    arr.push(this.thclist[i].name);
                }
                this.slotsthc[0].values=arr;
            },
            /*async _requestUnit(){
              let res=await axios.post(API_COMP_LIST,qs.stringify({
                  utoken:this.token,
                  dictType:'unit'
                  }));
                let data=res.data;
                console.log(data);
                this.units=data.list;
                var arr=[];
                for(var i=0;i<data.list.length;i++){
                    arr.push(data.list[i].name);
                }
                this.slotsUnit[0].values=arr;
            },*/
            showDatePicker(bOk) {
                this.$refs.picker.open();
                this.bOk = bOk
            },
            _dateFormat(date) {
                var month;var num;var date2;var num2;
                if(date.getMonth()+1<10&&date.getDate()>9){
                    num=date.getMonth()+1;
                    month='0'+num;
                    return `${date.getFullYear()}-${month}-${date.getDate()}`;
                }else if(date.getMonth()+1>=10&&date.getDate()<10){
                    num2=date.getDate();
                    date2='0'+num2;
                    return `${date.getFullYear()}-${date.getMonth()+1}-${date2}`;
                }
                else if(date.getMonth()+1<10&&date.getDate()<10){
                    num=date.getMonth()+1;
                    month='0'+num;
                    num2=date.getDate();
                    date2='0'+num2;
                    return `${date.getFullYear()}-${month}-${date2}`;
                }else if(date.getMonth()+1>=10&&date.getDate()>9){
                    return `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
                }
                // return `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
            },
            confirmHandler(value) {
                    this.startTime = this._dateFormat(value);
                    console.log(this.startTime);
            },
            addSeveral() {
                this.several.push({
                    // id: this.several.length + 1,
                    unit:'',
                    purName: '',
                    specification: '',
                    quantity: '',
                    purCode: '',
                    price: ''
                });
            },
            delSeveral(index) {
                console.log(index);
                MessageBox.confirm('确定执行此操作?').then(action => {
                    this.several.splice(index,1)
                });
            },
            /*onValuesChange(a) {
                this.leaveValue = a;
                this.values = [a];
                for(var i=0;i<this.shops.length;i++){
                    if(this.values[0]===this.shops[i].name){
                        this.officeId=this.shops[i].id;
                    }
                }
            },*/
            onValuesChangeType(b) {
                this.leaveValueType = b;
                this.valuesType = [b];
                for(var i=0;i<this.money.length;i++){
                    if(this.valuesType[0]===this.money[i].name){
                        console.log(this.money[i].id);
                        this.purTypeId=this.money[i].id;
                    }
                }
            },
            onValuesChangedep(c) {
                this.leaveValuedep = c;
                this.valuesdep = [c];
                for(var i=0;i<this.office.length;i++){
                    if(this.valuesdep[0]===this.office[i].name){
                        this.officeIdDep=this.office[i].id;
                    }
                }
                console.log(this.officeIdDep);
            },
            onValuesChangethc(d) {
                this.leaveValuethc = d;
                this.valuesthc = [d];
                for(var i=0;i<this.thclist.length;i++){
                    if(this.valuesthc[0]===this.thclist[i].name){
                        this.thclistId=this.thclist[i].value;
                    }
                    console.log(this.thclistId);
                }
            },
            /*onValuesChangeUnit(c) {
                this.leaveValueUnit = c;
                this.valuesUnit = [c];
                for(var i=0;i<this.units.length;i++){
                    if(this.valuesUnit[0]===this.units[i].name){
                        console.log(this.units[i].id);
                        this.several[0].unit=this.units[i].id;
                    }
                }
            },*/
            Sub(){
                this.imglist=this.$refs.SrcBase.$data.imgArr;
                var file=[];
                for(var i=0;i<this.imglist.length;i++){
                    file.push(this.imglist[i])
                }
                if(this.companyId===''||this.remark===''||file===''||this.purTypeId===''){
                   alert('请补充完整信息');
               }else {
                    for (var i = 0; i < this.several.length; i++) {
                        if (this.several[i].unit === '' || this.several[i].purName === '' || this.several[i].specification === '' || this.several[i].quantity === '' || this.several[i].purCode === '' || this.several[i].price === '') {
                            alert('请补充完整信息');
                        }
                    }
                    var severals = JSON.stringify(this.several);
                    axios.post(API_PURCHASE_ADD, qs.stringify({
                        utoken: this.token,
                        // officeId: this.officeId,
                        companyId: this.companyId,
                        remarks: this.remark,
                        file64Base: file,
                        purchase: severals,
                        purTypeId: this.purTypeId,
                        addTime:this.startTime,
                        officeId: this.officeIdDep,
                        processId:this.thclistId
                    })).then(res => {
                        console.log(res);
                        let data=res.data;
                        if(data.result===0){
                            this.$router.go(-1);
                            Toast({
                                message: "提交成功",
                                duration: 2000
                            });
                        }else if(data.result===2){
                            Toast({
                                message: '用户已失效，请重新登陆',
                                duration: 2000
                            });
                            window.localStorage.clear();
                            this.$router.push('/login');
                            Toast({
                                message: '用户已失效，请重新登陆',
                                duration: 2000
                            });
                        }
                    }).catch(err => {
                        console.log(err);
                    })
                }
            }
        },
	    components: {
            VHeader,
            ImgUpload
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .purchase-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        .purchase-btn
            position: absolute

            right: 10px
            top: 5px
            font-size: 16px
            color #fff
            text-align: center
            line-height: 50px
            /*background-color: $color-orange*/
        .pi-wrapper
            position: absolute
            top: 50px
            bottom: 0
            left: 0
            right: 0
        dl
            .title
                padding: 0 10px
                display: flex
                justify-content space-between
                line-height: 30px
                .del
                    color $color-orange
        .add-several
            .title
                padding: 0 10px
                line-height: 30px
            .add-several-btn
                line-height: 48px
                text-align: center
                color #26a2ff
                background-color: #fff
        .is-textarea
            margin-top: 10px
            .mint-cell-wrapper
                align-items flex-start
</style>



