<template>
    <div class="bill-list-wrapper">
        <v-header path="/" title="账单列表"></v-header>
        <div class="filter-bar">
            <div class="date">
                <mu-date-picker format="YYYY年MM月DD日" :maxDate="today" v-model="date"/>
            </div>
            <div class="filter-btn" @click="toggle">筛选</div>
        </div>
        <div class="content">
            <scroller>
                <CheckboxGroup :value="checkAll" v-model="checkAllGroup" @on-change="checkAllGroupChange">
                    <ul style="margin-top: 10px;">
                        <li v-for="item in billArr" class="item border-1px" @click="showBillDetail(item)">
                            <Checkbox v-if="showBx && (item.t ==2 && item.baoxiaoStatus == 0)" :label="item.id"></Checkbox>
                            <div class="icon">
                                <div class="ball">
                                    <i class="icon-dianhua"></i>
                                </div>
                            </div>
                            <div class="bill-text">
                                <div class="top">
                                    <span>{{item.soure}}</span>
                                    <span class="y" v-if="item.t == 2 && item.baoxiaoStatus == 0">可报销</span>
                                </div>
                                <div class="bot">
                                    <span class="bot-money green" v-if="item.t == 2">-{{item.money}}</span>
                                    <span class="bot-money red" v-else-if="item.t == 1">+{{item.money}}</span>
                                    <span>来自{{item.mode}} {{item.date}}</span>
                                </div>
                            </div>
                        </li>
                    </ul>
                </CheckboxGroup>
            </scroller>
        </div>
        <!--批量报销按钮-->
        <div class="bxBtn" @click="showBxCheck">
            <span v-show="!showBx">批量报销</span>
            <span v-show="showBx">取消选择</span>
        </div>
        <!--批量报销底栏-->
        <div class="bxBar" v-show="showBx">
            <Checkbox
                :indeterminate="indeterminate"
                :value="checkAll"
                @click.prevent.native="handleCheckAll">全选</Checkbox>
            <div class="goBx" :class="{disabled: checkAllGroup.length == 0}" @click="goBxList( checkAllGroup.length == 0)">去报销</div>
        </div>
        <!--筛选条件-->
        <mu-popup position="right" popupClass="popup-right" :open="open" @close="toggle">
            <div class="sx">
                <div class="name">条件</div>
                <div class="content">
                    <div style="width: 33.3%;" v-for="(item, index) in 5">
                        <div class="term" @click="choiceTerm(index)" :class="{on:index==chose}">条件{{item}}</div>
                        <div class="btn-bar">
                            <div class="reset">重置</div>
                            <div class="make-sure">确定</div>
                        </div>
                    </div>
                </div>
            </div>
        </mu-popup>
        <!--账单弹窗-->
        <mt-popup
            popup-transition="popup-fade"
            :closeOnClickModal="true"
            v-model="popupVisible">
            <div class="ticket">
                <div class="ticket-top">
                    <div class="icon-gouwuche icon"></div>
                    <div class="ticket-title">
                        <div class="name">{{ticket.soure}}</div>
                        <div class="date">{{ticket.date}}</div>
                    </div>
                </div>
                <div class="ticket-money">
                    <div class="num">￥{{ticket.money}}</div>
                    <div class="bx" v-show="ticket.t == 2 && ticket.baoxiaoStatus == 0">可报销</div>
                </div>
                <div style="font-size: 12px;color:#ccc">来自{{ticket.mode}}</div>
                <!--<mu-raised-button
                    class="demo-raised-button"
                    label="去报销"
                    :to="`billd${id}`"
                    backgroundColor="rgb(240, 134, 25)" />-->
                <mu-raised-button label="去报销" class="demo-raised-button" primary @click="clickHandler(ticket.id)" :disabled="ticket.t == 1 || ticket.baoxiaoStatus!=0"/>
            </div>
        </mt-popup>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import { mapGetters, mapMutations } from 'vuex';
    let date = new Date();
	export default {
	    data() {
	        return {
	            bxArr: [],
	            showBx: false,
                checkAllGroup: [],
                indeterminate: false,
                checkAll: false,
	            open: false,
                today:new Date(),
                date: date.getFullYear()+'年'+(date.getMonth()+1)+'月'+date.getDate()+'日',
                popupVisible: false,
                id: 0,
                chose:0,
                billArr: [],
                ticket: {}
            }
        },
        computed: {
            ...mapGetters([
                'getBillList'
            ])
        },
        created() {
	        this.billArr = this.getBillList;
	        if(this.billArr.length !== 0) {
                for(let i = 0; i < this.billArr.length; i++) {
                    if (this.billArr[i].t === 2 && this.billArr[i].baoxiaoStatus == 0) {
                        this.bxArr.push(this.billArr[i].id)
                    }
                }
            }
        },
        methods: {
            ...mapMutations({
                setBxList: 'SET_BX_LIST'
            }),
            goBxList(b) {
                if(!b) {
                    this.setBxList(this.checkAllGroup);
                    this.$router.push({
                        path: 'bxList'
                    });
                }
            },
            showBxCheck() {
                this.showBx = !this.showBx;
            },
            checkAllGroupChange(data) {
                if (data.length === this.bxArr.length) {
                    this.indeterminate = false;
                    this.checkAll = true;
                } else if (data.length > 0) {
                    this.indeterminate = true;
                    this.checkAll = false;
                } else {
                    this.indeterminate = false;
                    this.checkAll = false;
                }
            },
            handleCheckAll() {
                if (this.indeterminate) {
                    this.checkAll = false;
                } else {
                    this.checkAll = !this.checkAll;
                }
                this.indeterminate = false;

                if (this.checkAll) {
                    this.checkAllGroup = this.bxArr
                } else {
                    this.checkAllGroup = [];
                }
            },
	        clickHandler(id) {
	            this.$router.push({
                    path: `billd${id}`
                })
            },
            toggle () {
                this.open = !this.open
            },
            showBillDetail(item) {
                if(!this.showBx) {
                    this.ticket = item;
                    this.id = item.id;
                    this.popupVisible = true;
                }
            },
            choiceTerm(i) {
                this.chose = i
            }
        },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"

    .bill-list-wrapper
        position: fixed
        left: 0
        top: 0
        right: 0
        bottom: 0
        z-index 1000
        background-color $color-bg
        .ivu-checkbox-group-item
            position: absolute
            left: -10px
            right: 0
            top: 0
            bottom: 0
            display: flex
            align-items center
            span:last-child
                display: none
        .bxBar
            position: fixed
            left: -10px
            right: 0
            bottom: 0
            height: 50px
            background-color: $color-black
            display: flex
            align-items center
            color #fff
            padding-left: 20px
            justify-content space-between
            .goBx
                line-height: 50px
                width: 96px
                text-align: center
                background-color: $color-orange
                &.disabled
                    background-color: #ccc
        .bxBtn
            position: absolute
            right: 0
            top: 0
            line-height: 50px
            padding: 0 10px
            color #fff
        .mint-popup
            z-index:10000!important
        .filter-bar
            width: 100%
            height: 30px
            display: flex
            font-size: 12px
            color #fff
            background-color $color-black
            .date,.filter-btn
                flex 1
                line-height: 30px
                text-align: center
            .date
                .mu-date-picker
                    width: 100%
                .mu-text-field
                    width: 100%
                    margin: 0
                    min-height 0
                .mu-text-field-content
                    padding: 0
                .mu-text-field-input
                    height: 100%
                    color #fff
                    font-size: 12px
                    text-align: center
                .mu-text-field-line
                    height: 0
        .content
            position: absolute
            left: 0
            right: 0
            top: 80px
            bottom: 0
            ul
                background-color #fff
                padding: 0 20px
            .item
                height: 38px
                border-b(#ccc)
                font-size: 0
                box-sizing border-box
                padding: 0 15px
                display: flex
                .icon
                     width: 21px
                     height: 100%
                     line-height: 38px
                     display: inline-block
                     vertical-align: top
                .ball
                     display: inline-block
                     vertical-align: middle
                     width: 21px
                     height: 21px
                     border-radius 50%
                     background-color $color-orange
                     color #fff
                     text-align: center
                     line-height: 21px
                 i
                     font-size: 12px
                .bill-text
                    padding: 5px 0 0 11px
                    box-sizing border-box
                    flex 1
                    display: flex
                    justify-content space-between
                    font-size: 12px
                .bot,.top
                    display: flex
                    flex-direction column
                    justify-content center
                .bot
                    font-weight normal
                    font-size: 10px
                    color #929292
                    text-align: right
                    .bot-money
                        font-size: 12px
                        &.red
                            color red
                        &.green
                            color green
                .y
                    font-size: 10px
                    color $color-orange

        .mint-popup
            background-color transparent
        .ticket
            width: 300px
            height: 248px
            margin: 0 auto
            padding: 20px 20px 0
            background-color red
            background url("../../../../../static/ticket.png") center no-repeat
            background-size 100%
            .demo-raised-button
                width: 100%
            .ticket-top
                display: flex
                height: 30px
            .ticket-title
                padding-left: 10px
                flex 1
                .name
                    line-height: 18px
                .date
                    line-height: 13px
                    font-size: 12px
                    color #ccc
            .ticket-money
                display: flex
                height: 100px
                .num
                    font-size: 28px
                .bx
                    color $color-orange
                    text-align: center
                    line-height: 110px
                div
                    line-height: 100px
                    flex 1
            .icon
                width: 30px
                height: 30px
                display: inline-block
                vertical-align: top
                line-height: 30px
                color #fff
                border-radius 50%
                text-align: center
                background-color: $color-orange
    .popup-right
        width: 294px
        height: 100%
        padding: 0 20px
        box-sizing border-box
        background-color #fff
        .sx
            padding-top: 32px
            .name
                line-height: 36px
                color #454545
                font-size: 12px
            .content
                width: 100%
                font-size: 0
                display: flex
                flex-wrap wrap
                .term
                    width: 80px
                    height: 24px
                    margin: 0 auto 10px
                    font-size: 12px
                    text-align: center
                    line-height: 24px
                    color #797979
                    background-color #f5f5f5
                    &.on
                        color #f6a11c
                        background url("./bg.png") no-repeat center / 100%
            .btn-bar
                position: absolute
                left: 0
                bottom: 0
                width: 100%
                height: 42px
                display: flex
                font-size: 14px
                text-align: center
                line-height: 42px
                .reset
                    flex 1
                    height: 100%
                    color #7c7c7c
                    background-color #eee
                .make-sure
                    width: 231px
                    height: 100%
                    color #fff
                    background-color $color-orange
</style>



