<template>
    <div class="person-wrapper">
        <v-header path="/mine" title="个人账号"></v-header>
        <div class="info" @click="toinfodetail">
            <img :src="avatarPath?avatarPath:defaultAvatarPath">
            <span class="name">{{this.username}}</span>
        </div>
        <div class="exit-btn" @click="exitLogin">退出登录</div>
        <router-view></router-view>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import { API_EXIT_LOGIN } from 'common/js/api';
    import axios from 'axios';
    import { Toast } from 'mint-ui';
    import qs from 'qs';

	export default {
	    data(){
	        return{
                username:'',
                defaultAvatarPath:'https://fuss10.elemecdn.com/c/f5/d0b0f2fc83f3ac3e4a0cfae891256png.png?imageMogr/format/webp/thumbnail/!60x60r/gravity/Center/crop/60x60/',
                avatarPath:''
            }
        },
	    created(){
            this.username = this.$login.getUserName();
            this.avatarPath = this.$login.getAvatar();
            console.log(this.avatarPath)
            
        },
	    methods: {
            async exitLogin() {
                // let token = this.$login.getToken();
                // let res = await axios.post(API_EXIT_LOGIN, qs.stringify({
                //     utoken: token
                // }));
                // let data = res.data;
                // console.log(data);
                // if(data.result === 0) {
                    window.localStorage.clear();
                    Toast({
                        message: '退出成功',
                        duration: 2000
                    });
                    this.$router.push({
                        path: '/mine'
                    })
                    window.android.logoutAndroid();
                // }else if(data.result === 2){
                    // Toast({
                    //     message: data.msg,
                    //     duration: 2000
                    // });
                    // window.localStorage.clear();
                    // this.$router.push({
                    //     path: '/login'
                    // })
                // }

            },
            toinfodetail(){
                this.$router.push('person/infodetail');
            }
        },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    .person-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        z-index 1000
        .exit-btn
            height: 44px
            margin-top: 20px
            text-align: center
            color red
            line-height: 44px
            background-color: #fff
        .info
            margin-top 20px
            padding 15px
            background-color #ffffff
            .name
                vertical-align top
                margin-left 10px
                font-size 18px
            img
                width 45px
                height 45px
</style>



