<template>
    <div class="login-wrapper">
        <v-header :path="fromUrl" title="登录"></v-header>
        <div class="register" @click="goRegister">注册</div>
        <div class="form-wrap">
            <Form :model="formTop" label-position="top" :rules="ruleValidate">
                <FormItem label="用户名" prop="username">
                    <Input v-model="formTop.username"></Input>
                </FormItem>
                <FormItem label="密码" prop="password">
                    <Input type="password"  v-model="formTop.password"></Input>
                </FormItem>
                <FormItem>
                    <Button long type="success" @click="handleSubmit">Submit</Button>
                </FormItem>
            </Form>
        </div>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import { API_LOGIN } from "common/js/api";
    import axios from 'axios';
    import qs from 'qs';
    import { Toast } from 'mint-ui';

	export default {
	    data() {
	        return {
	            fromUrl: '',
                formTop: {
                    username: '',
                    password: ''
                },
                ruleValidate: {
                    username: [
                        {
                            required: true,
                            message: '请输入用户名',
                            trigger: 'blur'
                        }
                    ],
                    password: [
                        {
                            required: true,
                            message: '请输入密码',
                            trigger: 'blur'
                        }
                    ]
                },
                mobiletype:''
            }
        },
        beforeRouteEnter (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path === '/register'|| from.path === '/mine/person' ? '/mine': from.path;
            })
        },

        methods: {
            async handleSubmit() {
                let res = await axios.post(API_LOGIN,qs.stringify({
                    username: this.formTop.username,
                    password: this.formTop.password,
                    mobileinfo:navigator.userAgent,
                    mobiletype:this.mobiletype 
                }));
                let data = res.data;
                console.log(data);
                if(data.result === 0) {
                    window.localStorage.setItem('u_token',JSON.stringify({
                        username: data.username,
                        token: encodeURIComponent(data.utoken),
                        avatar: data.headpic
                    }));
                    this.$router.push({
                        path: this.fromUrl
                    })
                    console.log(data.restUserName)
                    console.log(data.restUserPass)
                    if(this.mobiletype=='iphone'){
                        share(data.restUserName,data.restUserPass,encodeURIComponent(data.utoken));
                    }else if(this.mobiletype=='android'){
                        window.android.callAndroid(data.restUserName,data.restUserPass,encodeURIComponent(data.utoken));
                    }
                }else{
                    Toast({
                        message:'账号或密码错误'
                    })
                }
            },
            goRegister() {
                this.$router.push({
                    path: '/register'
                })

            },
            Sjxh(){
                var u = navigator.userAgent;
                if (u.indexOf('Android') > -1 || u.indexOf('Linux') > -1) {
                    this.mobiletype = 'android';
                } else if (u.indexOf('iPhone') > -1) {
                    this.mobiletype = 'iphone';
                }
            },
        },
        created(){
            this.Sjxh();
            window.AndroidLogin=()=>{
                this.$router.push({
                        path: this.fromUrl
                    })
                Toast({
                        message:'登录成功'
                    })
            }
            window.IOSLogin=()=>{
                this.$router.push({
                        path: this.fromUrl
                    })
                Toast({
                        message:'登录成功'
                    })
            }
        },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    .login-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index: 1000
        background-color: $color-bg
        .register
            width: 50px
            height: 50px
            position: absolute
            right: 0
            top: 0
            text-align: center
            line-height: 50px
            color #fff
        .form-wrap
            padding: 20px
</style>



