<template>
    <div style="width:100%">
        <!-- 搜索 -->
        <div class="search-box">
            <i class="icon-magnifier"></i>
            <input class="search-input" id="input" v-on:input="inputs" v-model.trim="searchText" type="text" placeholder="搜商家、地点">
            <i class="icon-QR_code"></i>
        </div>
        <div class="search-list" v-show="searchListShown">
            <ul v-if="merchants.length>0">
                <li v-for="(item,index) in merchants" @click="goToHere(item)" :key="index">{{item.name}}</li>
            </ul>
            <p class="no-data" v-else>暂无相关数据</p>
        </div>
        <!-- 左侧功能条 -->
        <div class="util-bar">
            <div class="item border-1px">
                <mu-icon value="cloud_download" :size="20" color="orange"/>
                功能1
            </div>
            <div class="item border-1px">
                <mu-icon value="flight_takeoff" :size="20" color="red" @click="tohairred"/>
                红包
            </div>
            <div class="item border-1px">
                <mu-icon value="home" :size="20" color="blue"/>
                功能3
            </div>
        </div>
        <!-- 地图 -->
        <u-map @map="setMap" @clickMap="clickMapHandler" ref="map">
            <!-- 商铺坐标点 -->
            <point :map="map"
                   @pointclick="clickHandler"
                   v-for="(item, index) in points"
                   :id="item.id"
                   :key="index"
                   :is-red="item.isRed"
                   :position="{lng: item.locationX,lat:item.locationY}"
                   :ref='"marker"+item.id'>
            </point>
        </u-map>
        <!-- 红包弹窗 -->
        <mu-popup position="top" popupClass="demo-popup-top" @close="close" :open="redPopup">
            <red-bag @receiveBonus="receiveBonusHandler"></red-bag>
        </mu-popup>
        <!-- 商家弹窗 -->
        <mu-popup position="top" popupClass="demo-popup-top" @close="close" :open="topPopup">
            <div class="seller">
                <div class="top">
                    <img class="blur" src="./timg.jpg" alt="">
                    <div class="zz"></div>
                    <mu-avatar class="avatar" :size="80" src="data:image/jpeg;base64,"/>
                    <div class="name">{{sellerDialog.name}}</div>
                </div>
                <div class="bot">
                    <div class="com-name">{{sellerDialog.name}}</div>
                    <div class="contact">
                        <div class="con-row">
                            <div class="icon">
                                <i class="icon-dianhua"></i>
                            </div>
                            <span>{{sellerDialog.phone}}</span>
                        </div>
                        <div class="con-row">
                            <div class="icon" style="background-color: #c781f6">
                                <i class="icon-yule"></i>

                            </div>
                            <span>{{sellerDialog.address}}</span>
                        </div>
                        <div class="goon" @click="goSeller(sellerDialog.id)">进入商铺</div>
                    </div>
                </div>
            </div>
        </mu-popup>
        <transition name="slide">
            <router-view v-on:sendBonus="sendBonusHandler"></router-view>
        </transition>
    </div>
</template>

<script>
    import UMap from './comp/map/map';
    import Point from './comp/point/point';
    import RedBag from './comp/red-bag/red-bag';
    import { mapMutations } from 'vuex';
    import {API_SELLER ,API_REDBAG, API_GET_USER_INFO,API_RECEIVE_WELFARE} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Toast,Indicator } from 'mint-ui';

	export default {
	    data() {
	       return {
               sellerDialog: {},
               sellerDialoe: {},
               map: null,
               topPopup: false,
               redPopup: false,
               i: 0,
               points: [],
               merchants:[],
               bonus:[],
               searchListShown:false,
               searchText:'',
               userStatus:"0",
               activeBonus:null
           }
        },
        mounted(){
            this.getUserInfo();
        },
        activated(){
            console.log('activated')
        },
	    methods: {
            ...mapMutations({
                setCompany: 'SET_COMPANY_MSG'
            }),
            //获取范围内商家
            getSeller () {
                return axios.post(API_SELLER, qs.stringify({
                    latitude: this.map.getCenter.lat,
                    longidute: this.map.getCenter.lng,
                    scale: this.map.getZoom(),
                    screenHeight: window.screen.height,
                    screenWidth: window.screen.width
                }));
            },
            //获取范围内红包
            getBonus () {
                return axios.post(API_REDBAG,qs.stringify({
                    latitude:this.map.getCenter.lat,
                    longidute:this.map.getCenter.lng,
                    scale:this.map.getZoom(),
                    screenHeight:window.screen.height,
                    screenWidth:window.screen.width,
                    utoken:this.$login.getToken()
                }));
            },
            //初始化数据
            initData(){
                let that = this
                axios.all([this.getSeller(), this.getBonus()])
                    .then(axios.spread(function (sellerData, bonusData) {
                        let merchants = sellerData.data.result === 0 ? sellerData.data.list : [];
                        let bonus = bonusData.data.result === 0 ? bonusData.data.list : []
                        for(let i=0;i < bonus.length;i++){
                            bonus[i].isRed = true
                        }
                        that.merchants = merchants
                        that.bonus = bonus;
                        that.points = [...that.merchants, ...that.bonus];
                    }));
            },
            // 获取用户信息
            getUserInfo(){
                axios.post(API_GET_USER_INFO, qs.stringify({
                    utoken:this.$login.getToken()
                })).then((response) => {
                    let res = response.data
                    this.userStatus = res.userStatus
                }, function(error){
                    console.log(error)
                })
            },
	        setMap(a) {
                this.map = a.map
                this.initData()
            },
            // 点击搜索到的商家
            goToHere(merchant){
                this.$refs.map.goTo(merchant)
                this.searchListShown =false;
                this.searchText = merchant.name
            },
            //点击地图
            clickMapHandler(location){
                if(this.userStatus == '1'){
                    this.$router.push({path:'index/hairredbag?lng='+location.posx+'&lat='+location.posy});
                }
            },
            clickHandler(obj) {
	            for(let i = 0; i < this.points.length; i++) {
	                if(this.points[i].id === obj.id) {
	                    this.sellerDialog = this.points[i]
                    }
                }
	            if(obj.isRed) {
	                this.redPopup = true;
                    this.activeBonus = obj
                }else{
	                this.topPopup = true;
                }
            },
            receiveBonusHandler(){
                Indicator.open({
                  text: '领取中'
                });
                let bonusId = this.activeBonus.id
                axios.post(API_RECEIVE_WELFARE, qs.stringify({
                    welfareId:bonusId,
                    utoken:this.$login.getToken()
                })).then((response) => {
                    Indicator.close()
                    let res = response.data
                    if(res.result == 0){
                        this.redPopup = false;
                        //删除红包
                        this.deleteBonusFromMap(bonusId)
                    }
                    Toast({
                        message:res.msg
                    })
                }, function(error){
                    setTimeout(function(){
                        Indicator.close()
                        Toast({
                            message:'领取失败'
                        })
                    },1000)
                })
            },
            //删除红包
            deleteBonusFromMap(bonusId){
                this.$refs['marker'+bonusId][0].removeMarker()
                this.initData()
            },
            close() {
	            this.topPopup = false;
                this.redPopup = false;
            },
            inputs(){
                var searchText = this.searchText;
                if(searchText.length>0){
                    this.searchListShown = true
                }else{
                    this.searchListShown = false
                }
                console.log(searchText)
                // for(let i = 0; i < this.points.length; i++) {
	               //  if(searchText === this.points[i].name) {
                //         console.log(this.points[i].locationX);
                //         console.log(this.points[i].locationY);
                //     }
                // }
            },
            goSeller(id) {
	            this.setCompany(this.sellerDialog);
	            this.$router.push({
                    path: `/index/seller${id}`
                });
                this.topPopup = this.redPopup = false;
            },
            tohairred(){
                this.$router.push({path:'index/hairredbag?lng='+location.posx+'&lat='+location.posy});
            },
            sendBonusHandler (){
                this.initData();
            }
        },
        components: {
            UMap,
            Point,
            RedBag
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/mixin"
    @import "~common/stylus/variable"

    .mint-toast{
        z-index: 999999999 !important;
    }
    .no-data
        color:#999;
    .search-list 
        position:absolute;
        top:52px;
        left: 30px;
        right: 40px;
        background: #fff;
        box-shadow: 0 0 5px rgba(0,0,0,0.6);
        z-index: 2;
        padding: 5px 10px;
        line-height: 24px;

    .search-box
        width: 360px
        height: 44px
        background-color #fff
        position: absolute
        z-index: 9
        left: 50%
        top: 8px
        border-radius 8px
        box-shadow 1px 1px 5px 1px #cecacb
        margin-left: -180px
        line-height: 44px
        padding-left: 22px
        padding-right: 16px
        box-sizing border-box
        color #959595
        display: flex
        .search-input
            border none
            padding: 0
            outline none
            color #959595
            flex 1
    .util-bar
        width: 36px
        background-color #fff
        position: absolute
        top: 50%
        margin-top: -173px
        right: 6px
        z-index: 9
        border-radius 10px
        box-shadow 1px 1px 5px 1px #cecacb
        padding: 0 4px
        box-sizing border-box
        .item
            width: 100%
            height: 43px
            border-b(#ccc)
            font-size: 10px
            text-align: center
    .seller
        width: 242px
        height: 319px
        .top
            height: 158px
            position: relative
            overflow: hidden

            img
                width: 100%
            .zz
                width: 100%
                height: 100%
                background-color: #000
                opacity: 0.5
                position: absolute
                left: 0
                top: 0
            .avatar
                position: absolute
                left: 50%
                top: 50%
                margin: -40px 0 0 -40px
            .name
                position: absolute
                left: 0
                bottom 10px
                font-weight: bold
                color #fff
                font-size: 14px
                text-align: center
                width: 100%
        .bot
            height: 161px
            color #636161
            font-size: 14px
            box-sizing border-box
            padding-top: 16px
            .com-name
                text-align: center
            .contact
                box-sizing border-box
                padding: 15px 20px 0
                font-size: 0
                .goon
                    width: 96px
                    height: 23px
                    border-radius 5px
                    color #fff
                    text-align: center
                    line-height: 23px
                    background-color $color-orange
                    font-size: 12px
                    position: absolute
                    left: 50%
                    margin-left: -48px
                    bottom: 25px
                .con-row
                    font-size: 14px
                .icon
                    width: 18px
                    height: 18px
                    color #fff
                    text-align: center
                    line-height: 1
                    background-color: $color-orange
                    border-radius 50%
                    display: inline-block
                    vertical-align: top
                    font-size: 14px
                    i
                        font-size: 10px
                span
                    font-size: 12px

    .demo-popup-top
        top:50% !important
        margin-top: -200px

</style>



