<template>
    <div class="pay-wrapper">
        <scroller>
            <enter-money msg="" @on-accountChange="accountHandler" @on-input="inputHandler"></enter-money>
            <div class="type">
                <div class="title border-1px">分类</div>
                <div class="content">
                    <ul class="u">
                        <li v-for="(value,index) in typeArr" :class="{on:index == b}" @click="cOne(index, value.id)">
                            <div class="icon-zhangmuguanli item"></div>
                            <span>{{value.name}}</span>
                        </li>
                        <li style="position: static;"  class="add">
                            <div class="icon-jia item"></div>
                        </li>
                    </ul>
                </div>
            </div>
            <mt-cell title="是否报销">
                <mt-switch v-model="value"></mt-switch>
            </mt-cell>
            <div :class="{bx:true,on:value}">
                <mt-cell @click.native="showPicker" title="报销单位" is-link>
                    <span>{{nowChoose}}</span>
                </mt-cell>
                <mt-cell @click="showLeixin" title="账目类别" is-link>
                    <span>请选择账目类别</span>
                </mt-cell>
                <mt-field label="商品名" placeholder="请输入商品名" v-model="goodsname"></mt-field>
                <mt-cell title="商品状态" is-link>
                    <span>请选择商品状态</span>
                </mt-cell>
                <img-upload style="margin-top: 5px;" @imgupload="imgUpload" title="上传凭证"></img-upload>
            </div>
        </scroller>
        <mt-popup
            v-model="popupVisible"
            position="bottom">
            <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
        </mt-popup>
        <div class="btn" @click="submitHandler">提交</div>
    </div>
</template>

<script>
    import EnterMoney from 'components/enter-money/enter-money';
    import ImgUpload from 'components/img-upload/img-upload';
    import { API_WRITE_ONE, API_TEAMLIST, API_PROCESS, API_EXPEND_TYPE } from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import writeOne from 'common/js/writeOne';

	export default {
	    data() {
	        return {
	            i: '',
                b: 0,
                value: false,
                goodsname: '',
                money: 0,
                slots: [
                    {
                        flex: 1,
                        values: []
                    }
                ],
                popupVisible: false,
                comArr: [],
                nowChoose: '请选择报销单位',
                nowCompany: {},
                nowAccount: {},
                imgArr: [],
                progressId: '',
                typeArr: []
            }
        },
        async created() {
            this._getCompany();
            this._getProcess();
            let res = await axios.post(API_EXPEND_TYPE, qs.stringify({
                utoken: this.$login.getToken()
            }));
            this.typeArr = res.data.list;
        },
        methods: {
            imgUpload(a) {
                this.imgArr = a;
            },
            accountHandler(a) {
                this.nowAccount = a
            },
	        async submitHandler() {
                let date = new Date();
                let json = {
                    utoken: this.$login.getToken(),
                    noteType: 1,
                    indexType: 1,
                    money: this.money,
                    bxStatus: this.value ? 1 : 0,
                    sourceId: this.i,
                    bxOfficeId: this.nowCompany.id,
                    accountId: this.nowAccount.id,
                    file64Base: this.imgArr,
                    t: this.nowAccount.t,
                    bxProcess: this.progressId,
                    useTime: `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()} ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`
                };
                writeOne(API_WRITE_ONE, json, this);
            },
            async _getCompany() {
	            let res = await axios.post(API_TEAMLIST, qs.stringify({
                    utoken: this.$login.getToken()
                }));
	            let data = res.data;
	            console.log('获取公司列表');
	            console.log(data);
	            let arr = data.listCompany.concat(data.listTeam);
	            this.comArr = arr;
            },
            async _getProcess() {
                let res = await axios.post(API_PROCESS, qs.stringify({
                    type: 2
                }));
                let data = res.data;
                console.log(res)
                console.log(1111);
                for (let i = 0; i < data.list.length; i++) {
                    if(data.list[i].name === '支出报销') {
                        this.progressId = data.list[i].value;
                    }
                }
            },
            showPicker() {
                let arr = this.comArr;
                this.slots[0].values = [];
                for(let i = 0; i < arr.length; i++) {
                    this.slots[0].values.push(arr[i].name);
                }
	            this.popupVisible = true;
            },
            showLeixin(){
                console.log(1)
            },
            onValuesChange(picker, v) {
                if(!v[0]) {
                    this.nowChoose = '请选择报销单位';
                    return;
                }
                this.nowChoose = v[0];
                for(let i = 0; i < this.comArr.length; i++) {
                    if (v[0] === this.comArr[i].name) {
                        this.nowCompany = this.comArr[i];
                    }
                }
            },
            inputHandler(value) {
                this.money = value;
            },
            cOne(index,value) {
                this.b = index;
                this.i = value;
            }
        },
	    components: {
	        EnterMoney,
            ImgUpload
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    .pay-wrapper
        position: absolute
        left: 0
        right: 0
        bottom: 100px
        top: 105px
        .mint-popup
            z-index: 10000!important
            width: 100%
        .mint-field-core
            text-align: right
            padding-right: 24px
        .mint-cell-text
            font-size: 12px
            color #8e8d8d
        .type
            width: 100%
            margin: 5px 0
            padding: 0 10px
            background-color #fff
            .title
                width: 100%
                height: 30px
                border-b(#ccc)
                font-size: 12px
                line-height:30px
                color #8e8d8d
            .content
                box-sizing border-box
                padding-top: 20px
                .u
                    display: flex
                    flex-wrap wrap
                    li
                        width: 25%
                        text-align: center
                        margin-bottom: 25px
                        &.on
                            span
                                color $color-orange
                            .item
                                background-color: $color-orange
                                color #fff
                        span
                            color #919191
                            font-size: 12px
                        .item
                            margin: 0 auto
                            width: 35px
                            height: 35px
                            border-radius 5px
                            text-align: center
                            line-height: 35px
                            color #bbb
                            background-color $color-gray
        .bx
            width: 100%
            margin-top: 5px
            display: none
            &.on
                display: block
        .btn
            width: 100%
            height: 42px
            position: absolute
            left: 0
            bottom: -45px
            line-height: 42px
            color #ffffff
            text-align: center
            background-color $color-orange
</style>



