<template>
    <div class="choose">
        <div class="subbtn" @click="decreaseCart">
            <i class="icon-jian"></i>
        </div>
        <div class="count">{{count}}</div>
        <div class="subbtn jia" @click="addCart">
            <i class="icon-jia"></i>
        </div>
    </div>
</template>

<script>
	export default {
	    props:{
	        goodMsg: {
	            type: Object
            }
        },
	    data() {
	        return {
	            count: 0,
                name:''
            }
        },
        methods: {
            addCart() {
                this.count++;
                this.$emit('addCart', {
                    id: this.goodMsg.id,
                    count: this.count,
                    sumPrice: this.count * this.goodMsg.price,
                    name:this.goodMsg.name
                })
            },
            decreaseCart() {
                if(this.count<=0) return;
                this.count--;
                this.$emit('addCart', {
                    id: this.goodMsg.id,
                    count: this.count,
                    sumPrice: this.count * this.goodMsg.price,
                    name:this.goodMsg.name
                })
            }
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    .choose
        flex 0 0 86px
        font-size: 12px
        display: flex!important
        justify-content center
        align-items center
        .subbtn
            width: 18px
            height: 18px
            border-radius 50%
            text-align: center
            background-color $color-orange
            color #fff
            &.jia
                border none
            i
                font-size: 10px
        .count
            margin: 0 10px
</style>



