<template>
    <div class="tonotiver-wrapper">
        <v-header :path="fromUrl" title="公告详情"></v-header>
        <div class="lists">
        <img src="http://img2.imgtn.bdimg.com/it/u=1383722746,199615552&fm=27&gp=0.jpg">
        <div class="content">
            <p>{{data.title}}</p>
            <span>{{data.content}}</span>
            <div class="time">{{data.type}}</div>
        </div>
        </div>
    </div>
</template>
<script>
    import VHeader from 'components/header/header';
    import {API_READ} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    export default {
        data() {
            return {
                fromUrl:'',
                token:'',
                id:'',
                data:{}
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        created(){
            this.token=this.$login.getToken();
            this.id=this.$route.params.id;
            console.log(this.id);
            this._request();
        },
        computed: {},
        components: {VHeader},
        methods: {
            async _request(){
                let res =await axios.post(API_READ,qs.stringify({
                    utoken:this.token,
                    notityId:this.id
                }));
                this.data=res.data.data;
                console.log(this.data);
            }
        }
    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"

    .tonotiver-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        z-index 9999
    .lists
        /*min-height 80px*/
        background-color #FFFFFF
        margin-top 10px
        display flex
        img
            margin 5px 0 5px 10px
            width 70px
            height:70px
            border-radius 50%
            justify-content center
            align-items center
        .content
            margin 5px 0 5px 10px
            p
                font-size 17px
                width 200px
            .time
                text-align right

</style>



