<template>
    <div class="approve-wrapper">
        <v-header :path="fromUrl" title="审批" style="position: relative; z-index: 2"></v-header>
        <!--<mt-search v-model="searchValue" style="position: relative; z-index: 2"></mt-search>-->
        <mt-navbar v-model="selected" style="position: relative; z-index: 2">
            <mt-tab-item id="1">待审批</mt-tab-item>
            <mt-tab-item id="2">已审批</mt-tab-item>
            <mt-tab-item id="3">我的申请</mt-tab-item>
        </mt-navbar>
        <div style="position: absolute;top:100px;bottom: 0;left: 0;right: 0">
        <scroller style="position: relative; z-index: 1">
        <!-- tab-container -->
        <mt-tab-container v-model="selected">
            <mt-tab-container-item id="1" v-if="exaList.length>0">
                <div>
                   <!-- <router-link tag="div" append :to="items.taskId" class="approve-item"  v-for="items in exaList" :key="items.taskId">-->
                        <div class="approve-item" v-for="items in exaList" :key="items.taskId" @click="ToDetails(items.taskId,$event)">
                        <div class="item-wrap">
                            <img src="//fuss10.elemecdn.com/f/8d/f29dbf20be425fc12426c0b1f90b7jpeg.jpeg?imageMogr/format/webp/thumbnail/!130x130r/gravity/Center/crop/130x130/" alt="">
                            <div class="title">
                                <span class="name">{{items.title}}</span>
                                <span class="date">{{items.createTime}}</span>
                            </div>
                            <div class="approve-type">{{items.procDefName}}</div>
                            <div class="approve-type">开始时间：{{items.createTime}}</div>
                            <!--<div class="approve-type">结束时间：2017-12-28</div>-->
                            <span class="state">{{items.status}}</span>
                            <span class="sign" v-show="items.taskType===2" @click.stop="sign(items.taskId,$event)">未签收</span><span class="bg" v-show="items.taskType===1" ref="yessign">已签收</span>
                        </div>
                        </div>
                    <!--</router-link>-->
                </div>
            </mt-tab-container-item>
            <mt-tab-container-item id="2" v-if="edList.length>0">
                <div class="con-my">
                    <router-link tag="div" append :to="items.taskId" class="approve-item"  v-for="items in edList" :key="items.taskId">
                        <div class="item-wrap">
                            <img src="//fuss10.elemecdn.com/f/8d/f29dbf20be425fc12426c0b1f90b7jpeg.jpeg?imageMogr/format/webp/thumbnail/!130x130r/gravity/Center/crop/130x130/" alt="">
                            <div class="title">
                                <span class="name">{{items.title}}</span>
                                <span class="date">{{items.createTime}}</span>
                            </div>
                            <div class="approve-type">{{items.procDefName}}</div>
                            <!--<div class="approve-type">开始时间：{{items.createTime}}</div>-->
                            <div class="approve-type">结束时间：2017-12-28</div>
                            <div class="state">{{items.status}}</div>
                        </div>
                    </router-link>
                </div>
            </mt-tab-container-item>
            <mt-tab-container-item id="3" v-if="applyList.length>0">
                <div>
                    <router-link tag="div" append :to="item.id" class="approve-item" v-for="item in applyList" :key="item.id">
                        <div class="item-wrap">
                            <img src="//fuss10.elemecdn.com/f/8d/f29dbf20be425fc12426c0b1f90b7jpeg.jpeg?imageMogr/format/webp/thumbnail/!130x130r/gravity/Center/crop/130x130/" alt="">
                            <div class="title">
                                <span class="name">{{item.applyTitle}}</span>
                                <span class="date">{{item.applyStartTime}}</span>
                            </div>
                            <div class="approve-type">请假类型：{{item.applyType}}</div>
                            <div class="approve-type">开始时间：{{item.applyStartTime}}</div>
                            <div class="approve-type">结束时间：{{item.applyEndTime}}</div>
                            <div class="state">{{item.applyuser}}</div>
                        </div>
                    </router-link>
                </div>
            </mt-tab-container-item>
        </mt-tab-container>

        </scroller>
        </div>
        <transition name="slide">
            <router-view></router-view>
        </transition>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import {API_APPLY_LIST,API_EXA_APP,API_SIGN_TASK} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Toast, Indicator } from 'mint-ui';

    export default {
	    data() {
	        return {
	            searchValue: '',
                selected: '1',
                fromUrl: '',
                token:'',
                id:'',
                applyList:[],
                exaList:[],
                edList:[]
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        created(){
            this.token=this.$login.getToken();
            this.id=this.$route.params.id;
            console.log(this.id);
            this._request();
            this._request_ex();
            this._request_ed();
        },
        methods:{
            async _request(){
                Indicator.open();
                let res=await axios.post(API_APPLY_LIST,qs.stringify({
                    utoken:this.token,
                    officeId:this.id
                }));
                Indicator.close();
                let data=res.data;
                if(data.result===0){
                    this.applyList=data.list;
                    console.log(data);
                }else if(data.result===2){
                    window.localStorage.clear();
                    this.$router.push('/login');
                    Toast({
                        message: data.msg,
                        duration: 2000
                    });
                }
            },
            //带
            async _request_ex(){
                Indicator.open();
                let res=await axios.post(API_EXA_APP,qs.stringify({
                    utoken:this.token,
                    actType:'1'
                }));
                Indicator.close();
                let data=res.data;
                if(data.result===0){
                    console.log(data);
                    this.exaList=data.list;
                }else if(data.result===2){
                    window.localStorage.clear();
                    this.$router.push('/login');
                    Toast({
                        message: data.msg,
                        duration: 2000
                    });
                }
            },
            async _request_ed(){
                Indicator.open();
                let res=await axios.post(API_EXA_APP,qs.stringify({
                    utoken:this.token,
                    actType:'2'
                }));
                Indicator.close();
                let data=res.data;
                console.log(data);
                if(data.result===0){
                    this.edList=data.list;
                }else if(data.result===2){
                    window.localStorage.clear();
                    this.$router.push('/login');
                    Toast({
                        message: data.msg,
                        duration: 2000
                    });
                }
            },

            ToDetails(id,ev){
                event= ev || event;
                var sign=event.currentTarget;
                console.log(sign);
                console.log(sign.lastChild.lastChild);
                if(sign.lastChild.lastChild.style.display==='none'){
                     alert('请先签收');
                 }else{
                    this.$router.push({
                        path:`approve/${id}`
                    })
                 }
            },
            sign(id,ev){
               event= ev || event;
               var nosign=event.target;
               var yessign=event.target.parentNode.lastChild;
               axios.post(API_SIGN_TASK,qs.stringify({
                       utoken:this.token,
                       taskId:id
                   })).then(res=>{
                       console.log(res);
                       nosign.style.display='none';
                       yessign.style.display='block';
               }).catch(err=>{
                       console.log(err);
                   })
           },
        },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .approve-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        .is-selected
            margin: 0!important
        .approve-item
            width: 100%
            padding: 10px
            margin-top: 10px
            background-color: #fff
            .item-wrap
                width: 100%
                position: relative
                padding-left: 40px
                .title
                    line-height: 20px
                    margin-bottom: 5px
                    display: flex
                    justify-content space-between
                    align-items flex-end
                    .name
                        font-size: 16px
                    .date
                        color #999
                .approve-type
                    color #777
                .state
                    margin-top: 4px
                    color $color-orange
                .sign
                    display inline-block
                    width 55px
                    height 25px
                    background-color #F08619
                    line-height 25px
                    border-radius 4px
                    color #FFF
                    text-align center
                    margin-left 70%
                img
                    width: 32px
                    height: 32px
                    border-radius 2px
                    position: absolute
                    left: 0
                    top: 0

    .bg
        display inline-block
        width 55px
        height 25px
        line-height 25px
        border-radius 4px
        color #FFF
        text-align center
        margin-left 70%
        background-color #CCC
</style>



