<template>
    <div class="friend-detail-wrap">
        <v-header path="/message/maillist" title="好友详情"></v-header>
        <div class="friend-detail">
            <img src="https://fuss10.elemecdn.com/c/f5/d0b0f2fc83f3ac3e4a0cfae891256png.png?imageMogr/format/webp/thumbnail/!60x60r/gravity/Center/crop/60x60/" alt="">
            <div class="name">{{name2}}</div>
        </div>
        <div class="btn-group">
            <div class="send-msg" @click="goSendMsg">发消息</div>
            <div class="del-friend" @click="delFriend">删除好友</div>
        </div>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import { Indicator, Toast } from 'mint-ui';
    import {API_DEL_FRIEND} from 'common/js/api';
    import  axios from 'axios'
    import qs from 'qs';



    export default {
	    data() {
	        return {
                name2:'',
                id:'',
                token:''

            }
        },
        created() {
	        this.name2=this.$route.params.login;
            this.id = this.$route.params.id;
            this.token=this.$login.getToken();
            console.log(this.$route.params);
            console.log(this.$router);
        },
        methods: {
            delFriend() {
               /* this.$router.push({
                    path: '/message'
                });
                let self = this;
                Indicator.open();*/
               /*this.$im.removeRoster({
                    to: this.name,
                    success: function () {  // 删除成功
                        Indicator.close();
                        Toast({
                            message: '删除成功',
                            duration: 2000
                        });
                        self.$router.push({
                            path: '/message/maillist'
                        });
                        this.$im.unsubscribed({
                            to: this.name
                        });
                    },
                    error: function () {    // 删除失败
                        console.log(2)
                    }
                });*/
                axios.post(API_DEL_FRIEND,qs.stringify({
                    utoken:this.token,
                    target:this.id
                })).then((res)=> {
                    let data=res.data;
                    if(data.result===0){
                    this.$router.push('/message/maillist');
                    }else if(data.result===2){
                        Toast({
                            message: '用户已失效,请重新登陆',
                            duration: 1000
                        });
                        window.localStorage.clear();
                        this.isLogin = false;
                    }
                }).catch((err)=>{
                    console.log(err);
                })
            },
                goSendMsg() {
                this.$router.push({
                    path: `/message/talk/${this.name2}`
                })
            }
        },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .friend-detail-wrap
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index: 1000
        background-color: $color-bg
        .friend-detail
            padding: 20px
            display: flex
            background-color: #fff
            .name
                font-size: 16px
                padding-left: 20px
                font-weight: bold
        .btn-group
            div
                margin: 0 auto
                text-align: center
                width: 90%
                line-height: 40px
                margin-top: 20px
                color #fff
                border-radius 4px
                font-size: 14px
                background-color: $color-orange
            .del-friend
                background-color: rgb(255, 73, 72)
            .send-msg
                background-color: rgb(60, 176, 53)
    /*60 176 52*/
</style>



