import Index from 'page/index/index';
import Seller from 'page/index/subpage/seller/seller';
import Goods from 'page/index/subpage/seller/goods';
import Comments from 'page/index/subpage/seller/comments';
import Msg from 'page/index/subpage/seller/msg';
import Cart from 'page/index/subpage/cart/cart';

import HairRedBag from 'page/index/subpage/hairredbag/hairredbag';
import PaySelect from 'page/index/subpage/payselect/payselect'
export default {
    path: '/index',
    component: Index,
    children: [
        {
            path: 'seller:id',
            component: Seller,
            redirect: 'seller:id/goods',
            children: [
                {
                    path: 'goods',
                    component: Goods,
                    children: [
                        {
                            path: 'cart',
                            component: Cart,
                            children:[
                                {
                                    path:'payselect',
                                    component:PaySelect
                                }
                            ]
                        }
                    ]
                },
                {
                    path: 'comments',
                    component: Comments
                },
                {
                    path: 'msg',
                    component: Msg
                }
            ]
        },
        {
            path:'hairredbag',
            component:HairRedBag,
            meta: {
                requireAuth: true
            }
        }
    ]
}



