<template>
    <div class="payapp-wrapper">
        <v-header :path="fromUrl" title="购买"></v-header>
        <div class="cart-main">
            <div class="cart-item">
                <div class="seller-name border-1px">
                    <!--<mu-avatar :size="32" slot="left" src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=138253999,3830018517&fm=27&gp=0.jpg"/>-->
                    <!--xxx汽车服务中心-->
                </div>
                <div class="goods-item">
                    <div class="img-wrap">
                        <img :src="src" alt="">
                    </div>
                    <div class="text-content">
                        <div class="good-name">{{name}}</div>
                        <!--<div class="abstract"></div>-->
                        <div class="money">
                            <!--<div class="num" ref="money">￥/年</div>-->
                            <!--<div class="con">
                                <div class="prev" @click="Prev">-</div>
                                <div class="d-num">{{j}}</div>
                                <div class="add" @click="add">+</div>
                            </div>-->
                        </div>
                    </div>
                </div>
                <div v-on:click.capture="ToIndex($event)">
                    <mt-radio
                        title="请选择购买的类型"
                        v-model="value"
                        class="border-1px"
                        :options="options"
                    >
                    </mt-radio>
                </div>
            </div>
        </div>

        <div class="bot-bar">
            <!--<div class="text">
                已选购<span class="orange">￥</span><span class="fz18 orange"></span>
            </div>-->
            <div class="yes-btn"  @click="Settlement">结算</div>
        </div>
    </div>
</template>
<script>
    import VHeader from 'components/header/header';
    import { mapGetters } from 'vuex';
    import {API_SETTLEMENT} from 'common/js/api';
    import { Indicator, Toast } from 'mint-ui';
    import qs from 'qs';
    import axios from 'axios';


    export default {
        name: 'PayApp',
        data() {
            return {
                value:'',
                options:[],
                fromUrl:'',
                name:'',
                src:'',
                token:'',
                comid:'',
                id:'',
                arr:[],
                eventval:[]
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        created(){
            let token = this.$login.getToken();
            this.token=token;
            this.comid = localStorage.getItem("indexID");
            this.id=this.$route.params.id;
            // console.log(id);
            for(var i=0;i<this.getPayApp.length;i++){
                if(this.id===this.getPayApp[i].id){
                    console.log(this.getPayApp[i]);
                    this.name=this.getPayApp[i].name;
                    this.src=this.getPayApp[i].pic;
                    this.arr=this.getPayApp[i].priceList;
                    console.log(this.getPayApp[i].priceList);
                }
            }
            var opt=[];
            for(var j=0;j<this.arr.length;j++){
                var obj=this.arr[j];
                var mon=''+obj.money+'元 /'+obj.cycle+'年';
                opt.push({
                    label:mon,
                    value:obj.id,
                })
            }
            console.log(opt);
            this.options=opt;
        },
        computed: {
                ...mapGetters([
                        'getPayApp'
                ])
        },
        components: {VHeader},
        methods: {
            ToIndex(ev){
                var event = ev || ev.target;
                this.eventval=event.target.value
            },
            Settlement(){
                if(this.eventval===''){
                    alert('请选择购买的类型')
                }else{
                    axios.post(API_SETTLEMENT,qs.stringify({
                        utoken:this.token,
                        officeId:this.comid,
                        menuId:this.id,
                        priceId:this.eventval
                    })).then(res=>{
                        let data=res.data;
                        console.log(res);
                        if(data.result===0){
                            Toast({
                                message: '购买成功',
                                duration: 1000
                            });
                            this.$router.push('/app');
                        }else if(data.result===2){
                            Toast({
                                message: '用户已失效，请重新登陆',
                                duration: 2000
                            });
                            this.$router.push('/login');
                        }
                    }).catch(err=>{
                        console.log(err);
                    })
                }
            },
        }
    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"

    .payapp-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index: 100000000
        background-color: #ffffff


        .cart-main
            position: absolute
            top: 50px
            bottom: 45px
            left: 0
            right: 0
            font-size: 12px
            .cart-item
                margin-top: 10px
                padding: 0 10px
                background-color #fff
                .seller-name
                    padding: 5px 0
                    line-height: 32px
                    border-b(#ccc)
                .mu-avatar
                    margin-right: 5px
                    vertical-align: top
                .goods-item
                    padding: 10px 0
                    display: flex
                    align-items center
                    .img-wrap
                        flex 0 0 76px
                        height: 76px
                        border 1px solid #ccc
                        border-radius 5px
                        overflow: hidden
                        box-sizing content-box
                        img
                            width: 100%
                    .text-content
                        box-sizing content-box
                        padding: 0 10px
                        flex 1
                        .good-name
                            font-size: 14px
                        .abstract
                            color #989292
                            height: 39px
                        .money
                            display: flex
                            color $color-orange
                            .num,.con
                                flex 1
                            .con
                                text-align: right
                                font-size: 0
                                div
                                    display: inline-block
                                    vertical-align: top
                                .prev,.add
                                    width: 25px
                                    height: 100%
                                    text-align: center
                                    line-height: 17px
                                    font-size: 18px
                                    border 1px solid #ccc
                                .d-num
                                    width: 35px
                                    font-size: 12px
                                    line-height: 17px
                                    height: 100%
                                    border 1px solid #ccc
                                    border-left none
                                    border-right none
                                    text-align: center



    .bot-bar
        position: absolute
        left: 0
        right: 0
        bottom: 0
        height: 45px
        /*display: flex*/
        line-height: 45px
        box-sizing border-box
        /*padding-left: 28px*/
        /*color #b4b4b4*/
        /*background-color $color-black*/
        .orange
            color $color-orange
        .fz18
            font-size: 18px
        .text
            flex 1
        .yes-btn
            height: 100%
            width 100%
            background-color $color-orange
            color #fff
            text-align: center
            line-height: 45px
            flex 0 0 112px



</style>



