const state = {
    isLogin: ''
};

const getters = {
    getIsLogin: state => state.isLogin
};

const mutations = {
    ['SET_COMPANY_MSG'](state, isLogin) {
        state.msisLoging = isLogin;
    }
};

export default {
    state,
    getters,
    mutations
}



