<template>
    <div class="comments-wrapper">
        <scroller>
            <div style="padding-top: 5px;">
                <div v-for="item in commentList" class="comment-item">
                    <div class="comment-block border-1px">
                        <img class="user" src="https://fuss10.elemecdn.com/c/f5/d0b0f2fc83f3ac3e4a0cfae891256png.png?imageMogr/format/webp/thumbnail/!60x60r/gravity/Center/crop/60x60/" alt="">
                        <div class="comment-text">
                            <div class="name">
                                <div style="display: flex;">
                                    <h3>匿名用户</h3>
                                    <Rate v-model="item.starNum" :disabled="true"></Rate>
                                </div>

                                <small>2017-12-09</small>
                            </div>
                            <div class="comment-content">{{item.content}}</div>
                        </div>
                    </div>
                </div>
            </div>
        </scroller>
        <!--<img src="" alt="">-->
    </div>
</template>

<script>
    import { API_COMMENTS } from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Indicator } from 'mint-ui';

	export default {
	    data() {
	        return {
	            commentList: []
            }
        },
	    async created() {
            Indicator.open();
	        let res = await axios.post(API_COMMENTS, qs.stringify({
                officeId: this.$route.params.id,
                utoken: this.$login.getToken()
            }));
            Indicator.close();
	        let data = res.data;
	        if(data.result === 0) {
                this.commentList = data.list;
                console.log(this.commentList)
            }else{
	            window.localStorage.clear();

            }
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"
    .comments-wrapper
        width: 100%
        height: 100%
        box-sizing border-box
        padding-top: 10px
        background-color $color-bg
        .comment-item
            padding: 0 12px
            background-color: #fff
            .comment-block
                padding: 15px 0
                position: relative
                padding-left: 45px
                border-b(#bbb)
                .user
                    width: 30px
                    height: 30px
                    position: absolute
                    top: 15px
                    left: 0
                .comment-text
                    .name
                        h3
                            font-size: 12px
                            margin-right: 6px
                        .ivu-rate
                            font-size 12px!important
                        small
                            color #ddd
                    .comment-content
                        padding-top: 10px
                        font-size: 12px
</style>



