<template>
    <div class="bxList-wrapper">
        <v-header path="/account/billlist" title="批量报销"></v-header>
        <div class="scroller-wrapepr">
            <scroller>
                <mt-cell title="报销单位" :value="bxCom" isLink @click.native="popupVisible = !popupVisible"></mt-cell>
                <div class="bxList-item" v-for="item in getBxList">
                    <mt-cell class="border-1px" title="报销金额" value="￥10"></mt-cell>
                    <mt-field class="border-1px" label="商品名" placeholder="请输入商品名"></mt-field>
                    <mt-field class="border-1px" label="备注" placeholder="备注" type="textarea" rows="4"></mt-field>
                    <img-upload title="上传凭证"></img-upload>
                </div>
            </scroller>

        </div>
        <div class="bBar">
            <div>总报销金额：￥30</div>
            <div class="bBar-btn">提交报销</div>
        </div>
        <mt-popup
            v-model="popupVisible"
            position="bottom">
            <mt-picker :slots="slots" @change="changeHandler"></mt-picker>
        </mt-popup>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import { mapGetters } from 'vuex';
    import axios from 'axios';
    import { API_TEAMLIST } from 'common/js/api';
    import qs from 'qs';
    import ImgUpload from 'components/img-upload/img-upload';

	export default {
	    data() {
	        return {
                slots: [
                    {
                        values: []
                    }
                ],
                bxCom: '请选择报销单位',
                popupVisible: false,
                comArr: []
            }
        },
	    computed: {
            ...mapGetters([
                'getBxList'
            ])
        },
        async created() {
            let res = await axios.post(API_TEAMLIST, qs.stringify({
                utoken: this.$login.getToken()
            }));
            let data = res.data;
            let arr = data.listCompany.concat(data.listTeam);
            this.comArr = arr;
            this.slots[0].values = [];
            for(let i = 0; i < arr.length; i++) {
                this.slots[0].values.push(arr[i].name)
            }
        },
        methods: {
            changeHandler(picker, value) {
                this.bxCom = !value[0] ? '请选择报销单位' : value[0];
            }
        },
	    components: {
	        VHeader,
            ImgUpload
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"

    .bxList-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index: 1000
        background-color: $color-bg
        .bBar
            position: absolute
            left: 0
            right: 0
            bottom: 0
            height: 50px
            background-color: $color-black
            display: flex
            justify-content space-between
            align-items center
            color #fff
            padding-left: 10px
            .bBar-btn
                line-height: 50px
                background-color: $color-orange
                text-align: center
                width: 94px
        .scroller-wrapepr
            position: absolute
            left: 0
            right: 0
            top: 50px
            bottom: 50px
            .bxList-item
                margin-top: 10px
                background-color: #fff
                .mint-cell
                    border-b(#eee)
                .is-textarea
                    .mint-cell-wrapper
                        align-items flex-start
                    .name
                        flex 0 0 70px
                        padding: 0 5px
                        color #999
</style>



