<template>
    <div class="mail-list-wrap">
        <v-header path="/message" title="通讯录"></v-header>
        <mt-index-list>
            <mt-index-section v-for="(item, index) in arr" :key="index" :index="item.indexWord">
                <mt-cell v-for="(person, index) in item.group"
                         :key="index"
                         :title="person.name"
                         @click.native="goTalk(person.restName,person.name)">
                </mt-cell>
            </mt-index-section>
        </mt-index-list>
        <router-link class="search-friend" append tag="div" to="searchfriend">
            <i class="icon-jia"></i>
        </router-link>
        <router-view></router-view>
    </div>
</template>

<script>
    import { mapGetters ,mapMutations} from 'vuex';
    import VHeader from 'components/header/header';
    import {API_FRIEND_LIST} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
	export default {
	    data() {
	        return {
	            arr: [],
                friendList:[],
                token:''
            }
        },
        computed: {
            ...mapGetters([
                'getFriendList',
                'getConnState'
            ])
        },
        created() {
            let token = this.$login.getToken();
            this.token=token;
            this._request(this.token);
            },
        beforeRouteUpdate (to, from, next) {
            let str = '/message/maillist/friend';
            console.log(from.path);
            this._request(this.token);
            if(from.path.substring(0, str.length) === str) {
                console.log(from.path.substring(0, str.length));
                this._resetDom();
            }
            next();
        },
        methods: {
            async _request(token) {
                let res = await axios.post(API_FRIEND_LIST, qs.stringify({
                    utoken: token,
                    targetType: 'users'
                }));
                let data = res.data;
                console.log(data);
                this.friendList = data.list;
                this._resetDom();
            },

	        async _getFriend() {
                await this.$store.dispatch('getFriendList');
                console.log(this.getFriendList);
                this._resetDom();
            },

            _resetDom() {
                console.log(this.arr);
                this.arr = [];
                // let friendArr = this.getFriendList;
                let friendArr = this.friendList;
                console.log(friendArr);
                this.setFriendList(friendArr);
                for(let i = 0; i < friendArr.length; i++) {
                    let item = friendArr[i];
                    let indexWord = item.name.charAt(0).toUpperCase();
                    if(indexWord.charCodeAt() >= 65 && indexWord.charCodeAt() <= 90) {
                        if(this.arr.length > 0) {
                            let obj = this._checkRepeat(this.arr, indexWord);
                            if(!obj.ok) {
                                /*if(!this.arr[i].group){
                                    this.arr[i].group.push(item);
                                }else{
                                    this.arr[i].group = [{item}]
                                }*/
                                this.arr.push({
                                    indexWord,
                                    group: [item]
                                })
                            }else{
                                this.arr[obj.i].group.push(item)
                            }
                        }else{
                            this.arr.push({
                                indexWord,
                                group: [item]
                            })
                        }
                    }
                }
                this.arr.sort((a, b) => {
                    return a.indexWord.charCodeAt() - b.indexWord.charCodeAt()
                });
            },
            goTalk(id,login) {
                /*this.$router.push({
                    path: `maillist/friend/${id}`
                })*/
                this.$router.push({
                    path: `maillist/friend/${id}/${login}`
                })
            },
            ...mapMutations({
                'setFriendList':"SET_FRIEND_LIST"
            }),
	        _checkRepeat(arr, word) {
	            for(let i = 0; i < arr.length; i++) {
	                if(arr[i].indexWord === word) {
	                    return {
	                        ok: true,
                            i
                        };
                    }
                }
                return {
	                ok: false
                }
            }
        },
        watch: {
            getConnState(a, b) {
                if(a) {
                    this._getFriend()
                }
            }
        },
        components: {
            VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    .mail-list-wrap
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index: 1000
        background-color: #fff
        .search-friend
            width: 50px
            line-height: 50px
            position: absolute
            right: 0
            top: 0
            text-align: center
            color #fff
</style>



