import Account from 'page/account/account';
import WriteOne from 'page/account/subpage/write-one/write-one';
import Income from 'page/account/subpage/income/income';
import Pay from 'page/account/subpage/pay/pay';
import Sincome from 'page/account/subpage/sincome/sincome';
import Spay from 'page/account/subpage/spay/spay';
import BillList from 'page/account/subpage/bill-list/bill-list';
import BillDetail from 'page/account/subpage/bill-detail/bill-detail';
import BxList from 'page/account/subpage/bxList/bxList';

export default {
    path: '/account',
    component: Account,
    children: [
        {
            path: 'writeone',
            component: WriteOne,
            redirect: 'writeone/income',
            children:[
                {
                    path: 'income',
                    component: Income
                },
                {
                    path: 'pay',
                    component: Pay
                },
                {
                    path: 'sincome',
                    component: Sincome
                },
                {
                    path: 'spay',
                    component: Spay
                }
            ]
        },
        {
            path: 'billlist',
            component: BillList
        },
        {
            path: `billd:id`,
            component: BillDetail
        },
        {
            path: 'bxList',
            component: BxList
        }
    ]
}



