<template>
    <div class="record-wrapper">
        <v-header path="/mine" title="消费记录"></v-header>
        <div class="record-main">
                <scroller>
                    <div v-for="(item,index) in list" :key="index" class="record-item">
                        <div class="record-item-body">
                            <div class="record-item-avatar">
                                <!--<img src="https://fuss10.elemecdn.com/4/2b/77b246abf67bd8929ee941c9aea94png.png?imageMogr/format/webp/thumbnail/!64x64r/gravity/Center/crop/64x64/" alt="">-->
                            </div>
                            <div class="record-item-content">
                                <div class="record-item-head border-1px">
                                    <div class="title">
                                        <div class="name">
                                            <span class="content">{{item.name}}</span>
                                        </div>
                                        <div class="status" v-if="item.paymentStatus==1">未付款</div>
                                        <div class="status" v-else-if="item.paymentStatus==2">已付款</div>
                                        <!-- <div class="status" v-else-if="item.status==2">正在处理</div>
                                        <div class="status" v-else-if="item.status==3">处理完毕</div> -->
                                    </div>
                                    <div class="datetime">{{item.payDate}}</div>
                                </div>
                                <div class="record-item-detail border-1px" @click="goDetail(item.id,item.name,item.payDate,item.code)">
                                    <div class="detail">
                                        <span class="productname">订单编号：{{item.code}}</span>
                                    </div>
                                    <div class="price">￥{{item.money}}</div>
                                </div>
                                <div class="record-item-bottom">
                                    <button class="record-btn" @click="goComment(item.id)"  v-if="item.discussStatus==1&&item.status ==1">评价</button>
                                    <button v-else-if="item.discussStatus===2&&item.status==!0" class="record-btn btnNot" >已评价</button>
                                    <button class="record-btn" @click="goPurchase(item.id,item.shoppingId)"  v-else-if="item.status==0">继续购买</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </scroller>
        </div>
        <transition name="slide">
            <router-view></router-view>
        </transition>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import {API_CONSUMPTION} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Toast, Indicator } from 'mint-ui';

    export default {
	    data() {
	        return {
	            list: [
                    /*{
                        id: 'abc01',
                        name: 'xxxxxx汽车服务中心',
                        price: '￥2000.00',
                        product: '汽车保养',
                        date: '2017-10-01 14:53'
                    },*/
                ],
            }
        },
        created(){
            let token = this.$login.getToken();
            this._request(token);
        },
        methods: {
	        async _request(token){
	            let res=await axios.post(API_CONSUMPTION,qs.stringify({
                    utoken:token
                }));
                let data=res.data;
                this.list=data.list;
                console.log(data);
            },
            goDetail(id,name,payDate,code) {
                this.$router.push({
                    path: `/mine/record/${id}`,query:{
                            id:id,
                            name:name,
                            payDate:payDate,
                            code:code
                        }
                })
            },
            goComment(id) {
                this.$router.push({
                    path: `/mine/record/comment/${id}`
                })
            },
            goPurchase(id,spid){
                console.log()
                this.$router.push({
                    path: `/index/sellerd${spid}/goods/cart`
                })
            }
        },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"

    .record-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index: 1000
        background-color: $color-bg
        .record-main
            position: absolute
            left: 0
            right: 0
            top: 50px
            bottom: 0
            .record-item
                margin-bottom: 10px
                padding: 14px 0 0 15px
                background-color: #fff
                .record-item-body
                    display: flex
                    .record-item-avatar
                        padding-right: 10px
                        flex none
                        img
                            width: 31px
                            height: 31px
                            border-radius 2px
                    .record-item-content
                        flex 1
                        .record-item-head
                            padding: 0 13px 10px 0
                            border-b(#eee)
                            .title
                                display: flex
                                align-items center
                                justify-content space-between
                                .name
                                    display: flex
                                    align-items center
                                    line-height: 24px
                                    .content
                                        max-width 160px
                                        display: inline-block;
                                        overflow: hidden;
                                        text-overflow: ellipsis;
                                        white-space: nowrap;
                                .status
                                    font-size: 12px;
                                    text-align: right;
                                    color: #333;
                                    max-width: 65px;
                                    overflow: hidden;
                                    text-overflow: ellipsis;
                                    white-space: nowrap;
                            .datetime
                                font-size: 10px
                                color #999
                        .record-item-detail
                            display: flex
                            justify-content space-between
                            padding: 15px 13px 15px 0
                            border-b(#eee)
                            .detail
                                font-size: 12px;
                                color: #666;
                                display: flex;
                                align-items: center;
                                .productname
                                    display: inline-block;
                                    max-width: 155px;
                                    overflow: hidden;
                                    text-overflow: ellipsis;
                                    white-space: nowrap;
                        .record-item-bottom
                            display: flex
                            justify-content flex-end
                            padding: 10px 16px
                            .record-btn
                                width: 100px
                                padding: 5px 10px
                                border-radius 2px
                                border: 1px solid $color-orange
                                background-color: transparent
                                color $color-orange
                                font-size: 12px
                            .btnNot
                                border: 1px solid #CCCCCC !important
                                color #CCCCCC !important

</style>



