<template>
    <scroller>
        <div class="detail-msg-wrapper">
            <div class="top-msg">
                <div class="detail-msg">
                    <div>净利润：<span class="fw">￥{{money.netProfit}}</span></div>
                    <div>次年公积金：<span class="fw">￥{{money.nextprovident}}</span></div>
                    <div>
                        <span style="display: inline-block;vertical-align: top;margin-right: 20px;">利润总额： ￥{{money.totalProfit}}</span>
                        <span>所得税： ￥{{money.incomeTax}}</span>
                    </div>
                </div>
            </div>

            <div class="detail-list">
                <Table :columns="tableData.columns1" :data="tableData.data1"></Table>
            </div>
        </div>
    </scroller>
</template>

<script>
    import echarts from 'echarts';
    import {API_MONEY_INFO} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Indicator, Toast } from 'mint-ui';

    export default {
        data() {
            return {
                charts: null,
                money:{},
                tables:[],
                tableData: {
                    columns1: [
                        {
                            title: '姓名',
                            key: 'inType'
                        },
                        {
                            title: '金额',
                            key: 'num'
                        }
                    ],
                    data1: []
                }
            }
        },
        methods:{

            income(){
                let res=axios.post(API_MONEY_INFO,qs.stringify({
                    utoken:this.$login.getToken(),
                    officeId:this.$route.params.id,
                    type:'3',
                })).then(res=>{
                    console.log(res);
                    console.log(res.data.data);
                    this.money=res.data.data;
                    let List=res.data.reportList;
                    let seriesList=[];
                    this.tables=seriesList;
                    for(var i=0;i<List.length;i++){
                        seriesList.push({
                            num:List[i].value,
                            inType:List[i].soure
                        })
                    }
                    this.tableData.data1=this.tables;
                    console.log(this.tableData.data1);
                    console.log(this.tables);
                }).catch(err=>{
                    console.log(err);
                })
            },
        },
        watch: {
            tableData:{
                handler:function(val,oldval){
                    console.log('开始');
                    this.tableData.data1=this.tables;
                },
                deep:true//对象内部的属性监听，也叫深度监听
            },
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    .detail-msg-wrapper
        width: 100%
        height: 100%
        .top-msg
            background-color: #fff
        .detail-date
            height: 16px
            box-sizing content-box
            padding: 10px

        .detail-msg
            padding: 5px 10px 10px
            .fw
                /*font-size: 30px*/
                color $color-orange


        .detail-chart
            margin-top: 10px
            background-color: #fff!important
            height: 155px

    .mu-text-field
        width: 100%
    .mu-text-field-content
        padding: 0
    .mu-text-field
        margin: 0
        line-height: 1
        min-height 100%
    .top-date
        display: inline-block
        vertical-align: top
        width: 125px
        text-align: center
        background-color #e5e5e5
        /*height: 170%*/
        font-size: 0
    .date-i
        width: 100%
        height: 100%
        color #787777
        font-size: 12px
        text-align: center
        line-height: 1

    .mu-text-field-hint
        font-size:12px;
        line-height 14px
</style>



