<template>
    <div class="top-wrapper">
        <div class="top-left">
            <div class="row">
                <mu-date-picker class="top-date"
                                :fullWidth="false"
                                :underlineShow="false"
                                v-model="date"
                                :maxDate="new Date()"
                                format="YYYY-MM-DD"
                                inputClass="date-i"
                                labelClass="top-date"
                                hintTextClass="top-text"
                                :hintText="dateText"
                                @change="change">
                </mu-date-picker>
                <div class="yl-wrapper">
                    <div class="top-yl" @click="open('bottom')">
                        {{cTypeText}}
                    </div>
                </div>
            </div>
            <div class="top-money">
                <span class="unit">￥</span>
                <span>{{moneytotal}}</span>
            </div>
            <div class="count">
                <span>总收入￥{{money.incomeMoney}}</span>
                <span>总支出￥{{money.expenditrueMoney}}</span>
            </div>
            <div class="company">{{name}}</div>
        </div>
        <div class="top-right">
            <div class="manage">
                <i class="icon-yinhangqia"></i>
                <div class="manage-btn" @click="toPurse">账户管理</div>
            </div>
            <div class="card-list">
                <ul>
                    <li v-for="item in cardList" :key="item.id">{{item.name}}（{{item.account.substr(item.account.length-4)}}）</li>
                </ul>
            </div>
        </div>
        <!---->
        <mu-popup position="bottom" popupClass="popup1" :open="bottomPopup" @close="close('bottom')">
            <mu-picker :slots="cTypeSlots" @change="cTypeChange" :values="cType"/>
        </mu-popup>
    </div>
</template>

<script>
    import { mapMutations } from 'vuex';
    import {API_INCOM_TYPE,API_MONEY_SEC,API_ACCOUNT_LIST} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';

    let getT = () => {
        let date = new Date();
            var month;var num;var date2;var num2;
            if(date.getMonth()+1<10&&date.getDate()>9){
                num=date.getMonth()+1;
                month='0'+num;
                return `${date.getFullYear()}-${month}-${date.getDate()}`;
            }else if(date.getMonth()+1>=10&&date.getDate()<10){
                num2=date.getDate();
                date2='0'+num2;
                return `${date.getFullYear()}-${date.getMonth()+1}-${date2}`;
            }
            else if(date.getMonth()+1<10&&date.getDate()<10){
                num=date.getMonth()+1;
                month='0'+num;
                num2=date.getDate();
                date2='0'+num2;
                return `${date.getFullYear()}-${month}-${date2}`;
            }else if(date.getMonth()+1>=10&&date.getDate()>9){
                return `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
            }
    };
	export default {
	    data() {
	        return {
                bottomPopup: false,
	            date: '',
                dateText: getT(),
                money:{},
                time:'',
                // incomeMoney:'',
                name:'',
                TypeList:[],
                TypeId:'',
                moneytotal:'',
                cardList:[],
                cTypeSlots:[
                    {
                        width: '100%',
                        values: []
                    }
                ],
                cType: [],
                cTypeText: '',
                token:'',
                officeid:'',
                T:'',
                ID:''
            }
        },
        activated(){
            this.T = localStorage.getItem("indexT");
            this.name = localStorage.getItem("name");
            this.ID = localStorage.getItem("indexID");
            this.token=this.$login.getToken();
            this._request();
            this._requestAcc();
            if(this.date===''){
                this.date=getT();
            }
            console.log(this.date);
            console.log(this.dateText);
            this.Selectchange(this.date);
            console.log(this.T+'T')
            console.log(this.ID+'IDT')
        },
        created(){
            // this._requestAcc();
            /* console.log(this.time);
             this.Selectchange(this.time);*/
             this.Select()
        },
        methods: {
            cTypeChange(a) {
                this.cType =[a];
                this.cTypeText = a;
                for(var i=0;i<this.TypeList.length;i++){
                    console.log(this.cTypeText);
                    if(this.cTypeText===this.TypeList[i].name){
                       this.TypeId= this.TypeList[i].id;
                    }
                }
                // this.Select();
            },
            change(a,b){
                console.log(a, b);
                this.time=a;
                this.Selectchange(this.time);
            },
            open (position) {
                this[position + 'Popup'] = true;
            },
            close (position) {
                this[position + 'Popup'] = false;
            },
            toPurse() {
                this.$router.push({
                    path: '/purse'
                })
            },
            async _request() {
                /*if (this.T === 'null' || this.ID === 'null') {
                    alert("请到我的-首页展示切换，选择您的账号");
                }else {*/
                let res = await axios.post(API_INCOM_TYPE, qs.stringify({
                    utoken: this.token,
                    indexType: this.T,
                    officeId: this.ID,
                }));
                let data = res.data;
                console.log(data);
                this.TypeList = data.list;
                let arr = [];
                for (var i = 0; i < this.TypeList.length; i++) {
                    arr.push(this.TypeList[i].name);
                }
                this.cTypeSlots[0].values = arr;
            // }
            },

           Select(){
               console.log('金额')
                console.log(this.date);
                axios.post(API_MONEY_SEC,qs.stringify({
                    utoken:this.token,
                    officeId:this.ID,
                    indexType:this.T,
                    startDate:this.dateText,
                    incomeExpenditureId:this.TypeId
                })).then(res=>{
                    console.log(res);
                    this.money=res.data.data;
                    console.log(this.money);
                }).catch(err=>{
                    console.log(err);
                })
            },
            Selectchange(time){
                console.log(time);
                axios.post(API_MONEY_SEC,qs.stringify({
                    utoken:this.token,
                    officeId:this.ID,
                    indexType:this.T,
                    startDate:time,
                    incomeExpenditureId:this.TypeId
                })).then(res=>{
                    console.log(res);
                    this.money=res.data.data;
                    console.log(this.money);
                }).catch(err=>{
                    console.log(err);
                })
            },
            async _requestAcc(){
                let res=await axios.post(API_ACCOUNT_LIST,qs.stringify({
                    utoken:this.token,
                    indexType:'1',
                    categoryId:'2'
                }));
                let data=res.data;
                console.log(data);
                this.moneytotal=data.moneyTotal;
                console.log(this.moneytotal);
                this.cardList=data.list;
            }
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    .top-wrapper
        width: 100%
        height: 125px
        background-color $color-black
        color #fff
        padding:0 15px 18px
        box-sizing border-box
        font-size: 0
        display: flex
        .top-left,.top-right
            display: inline-block
            vertical-align: top
            height: 100%
        .top-left
            flex 1
            font-size: 12px
            padding-right: 20px
            .row
                display: flex
                height: 16px
                margin-bottom: 17px
                .yl-wrapper
                    flex 1
                    text-align: center
                    height: 100%
                .mu-text-field
                    width: 100%
                .mu-text-field-content
                    padding: 0
                .mu-text-field
                    margin: 0
                    line-height: 1
                    min-height 100%
                .top-text
                    width: 100%
                    height: 100%
                    color #fff
                    line-height: 16px
                    font-size: 12px
                    text-align: center
                .date-i
                    width: 100%
                    height: 100%
                    color #fff
                    font-size: 12px
                    text-align: center
                    line-height: 1
                .top-date
                    display: inline-block
                    vertical-align: top
                    width: 45%
                    text-align: center
                    background-color #2f2d2b
                    height: 100%
                    font-size: 0
                .top-yl
                    display: inline-block
                    vertical-align: top
                    width:95%
                    height: 100%
                    text-align: center
                    background-color $color-orange
            .top-money
                font-size: 20px
                height: 26px
                line-height: 1
                margin-bottom: 7px
                span
                    font-size: 18px
                .unit
                    font-size: 18px
            .count
                height: 16px
                font-size: 0
                margin-bottom: 5px
                span
                    display: inline-block
                    width: 50%
                    font-size: 12px
                    overflow: hidden
                    text-overflow ellipsis
                    white-space nowrap
            .company
                overflow: hidden
                text-overflow ellipsis
                white-space nowrap
        .top-right
            flex 0 0 116px
            padding-left: 15px
            box-sizing border-box
            border-left: 1px solid #1b1b1b
            .manage
                height: 16px
                font-size: 0
                margin-bottom: 24px
                i
                    line-height: 1
                    margin-right: 16px
                .manage-btn
                    display: inline-block
                    vertical-align: top
                    width: 60px
                    height: 100%
                    font-size: 12px
                    text-align: center
                    background-color $color-orange
            .card-list
                width: 100%
                font-size: 12px
                overflow hidden
                height: 85px;
                li
                    margin-bottom: 6px
    .popup1
          width: 100%

</style>



