<template>
    <div class="write-wrapper">
        <v-header path="/" title="记一笔"></v-header>
        <div class="write-tabs">
            <router-link tag="div" to="/account/writeone/income">
                <div class="icon-revenue"></div>
                <span>收入</span>
            </router-link>
            <router-link tag="div" to="/account/writeone/pay">
                <div class="icon-zhichu"></div>
                <span>支出</span>
            </router-link>
            <router-link tag="div" to="/account/writeone/sincome">
                <div class="icon-yingshoukuan"></div>
                <span>应收</span>
            </router-link>
            <router-link tag="div" to="/account/writeone/spay">
                <div class="icon-yingfuguanli"></div>
                <span>应付</span>
            </router-link>
        </div>
        <div class="write-sub">
            <router-view></router-view>
        </div>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';

	export default {
	    data() {
	        return {
                activeTab: null
            }
        },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"
    .write-wrapper
        position: fixed
        top: 0
        right: 0
        bottom: -55px
        left: 0
        z-index: 1000
        background-color $color-bg
        .w
            /*background-color red*/
            color #000
        .write-tabs
            width: 100%
            height: 50px
            display: flex
            background-color #fff
            >div
                padding-top: 1px
                box-sizing border-box
                flex 1
                color #707070
                text-align: center
                &.router-link-active
                    color $color-orange
                    &:after
                        border-b($color-orange)
        .write-sub
            margin-top: 5px
</style>



