<template>
    <div class="infodetail-wrapper">
        <v-header :path="fromUrl" title="个人详情"></v-header>
        <scroller style="position: relative ;z-index: 2;">
            <img-upload title="修改头像" style="margin-top: 10px;" ref="SrcBase"></img-upload>

            <mt-field label="姓名" placeholder="请输入姓名" v-model="title.name"></mt-field>
            <!-- <mt-cell title="头像">
                    <img slot="icon" src="http://img3.imgtn.bdimg.com/it/u=398630285,4280237420&fm=11&gp=0.jpg" width="24" height="24" style="float: right">
            </mt-cell> -->
            <mt-field label="登录名称" placeholder="请输入登录名称" v-model="title.loginName"></mt-field>
            <mt-field label="邮箱" placeholder="请输入邮箱" v-model="title.email"></mt-field>
            <mt-field label="手机号" placeholder="请输入手机号" v-model="title.phone"></mt-field>

            <mt-field label="用户级别" readonly placeholder="" v-model="title.memberGraed "></mt-field>
            <mt-field label="用户类型" readonly placeholder="" v-model="title.userType"></mt-field>
            <mt-field label="所属部门" readonly placeholder="" v-model="title.officeName"></mt-field>
            <mt-field label="所属公司" readonly placeholder="" v-model="title.companyName"></mt-field>
            <mt-field label="故乡" placeholder="请输入故乡" v-model="title.hometown"></mt-field>

            <mt-cell title="是否允许登陆" :value="leaveValueflag" @click.native="popupVisibleflag=true" is-link></mt-cell>

            <mt-cell title="血型" :value="leaveValue" @click.native="popupVisible=true" is-link></mt-cell>

            <mt-cell title="最高学历" :value="leaveValueDep" @click.native="popupVisibleDep=true" is-link></mt-cell>

            <mt-field label="工作" placeholder="工作" v-model="title.job"></mt-field>
            <mt-field label="职业" placeholder="职业" v-model="title.profession"></mt-field>
            <mt-field label="微信oppenId" readonly placeholder="" v-model="title.wechatId"></mt-field>
            <mt-field label="微信昵称" readonly placeholder="" v-model="title.wechatName"></mt-field>
            <mt-field label="" placeholder=""></mt-field>
            <mt-field label="" placeholder=""></mt-field>
        </scroller>
        <mt-popup
            v-model="popupVisible"
            position="bottom">
            <mu-picker :slots="slots" :visible-item-count="3" @change="onValuesChange" :values="values"/>
        </mt-popup>

        <mt-popup
            v-model="popupVisibleDep"
            position="bottom">
            <mu-picker :slots="slotsDep" :visible-item-count="3" @change="onValuesChangeDep" :values="valuesDep"/>
        </mt-popup>

        <mt-popup
            v-model="popupVisibleflag"
            position="bottom">
            <mu-picker :slots="slotsflag" :visible-item-count="3" @change="onValuesChangeflag" :values="valuesflag"/>
        </mt-popup>

        <div class="sub" @click="Sub">保存</div>
        </div>

    <!--</div>-->

</template>
<script>
    import VHeader from 'components/header/header';
    import {API_DICT_TYPE,API_MY_INFO} from 'common/js/api';
    import qs from 'qs';
    import { Toast } from 'mint-ui';
    import axios from 'axios';
    import ImgUpload from 'components/img-upload/img-upload';

    export default {
        data() {
            return {
                fromUrl:'',
                title:{},
                imglist:'',

                bloodid:'',
                users:[],
                popupVisible: false,
                leaveValue:'请选择',
                values: [],
                slots: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],

                educationNameid:'',
                deus:[],
                popupVisibleDep: false,
                leaveValueDep:'请选择',
                valuesDep: [],
                slotsDep: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],

                loginFlag:'',
                popupVisibleflag: false,
                leaveValueflag:'请选择',
                valuesflag: [],
                slotsflag: [
                    {
                        flex: 1,
                        values: ['是','否'],
                        textAlign: 'center'
                    }
                ],
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        computed: {},
        components: {VHeader,ImgUpload},
        created(){
            this._request();
            this._requestdeu();
            this._requestInfo();
        },
        methods: {
            onValuesChange(a) {
                this.leaveValue = a;
                this.values = [a];
                for(var i=0;i<this.users.length;i++){
                    if(this.values[0]===this.users[i].name){
                        this.bloodid =this.users[i].id
                    }
                }
                console.log(this.bloodid);
            },
            onValuesChangeDep(c) {
                this.leaveValueDep = c;
                this.valuesDep = [c];
                for(var i=0;i<this.deus.length;i++){
                    if(this.valuesDep[0]===this.deus[i].name ){
                        this.educationNameid=this.deus[i].id;
                    }
                }
                console.log(this.educationNameid);
            },

            onValuesChangeflag(d) {
                this.leaveValueflag = d;
                this.valuesflag = [d];
                console.log(this.valuesflag);
            },
            //血
            async _request(){
                let res = await axios.post(API_DICT_TYPE, qs.stringify({
                    utoken:this.$login.getToken() ,
                    dictType: 'sys_user_blood_type'
                }));
                let data = res.data;
                console.log(data);
                this.users=data.list;
                let arr=[];
                for(let i=0;i<this.users.length;i++){
                    arr.push(this.users[i].name)
                }
                this.slots[0].values=arr;
            },
            //学历
            async _requestdeu(){
                let res = await axios.post(API_DICT_TYPE, qs.stringify({
                    utoken:this.$login.getToken() ,
                    dictType: 'education_type'
                }));
                let data = res.data;
                console.log(data);
                this.deus=data.list;
                let arr=[];
                for(let i=0;i<this.deus.length;i++){
                    arr.push(this.deus[i].name)
                }
                this.slotsDep[0].values=arr;
            },
        //    获取信息
            async _requestInfo(){
                    let res=await axios.post(API_MY_INFO,qs.stringify({
                        utoken:this.$login.getToken(),
                    }));
                    let data=res.data;
                console.log(data);
                this.title=data.data;
                if(this.title.bloodName===''){
                    this.leaveValue= '请选择';
                }else{
                    this.leaveValue=this.title.bloodName;
                }
                if(this.title.educationName===''){
                    this.leaveValueDep='请选择';
                }else {
                    this.leaveValueDep=this.title.educationName;
                }
                if(this.title.loginFlag==='0'){
                    this.leaveValueflag='否';
                }else if(this.title.loginFlag==='1'){
                    this.leaveValueflag='是';
                }else if(this.title.loginFlag===''){
                    this.leaveValueflag='请选择';
                }
            },
        //提交
            Sub(){
                this.loginFlag='1'
                if(this.valuesflag[1]==='是'){
                   this.loginFlag='1';
                }else if(this.valuesflag[0]==='否'){
                    this.loginFlag='0';
                }
                console.log(this.loginFlag)
                // console.log(this.$refs.SrcBase);
                this.imglist=this.$refs.SrcBase.$data.imgArr;
                var file=[];
                for(var i=0;i<this.imglist.length;i++){
                    file.push(this.imglist[i])
                }
                console.log(file);
                axios.post(API_MY_INFO,qs.stringify({
                    utoken:this.$login.getToken(),
                    name:this.title.name,
                    loginName:this.title.loginName,
                    email:this.title.email,
                    phone:this.title.phone,
                    mobile:this.title.mobile,
                    loginFlag: this.loginFlag,
                    headpic:file[0],
                    character:this.title.character ,
                    hometown:this.title.hometown ,
                    blood:this.bloodid,
                    education:this.educationNameid,
                    job:this.title.job ,
                    profession:this.title.profession
                })).then(res=>{
                    console.log(data);
                    let data=res.data;
                    if(data.result===1){
                        window.localStorage.clear();
                        this.$router.push('/login');
                        Toast({
                            message: '用户已失效,请重新登陆',
                            duration: 2000
                        });
                    }else if(data.result===0){
                        this.$router.go(-1);
                        Toast({
                            message: '修改资料成功',
                            duration: 2000
                        });
                    }
                }).catch(err=>{
                    console.log(err);
                })
            }
        }
    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"
    .infodetail-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
    .sub
        color #ffffff
        position absolute
        top:15px
        right 10px
        z-index 99
    a
        .mint-cell-wrapper
            padding 0

</style>



