const state = {
    msg: ''
};

const getters = {
    getmoneyTotal: state => state.msg
};

const mutations = {
    ['MONEY_TOTAL'](state, msg) {
        state.msg = msg;
        console.log(state.msg);
    }
};

export default {
    state,
    getters,
    mutations
}



