<template>
    <div class="con-my">
        <scroller>
            <div class="group" v-for="(item,index) in friendList" :key="index" @click="ToTalk(item.restName)">
                <img
                    src="https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=497686851,1173179120&fm=27&gp=0.jpg">
                <p>{{item.name}}</p>
            </div>
        </scroller>
    </div>
</template>

<script>
    import {API_FRIEND_LIST} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import {mapMutations} from 'vuex'

    export default {
        data() {
            return {
                friendList: [],
                id: ''
            }
        },

        created: function () {
            let token = this.$login.getToken();
            this._request(token);
        },
        methods: {
            async _request(token) {
                let res = await axios.post(API_FRIEND_LIST, qs.stringify({
                    utoken: token,
                    targetType: 'users'
                }));
                let data = res.data;
                // console.log(data);
                this.friendList = data.list;
                this.setFriendList(data.list)
            },
            ToTalk(id) {
                this.$router.push({
                    path: `talk/${id}`
                })
            },
            ...mapMutations({
                'setFriendList': "SET_FRIEND_LIST"
            })
        },
    }
</script>

<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"
    .con-my
        position absolute
        top 90px
        left 0
        right 0
        bottom 0
        padding-bottom 50px

    .group
        z-index 2
        height 66px
        width 100%
        border-bottom 1px solid #CCCCCC
        background-color #FFFFFF
        padding 8px 5px
        display flex
        align-items center
        img
            width 50px
            height 50px
            border-radius 5px
        p
            font-size 16px
            color #000000
            margin-left 10px
</style>



