<template>
    <div class="wrapper">
        <v-header @rightPop="rightPop" :zm="isLogin" title="账目"></v-header>
        <div v-if="isLogin">
            <top-msg></top-msg>
            <charts :chartArr="chartArr" class="charts-w"></charts>
            <Bill :billArr="billArr" class="bill"></Bill>
            <!-- 功能弹窗 -->
            <mu-popup position="right" popupClass="demo-popup-right" :open="rightPopup" @close="close">
                <div class="Notepad">
                    记事本
                </div>
                <span class="new" @click="add">
                    +新建
                </span>
                <div style="height: 568px; width:100%; overflow: auto" class="lists">

                <div class="text" v-for="item in holdlist" :key="item.id">
                    <textarea name="a" cols="22" placeholder="请输入" v-model="item.content"></textarea>
                    <span class="delete" @click="del($event,item.id)">删除</span>
                    <span class="hold" @click="hold(item.id)">保存</span>
                </div>
                </div>
                <!--<mu-raised-button label="关闭弹框" @click="close" primary fullWidth/>-->
            </mu-popup>
        </div>
        <not-login v-else></not-login>
        <transition name="slide">
            <router-view></router-view>
        </transition>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import TopMsg from './comp/top-msg/top-msg';
    import Charts from './comp/charts/chars';
    import Bill from './comp/bill/bill';
    import NotLogin from 'components/not-login/not-login';
    import axios from 'axios';
    import qs from 'qs';
    import { API_GET_BILL ,API_NOTE_LITE,API_ADD_NOTE,API_DEL_NODE,API_INDEX_LIST} from 'common/js/api';
    import { mapMutations } from 'vuex';
    import { Indicator, Toast } from 'mint-ui';


    export default {
	    data() {
	        return {
	            rightPopup: false,
                isLogin: false,
                billArr: [],
                chartArr: [],
                things:'',
                content:'',
                holdlist:[],
                val:'',
                indexType:'1',
                officeId:''
            }
        },
        created() {
            // this.getPerson()
	        this.isLogin = this.$login.isLogin();
            var T = localStorage.getItem("indexT");
            var ID =  localStorage.getItem("indexID");
            console.log(this.indexType);
            console.log(ID);
            this.officeId = ID
            if(T==1){
                this.indexType = 2
            }else {
                this.indexType = 1
            }
            console.log(this.indexType)
            this._NoteList();
            var u = navigator.userAgent;
            // android
            window['aNdroidaCcount']=()=>{
                window.location.reload()
                this.$router.push({
                    path: `/account`
                })
            }
            window['aNdroidtEam']=()=>{
                this.$router.push({
                    path: `/team`
                })
            }
            window['aNdroidiNdex']=()=>{
                this.$router.push({
                    path: `/index`
                })
            }
            window['aNdroidmIne']=()=>{
                this.$router.push({
                    path: `/mine`
                })
            }
            window['aNdroidClear']=()=>{
                window.localStorage.clear();
            }
            // iOS
            window['iOSReload']=()=>{
                window.location.reload()
                this.$router.push({
                    path: `/account`
                })
            }
            window['iOSaCcount']=()=>{
                this.$router.push({
                    path: `/account`
                })
            }
            window['iOStEam']=()=>{
                this.$router.push({
                    path: `/team`
                })
            }
            window['iOSiNdex']=()=>{
                this.$router.push({
                    path: `/index`
                })
            }
            window['iOSmIne']=()=>{
                this.$router.push({
                    path: `/mine`
                })
            }
            window['iOSClear']=()=>{
                window.localStorage.clear();
            }
        },
        activated() {
            this.isLogin = this.$login.isLogin();
            
        },
        beforeRouteEnter (to, from, next) {
            next( vm => {
                vm._getAllBill();
            });
        },
	    methods: {
	        async _getAllBill() {
                Indicator.open();
	            let res = await axios.post(API_GET_BILL, qs.stringify({
                    utoken: this.$login.getToken(),
                    officeId: this.officeId,
                    indexType: this.indexType
                }));
                Indicator.close();
                let data = res.data;
	            console.log(data);
	            if(data.result === 2) {
	                window.localStorage.clear();
	                this.isLogin = false;
                }else if(data.result === 0) {
                    this.setBillList(data.list);
                    this.billArr = [];
                    this.chartArr = data.list;
                    let count = data.list.length>=4 ? 4 : data.list.length;
	                for(let i = 0; i < count; i++) {
	                    this.billArr.push(data.list[i]);
                    }
                }
            },
            rightPop() {
                this.rightPopup = true;
                this._NoteList();
            },
            close() {
                this.rightPopup = false;
                this._NoteList();
            },
            goWrite() {
                this.$router.push({
                    path: '/account/writeone'
                })
            },
            ...mapMutations({
                setBillList: 'SET_BILL_LIST'
            }),
            hold(id){
                for(let i=0;i<this.holdlist.length;i++){
                    if(id===this.holdlist[i].id){
                        this.val=this.holdlist[i].content;
                    }
                }
                console.log(this.val);
                // 添加修改记事本
                axios.post(API_ADD_NOTE,qs.stringify({
                    utoken:this.$login.getToken(),
                    noteId:id,
                    remarks:this.val
                })).then(res=>{
                    console.log(res);
                    let data=res.data;
                    if(data.result===0){
                        Toast({
                            message: '保存成功',
                            duration: 2000
                        });
                        this._NoteList();
                    }else if(data.result===2){
                        Toast({
                            message: '用户已失效，请重新登陆',
                            duration: 2000
                        });
                        window.localStorage.clear();
                        this.isLogin = false;
                    }
                }).catch(err=>{
                    console.log(err);
                })
            },
            del(ev,id){
	            var event=ev||event;
                console.log(event.currentTarget);
                var parent=event.currentTarget.parentNode;
                console.log(parent);
                parent.style.display='none';
                // 删除记事本
                axios.post(API_DEL_NODE,qs.stringify({
                    utoken:this.$login.getToken(),
                    noteId:id
                })).then(res=>{
                    let data=res.data;
                    if(data.result===0){
                        Toast({
                            message: '删除成功',
                            duration: 2000
                        });
                    }else if(data.result===2){
                        Toast({
                            message: '用户已失效，请重新登陆',
                            duration: 2000
                        });
                        window.localStorage.clear();
                        this.isLogin = false;
                    }
                    console.log(res);

                }).catch(err=>{
                    console.log(err);
                })
            },
            add(){
                this.holdlist.push({
                    id:'',
                    content:''
                });
            },
            async _NoteList(){
                // 获取记事本列表
	            let res=await axios.post(API_NOTE_LITE,qs.stringify({
                    utoken:this.$login.getToken()
                }));
                let data=res.data;
                console.log(data);
                // this.holdlist=data.list;
                let arr=[];
                for(let i=0;i<data.list.length;i++){
                    arr.push({
                        content:data.list[i].remarks,
                        id:data.list[i].id
                    })
                }
                this.holdlist=arr;
            },
            setColor(color, text) {
                console.log(color);
                console.log(text);
            }
        },
        components: {
            VHeader,
            TopMsg,
            Charts,
            Bill,
            NotLogin
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .demo-popup-right
        /*display: flex;*/
        height: 100%;
        max-width: 375px;
        /*align-items: center;*/
        padding: 24px;
        width: 198px
        background-color: #2f2d2b
        color:#FFF
        .Notepad
            position relative
            top 0
            left 0
            font-size 20px
            height 30px
            width 100%
            disply block
        .new
            position absolute
            top 10px
            right 10px
            font-size 12px
            height 25px
        .lists::-webkit-scrollbar
            display: none;
        .text
            margin-top 10px
            .delete
                display inline-block
                width 40px
                height 20px
                text-align center
                border-radius 4px
                background-color red
                margin-left 7%
            .hold
                display inline-block
                width 40px
                height 20px
                text-align center
                border-radius 4px
                background-color $color-orange
                margin-left 30%

</style>



