<template>
    <div>
        <enter-money msg="" @on-accountChange="accountHandler" @on-input="inputHandler"></enter-money>
        <div class="type">
            <div class="title border-1px">分类</div>
            <div class="content">
                <ul class="u">
                    <li v-for="(value,index) in typeArr" :key="index" :class="{on:index == b}" @click="cOne(index,value.id)">
                        <div class="icon-zhangmuguanli item"></div>
                        <span>{{value.name}}</span>
                    </li>
                    <li style="position: static;"  class="add">
                        <div class="icon-jia item"></div>
                    </li>
                </ul>
            </div>
        </div>
        <div class="btn" @click="subHandler">提交</div>
    </div>
</template>

<script>
    import EnterMoney from 'components/enter-money/enter-money';
    import axios from 'axios';
    import qs from 'qs';
    import { API_WRITE_ONE ,API_EXPEND_TYPE } from 'common/js/api';
    import writeOne from 'common/js/writeOne';

    export default {
        data() {
            return {
                i: '',
                b: 0,
                typeArr: [],
                t:''
            }
        },
        async created() {
            let res = await axios.post(API_EXPEND_TYPE, qs.stringify({
                utoken: this.$login.getToken()
            }));
            this.typeArr = res.data.list;
        },
        methods: {
            subHandler() {
                let date = new Date();
                let json = {
                    utoken: this.$login.getToken(),
                    noteType: 4,
                    indexType: 1,
                    money: this.money,
                    bxStatus: this.value ? 1 : 0,
                    sourceId: this.i,
                    t:this.t,
                    accountId: this.nowAccount.id,
                    useTime: `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()} ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`
                };
                console.log(json);
               writeOne(API_WRITE_ONE, json, this)
            },
            accountHandler(a) {
                this.nowAccount = a
                this.t = a.t;
            },
            inputHandler(value) {
                this.money = value;
            },
            cOne(index, value) {
                this.b = index;
                this.i = value;
            }
        },
        components: {
            EnterMoney
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    .type
        width: 100%
        margin-top: 5px
        padding: 0 10px
        background-color #fff
        .title
            width: 100%
            height: 30px
            border-b(#ccc)
            font-size: 12px
            line-height:30px
            color #8e8d8d
        .content
            box-sizing border-box
            padding-top: 20px
            .u
                display: flex
                flex-wrap wrap
                li
                    width: 25%
                    text-align: center
                    margin-bottom: 25px
                    &.on
                        span
                            color $color-orange
                        .item
                            background-color: $color-orange
                            color #fff
                    span
                        color #919191
                        font-size: 12px
                    .item
                        margin: 0 auto
                        width: 35px
                        height: 35px
                        border-radius 5px
                        text-align: center
                        line-height: 35px
                        color #bbb
                        background-color $color-gray
    .btn
        width: 100%
        height: 42px
        position: absolute
        left: 0
        bottom: 55px
        line-height: 42px
        color #ffffff
        text-align: center
        background-color $color-orange
</style>



