<template>
    <div class="range-wrapper">
        <v-header :path="fromUrl" title="发布范围"></v-header>

        <!--成功之后跳到team-->
        <span @click='ToTeam' class="cb">发布</span>
    </div>
</template>
<script>
    import VHeader from 'components/header/header';

    export default {
        data() {
            return {
                fromUrl:''
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        computed: {},
        components: {VHeader},
        methods: {
            ToTeam:function () {
                this.$router.push('/team')
            }
        }
    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"
    .range-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        z-index 3333666
        .cb
            width: 80px
            padding: 0 5px
            text-align: center
            color #fff
            position: absolute
            right: 0
            top: 0
            line-height: 50px

</style>



