<template>
    <div class="leave-wrapper">
        <v-header :path="fromUrl" title="申请"></v-header>
        <scroller style="margin-top: 50px">
        <mt-field label="标题" placeholder="请输入标题" v-model="title"></mt-field>
        <mt-cell title="申请类型" :value="leaveValue" @click.native="popupVisible=true" is-link></mt-cell>

        <mt-cell title="流程名称" :value="leaveValuePro" @click.native="popupVisiblePro=true" is-link></mt-cell>

        <mt-cell title="部门名称" :value="leaveValueDep" @click.native="popupVisibleDep=true" is-link></mt-cell>

        <mt-cell title="开始时间" :value="startTime" isLink @click.native="showDatePicker(true)"></mt-cell>
        <mt-cell title="结束时间" :value="endTime" isLink @click.native="showDatePicker(false)"></mt-cell>
        <div class="textarea">
            <mt-field label="申请事由" placeholder="请输入申请事由" type="textarea" rows="4" v-model="things"></mt-field>
        </div>
        <img-upload title="上传凭证" style="margin-top: 10px;" ref="SrcBase"></img-upload>
        </scroller>
        <div class="leave-submit" @click="Sub">提交</div>

        <mt-popup
            v-model="popupVisible"
            position="bottom">
            <mu-picker :slots="slots" :visible-item-count="3" @change="onValuesChange" :values="values"/>
        </mt-popup>

        <mt-popup
            v-model="popupVisiblePro"
            position="bottom">
            <mu-picker :slots="slotsPro" :visible-item-count="3" @change="onValuesChangePro" :values="valuesPro"/>
        </mt-popup>

        <mt-popup
            v-model="popupVisibleDep"
            position="bottom">
            <mu-picker :slots="slotsDep" :visible-item-count="3" @change="onValuesChangeDep" :values="valuesDep"/>
        </mt-popup>

        <mt-datetime-picker
            ref="picker"
            type="date"
            @confirm="confirmHandler"
            v-model="pickerValue">
        </mt-datetime-picker>


    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import ImgUpload from 'components/img-upload/img-upload';
    import {API_APPLY_TYPE,API_ALL_DEP,API_ADD_APPLY} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Indicator, Toast } from 'mint-ui';

    export default {
	    data() {
	        return {
                title:'',
	            things: '',
	            bOk:null,
	            startTime: '请选择开始时间(必填)',
                endTime: '请选择结束时间(必填)',
                pickerValue: new Date(),
                popupVisible: false,
                leaveValue:'请选择（必选）',
                values: [],
                token:'',
                officeId:'',
                imglist:[],

                applyTypeId:'',
                applyType:[],
	            slots: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],

                processId:'',
                popupVisiblePro: false,
                leaveValuePro:'请选择（必选）',
                valuesPro: [],
                slotsPro: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],

                offeId:[],
                officeid3:'',
                popupVisibleDep: false,
                leaveValueDep:'请选择（必选）',
                valuesDep: [],
                slotsDep: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],
                fromUrl: ''
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        created(){
	        this.token=this.$login.getToken();
	        this.officeId=this.$route.params.id;
            this._request();
            this._requestDep();
        },
	    methods: {
            onValuesChange(a) {
                this.leaveValue = a;
                this.values = [a];
                for(var i=0;i<this.applyType.length;i++){
                    if(this.values[0]===this.applyType[i].name){
                        this.applyTypeId=this.applyType[i].id
                    }
                }
                console.log(this.applyTypeId);
            },
            onValuesChangePro(b) {
                this.leaveValuePro = b;
                this.valuesPro = [b];

                for(var i=0;i<this.applyType.length;i++){
                    if(this.valuesPro[0]===this.applyType[i].processName ){
                        this.processId=this.applyType[i].processId;
                    }
                }
                console.log(this.processId);
            },
            onValuesChangeDep(c) {
                this.leaveValueDep = c;
                this.valuesDep = [c];

                for(var i=0;i<this.offeId.length;i++){
                    if(this.valuesDep[0]===this.offeId[i].name ){
                        this.officeid3=this.offeId[i].id;
                    }
                }
                console.log(this.officeid3);
            },
            showDatePicker(bOk) {
                this.$refs.picker.open();
                this.bOk = bOk
            },
            _dateFormat(date) {
                var month;var num;var date2;var num2;
                if(date.getMonth()+1<10&&date.getDate()>9){
                    num=date.getMonth()+1;
                    month='0'+num;
                    return `${date.getFullYear()}-${month}-${date.getDate()}`;
                }else if(date.getMonth()+1>=10&&date.getDate()<10){
                    num2=date.getDate();
                    date2='0'+num2;
                    return `${date.getFullYear()}-${date.getMonth()+1}-${date2}`;
                }
                else if(date.getMonth()+1<10&&date.getDate()<10){
                    num=date.getMonth()+1;
                    month='0'+num;
                    num2=date.getDate();
                    date2='0'+num2;
                    return `${date.getFullYear()}-${month}-${date2}`;
                }else if(date.getMonth()+1>=10&&date.getDate()>9){
                    return `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
                }
                // return `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
            },
            confirmHandler(value) {
                if(this.bOk) {
                    this.startTime = this._dateFormat(value);
                    console.log(this.startTime);
                }else {
                    this.endTime = this._dateFormat(value);
                    console.log(this.endTime);
                }
            },
            async _request(){
                let res=await axios.post(API_APPLY_TYPE,qs.stringify({
                    utoken:this.token,
                    officeId:this.officeId
                }));
                let data=res.data;
                console.log(data);
                this.applyType=data.list;
                let arr=[];
                let arr2=[];
                for(var i=0;i<this.applyType.length;i++){
                    arr.push(this.applyType[i].name);
                    arr2.push(this.applyType[i].processName)
                }
                this.slots[0].values=arr;
                this.slotsPro[0].values=arr2;

            },
            async _requestDep(){
                let res=await axios.post(API_ALL_DEP,qs.stringify({
                    utoken:this.token,
                    type:'1',
                    officeId:this.officeId
                }));
                let data=res.data;
                console.log(data);
                let arr=[];
                this.offeId=data.list;
                for(var i=0;i<this.offeId.length;i++){
                    arr.push(this.offeId[i].name);
                }
                this.slotsDep[0].values=arr;

            },
            Sub(){
                this.imglist=this.$refs.SrcBase.$data.imgArr;
                var file=[];
                for(var i=0;i<this.imglist.length;i++){
                    file.push(this.imglist[i])
                }
                console.log(file);
                if(this.officeId===''||this.officeid3===''||this.title===''||this.applyTypeId===''||this.processId===''||this.startTime===''||this.endTime===''||this.things===''||file===''){
                    alert('请补充完整信息');
                }else {
                axios.post(API_ADD_APPLY,qs.stringify({
                    utoken:this.token,
                    companyId:this.officeId,
                    officeId:this.officeid3,
                    title:this.title,
                    applyTypeId:this.applyTypeId,
                    processId:this.processId,
                    startDate:this.startTime,
                    endDate:this.endTime,
                    applyReason:this.things,
                    voucherPath:file
                })).then(res=>{
                    let data=res.data;
                    if(data.result===0){
                        Toast({
                            message:'已提交申请',
                            duration: 1000
                        });
                        console.log(res.data);
                        this.$router.go(-1);
                    }else if(data.result===2){
                        window.localStorage.clear();
                        this.$router.push('/login');
                        Toast({
                            message: '用户已失效，请重新登陆',
                            duration: 2000
                        });
                    }
                }).catch(err=>{
                    console.log(err);
                })
                }
            }

        },
	    components: {
	        VHeader,
            ImgUpload
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .leave-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        .mint-popup
            z-index: 10000
        .mint-popup-bottom
            width: 100%
        .textarea
            .mint-cell-wrapper
                align-items flex-start
        .leave-submit
            width: 100%
            height: 50px
            font-size: 16px
            text-align: center
            line-height: 50px
            position: absolute
            left: 0
            bottom: 0
            color #fff
            background-color: $color-orange
</style>



