<template>
    <div class="mine-wrapper">
        <v-header title="个人中心"></v-header>
        <div class="mine-msg">
            <router-link v-if="isLogin" tag="div" append to="person" class="msg-wrap">
                <mu-avatar :size="80"
                           :src="avatarPath?avatarPath:defaultAvatarPath"/>
                <div class="name">{{username}}</div>
            </router-link>
            <router-link tag="div" append to="/login" v-else class="msg-wrap">
                <mu-avatar :size="80"
                           :src="defaultAvatarPath"/>
                <div class="name">未登录</div>
            </router-link>
        </div>
        <div class="tab-wrap">
            <mt-cell class="border-1px" title="账户" @click.native="goPurse" isLink>
                <img slot="icon" src="./zhanghu.png" width="24" height="24">
            </mt-cell>
            <mt-cell class="border-1px" to="/mine/vip" title="会员" isLink>
                <img slot="icon" src="./huiyuan.png" width="24" height="24">
            </mt-cell>
            <mt-cell class="border-1px" to="/mine/record" title="消费记录" isLink>
                <img slot="icon" src="./xiaofeijilu.png" width="24" height="24">
            </mt-cell>
        </div>
        <div class="tab-wrap">
            <mt-cell class="border-1px" to="/mine/indexdatatab" title="首页展示切换" isLink>
                <img slot="icon" src="./qiehuan.png" width="24" height="24">
            </mt-cell>
            <mt-cell class="border-1px" title="功能管理" to="/app" isLink>
                <img slot="icon" src="./gongnengguanli.png" width="24" height="24">
            </mt-cell>
            <mt-cell class="border-1px" to="/mine/popularize" title="推广" isLink>
                <img slot="icon" src="./tuiguang.png" width="24" height="24">
            </mt-cell>
            <mt-cell class="border-1px" to="/mine/collection" title="我的收藏" isLink>
                <img slot="icon" src="./shoucang.png" width="24" height="24">
            </mt-cell>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import { mapMutations } from 'vuex';
    export default {
        data() {
            return {
                isLogin: false,
                username: '',
                avatarPath: '',
                defaultAvatarPath: 'https://fuss10.elemecdn.com/c/f5/d0b0f2fc83f3ac3e4a0cfae891256png.png?imageMogr/format/webp/thumbnail/!60x60r/gravity/Center/crop/60x60/'
            }
        },
        beforeRouteEnter (to, from, next) {
            next(vm => {
                vm._judgeLogin()
            })
        },
        beforeRouteUpdate (to, from, next) {
            this._judgeLogin();
            next()
        },
        created() {
            this._judgeLogin();
        },
        methods: {
            _judgeLogin() {
                this.isLogin = this.$login.isLogin();
                this.username = this.$login.getUserName();
                this.avatarPath = this.$login.getAvatar();
            },
            goPurse() {
                this.$router.push({
                    path: '/purse'
                })
            }
        },
        components: {
            VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"
    .mine-wrapper
        position: absolute
        left: 0
        right: 0
        top: 0
        bottom: 55px
        background-color $color-bg
        .mine-msg
            width: 100%
            height: 135px
            color #fff
            background linear-gradient(#45413e, #5f5f5f)
            .msg-wrap
                margin: 0 auto
                .mu-avatar
                    margin: 0 auto
            .name
                margin-top: 10px
                font-size: 18px
                font-weight: normal
                text-align: center
            .mu-avatar
                display: block
        .tab-wrap
            margin-top: 10px
            .tab-item
                width: 100%
                height: 48px
                border-b(#ccc)
                background-color #fff
                display: flex
                .iconwrap
                    flex 0 0 26px
                    padding: 0 15px
                    height: 100%
                    display: flex
                    align-items center
                .icon
                    width: 26px
                    height: 26px
                    text-align: center
                    color #fff
                    border-radius 50%
                    background-color: red
            .mint-cell-text
                font-size: 12px
        .mint-cell
            border-b(#ccc)
            &:last-child
                &:after
                    height: 0
</style>



