<template>
    <div class="purse-wrapper">
        <v-header :path="fromUrl" title="账户"></v-header>
        <div class="purse-top">
                    <div class="balance">余额</div>
                    <div class="balance-num">￥{{money}}</div>
                    <div class="app-btn">
                        <div class="recharge" @click="Recharge">充值</div>
                        <div class="extract" @click="Extract">提现</div>
                    </div>
                </div>
        <div class="purse-bot">
            <scroller>
                <div class="content">
                    <router-link tag="div" to="/purse/addcard" class="add-card">
                        <div class="icon-jia icon"></div>
                        <span>添加银行卡</span>
                    </router-link>
                    <div class="card-item" v-for="item in lists">
                        <div class="title">
                            <div class="avatar">
                                <img src="./abc.png" alt="">
                            </div>
                            <div class="msg">
                                <div>{{item.name}}</div>
                                <!--<div>储蓄卡</div>-->
                            </div>
                        </div>
                        <div class="card-num">
                            **** **** **** {{item.account.substr(item.account.length-4)}}
                        </div>
                    </div>
                </div>
            </scroller>
        </div>
        <transition name="slide">
            <router-view></router-view>
        </transition>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import { isLogin } from 'common/js/isLogin';
    import {API_BANK} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Toast, Indicator } from 'mint-ui';
    import {mapMutations} from 'vuex';

	export default {
        data() {
            return {
                fromUrl: '',
                lists:[],
                money:'',
                token:''
            }
        },
        beforeRouteUpdate(to, from, next){
            console.log(from.path);
            let str='/purse/recharge';
            if(from.path===str||from.path==='/purse/extract'||from.path==='/purse/addcard'){
                this._request(this.token);
            }
            next();
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },

        created(){
            let token = this.$login.getToken();
            this.token=token;
            this._request(token);
        },
        methods:{
            async _request(token) {
                let res=await axios.post(API_BANK,qs.stringify({
                    utoken:token,
                    noteType:'',
                    indexType:'1',
                    categoryId:'2'
                }));
                let data=res.data;
                console.log(data);
                this.lists=data.list;
                console.log(this.lists);
                this.setBankCard(data.list);
                this.money=data.moneyTotal;
                this.moneyTotal(data.moneyTotal)
            },
            Recharge(){
                if(this.lists.length<=0){
                    alert('请添加银行卡')
                }else{
                    this.$router.push('/purse/recharge') ;
                }

            },
            Extract(){
                if(this.lists.length<=0){
                    alert('请添加银行卡')
                }else{
                    this.$router.push('/purse/extract') ;
                }
            },
            ...mapMutations({
                'setBankCard':"SET_BANK_CARD",
                'moneyTotal':"MONEY_TOTAL"
            }),

        },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"

    .purse-wrapper
        position: fixed
        left: 0
        top: 0
        right: 0
        bottom: 0
        background-color $color-bg
        z-index: 1000
        .purse-top
            width: 100%
            height: 125px
            text-align: center
            color #fff
            background-color: $color-black
            .balance
                font-size: 12px
            .balance-num
                line-height: 60px
                font-size: 30px
            .app-btn
                font-size: 0
                div
                    width: 55px
                    height: 20px
                    font-size: 12px
                    display: inline-block
                    vertical-align: top
                    background-color $color-orange
                .recharge
                    margin-right: 10px
        .purse-bot
            position: absolute
            top: 185px
            left: 0
            right: 0
            bottom: 0
            background-color #fff
            .content
                padding: 0 15px
                .add-card
                    line-height: 35px
                    color #6a6969
                    .icon
                        width: 20px
                        height: 20px
                        display: inline-block
                        border-radius 50%
                        text-align: center
                        line-height: 20px
                        color #fff
                        background-color #919191
                .card-item
                    width: 100%
                    height: 110px
                    background-color #eee
                    border-radius 8px
                    margin-bottom 20px
                    .title
                        width: 100%
                        padding: 17px 23px 0
                        box-sizing border-box
                        border-radius 50%
                        font-size: 0
                        .msg,.avatar
                            display: inline-block
                            vertical-align: top
                        .avatar
                            width: 35px
                            height: 35px
                            img
                                width: 100%
                                height: 100%
                        .msg
                            margin-left: 10px
                            font-size: 12px
                            color #6a6969
                    .card-num
                        color #6a6969
                        text-align: center
                        margin-top: 7px
                        font-size: 18px
</style>



