<template>
    <div class="search-friend-wrap">
        <v-header path="/message/maillist" title="查找"></v-header>
        <mt-search @input.native="inputHandler" v-model="val">
            <ul id="lists">
            <li class="list" v-for="(item,index) in friend" @click="friendDetail(item.id,item.loginName)" :key="index">
                <span class="name">{{item.name}}</span>
                <span>{{item.loginName}}</span>
            </li>
        </ul>
        </mt-search>


    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import _ from 'lodash';
    import {API_GET_PEOPLE} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { mapGetters } from 'vuex';

    export default {
        data() {
            return {
                t: null,
                token: '',
                user: '',
                val: '',
                friend: []
            }
        },
        computed: {
            ...mapGetters([
                'getFriendList'
            ])
        },

        mounted() {
            let List = document.getElementById('lists');
            console.log(List);
        },

        methods: {
            inputHandler: _.debounce(function () {
                this.token = this.$login.getToken();
                axios.post(API_GET_PEOPLE, qs.stringify({
                    utoken: this.token,
                    target: this.val
                })).then((res) => {
                    let data = res.data;
                    console.log(data.list);
                    this.friend = data.list;
                }).catch((err) => {
                    console.log(err);
                });
            }, 1000),

            friendDetail(id,loginName) {
                console.log(loginName);
                this.$store.dispatch('getFriendList');
                console.log(this.getFriendList);
                let FriendLength=this.getFriendList;
                let arr=[];

                for(let i=0;i<FriendLength.length;i++){
                    console.log(FriendLength[i].Name);
                    arr.push(FriendLength[i].restName);
                }
                console.log(arr);
                if(arr.includes(id)){
                    console.log('等于');
                    console.log(id);
                    this.$router.push({
                        'path':`friend/${id}/${loginName}`
                    })
                }else {
                    console.log('不等于');
                    this.$router.push({
                        'path':`addfriend/${id}/${loginName}`
                    })
                }
            }
        },

        components: {
            VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .search-friend-wrap
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        z-index: 1000
        #lists
            margin-top 52px
        .list
            width 100%
            height 40px
            background-color #FFFFFF
            line-height 40px
            border-bottom 1px solid #CCCCCC
            z-index 9999999999
            .name
                font-size 20px
                color #000000
                margin-left 10px
                margin-right 20px

</style>



