import Mine from 'page/mine/mine';
import Vip from 'page/mine/subpage/vip/vip';
import Record from 'page/mine/subpage/record/record';
import RecordDetail from 'page/mine/subpage/record-detail/record-detail';
import RecordComment from 'page/mine/subpage/comment/comment';
import IndexDataTab from 'page/mine/subpage/index-data-tab/index-data-tab';
import Popularize from 'page/mine/subpage/popularize/popularize';
import PersonMsg from 'page/mine/subpage/person-msg/person-msg';
import Infodetail from 'page/mine/subpage/infodetail/infodetail';
import Cart from 'page/index';
import CartA from 'components/cart/cart';
import Collection from 'page/mine/subpage/collection/collection';

export default {
    path: '/mine',
    component: Mine,
    children: [
        {
            path: 'vip',
            component: Vip,
            meta: {
                requireAuth: true
            }
        },
        {
            path: 'collection',
            component: Collection,
            meta: {
                requireAuth: true
            }
        },
        {
            path: 'record',
            component: Record,
            meta: {
                requireAuth: true
            },
            children: [
                {
                    path: ':id',
                    component: RecordDetail
                },
                {
                    path: `comment/:id`,
                    component: RecordComment
                },
                {
                    path: ':id',
                    component: Infodetail
                },
                {
                    path: 'cart',
                    component: CartA
                },
            ]
        },
        {
            path: 'indexdatatab',
            component: IndexDataTab,
            meta: {
                requireAuth: true
            }
        },
        {
            path: 'popularize',
            component: Popularize,
            meta: {
                requireAuth: true
            }
        },

        {
            path: 'person',
            component: PersonMsg,
            children: [
                {
                    path: 'infodetail',
                    component: Infodetail
                }
            ]
        },


    ]
}



