<template>
    <div class="register-wrapper">
        <v-header path="/login" title="注册"></v-header>
        <div class="form-wrap">
            <Form ref="formTop" :model="formTop" label-position="top" :rules="ruleValidate">
                <FormItem label="用户名" prop="username">
                    <Input v-model="formTop.username"></Input>
                </FormItem>
                <FormItem label="密码" prop="password">
                    <Input v-model="formTop.password"></Input>
                </FormItem>
                <FormItem label="确认密码" prop="confirm_password">
                    <Input v-model="formTop.confirm_password"></Input>
                </FormItem>
                <FormItem>
                    <Button long type="success" @click="handleSubmit('formValidate')">Submit</Button>
                </FormItem>
            </Form>
        </div>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import { API_REGISTER } from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Toast } from 'mint-ui';


    export default {
	    data() {
            const validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('Please enter your password'));
                } else {
                    if (this.formTop.confirm_password !== '') {
                        // 对第二个密码框单独验证
                        this.$refs.formTop.validateField('confirm_password');

                    }
                    callback();
                }
            };
            const validatePassCheck = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('Please enter your password again'));
                } else if (value !== this.formTop.password) {
                    callback(new Error('The two input passwords do not match!'));
                } else {
                    callback();
                }
            };
	        return {
	            formTop: {
                    username: '',
                    password: '',
                    confirm_password: ''
                },
                ruleValidate: {
                    username: [
                        {
                            required: true,
                            message: '请输入用户名',
                            trigger: 'blur'
                        }
                    ],
                    password: [
                        {
                            required: true,
                            message: '请输入密码',
                            trigger: 'blur',
                            validator: validatePass
                        }
                    ],
                    confirm_password: [
                        {
                            required: true,
                            message: '请重新输入密码',
                            trigger: 'blur',
                            validator: validatePassCheck
                        }
                    ]
                }
            }
        },
        methods: {
            async handleSubmit(){
                let user=this.formTop.username;
                let pass=this.formTop.confirm_password;
                let res=await axios.post(API_REGISTER,qs.stringify({
                    password:pass,
                    username:user
                    }));
                let data=res.data;
                console.log(data);
                if(data.result === 0) {
                    window.localStorage.setItem('u_token',JSON.stringify({
                        username: data.username,
                        token: encodeURIComponent(data.utoken),
                        avatar: data.headpic
                    }));
                     this.$router.go(-2);
                }else {
                    Toast({
                        message: data.msg,
                        duration: 2000
                    });
                }
            }
        },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .register-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index: 1000
        background-color: $color-bg
        .form-wrap
            padding: 20px
</style>



