<template>
    <div class="iou-wrapper">
        <v-header :path="fromUrl" title="借条"></v-header>
        <!--<mt-search v-model="searchValue"></mt-search>-->
        <div class="pi-wrapper">
        <scroller>
        <!--<mt-field label="申请人" placeholder="请输入"></mt-field>-->
            <mt-cell title="部门" :value="leaveValue" @click.native="popupVisible=true" is-link></mt-cell>
            <mt-cell title="审批流程" :value="leaveValuePro" @click.native="popupVisiblePro=true" is-link></mt-cell>
            <mt-field label="事由" placeholder="请输入" type="textarea" rows="4" class="bottom" v-model="Reason"></mt-field>
            <mt-field label="申请金额" placeholder="请输入" v-model="money"></mt-field>
            <mt-cell title="使用日期" :value="startTime" isLink @click.native="showDatePicker(true)"></mt-cell>
            <mt-cell title="归还日期" :value="endTime" isLink @click.native="showDatePicker(false)" class="bottom"></mt-cell>
            <mt-field label="出纳" placeholder="请输入" type="textarea" v-model="Cashier"></mt-field>

            <mt-field label="备注" placeholder="请输入" type="textarea" rows="4" v-model="Remarks"></mt-field>
        <!--<mt-field label="审批人" placeholder="请输入"></mt-field>-->
        </scroller>
        </div>
        <div class="purchase-btn" @click="Sub">提交</div>

        <mt-datetime-picker
            ref="picker"
            type="date"
            @confirm="confirmHandler"
            v-model="pickerValue">
        </mt-datetime-picker>

        <mt-popup
            v-model="popupVisible"
            position="bottom">
            <mu-picker :slots="slots" :visible-item-count="5" @change="onValuesChange" :values="values"/>
        </mt-popup>

        <mt-popup
            v-model="popupVisiblePro"
            position="bottom">
            <mu-picker :slots="slotsPro" :visible-item-count="5" @change="onValuesChangePro" :values="valuesPro"/>
        </mt-popup>

    </div>
</template>
<script>
    import VHeader from 'components/header/header';
    import {API_ADD_IOU,API_ALL_DEP,API_APPROVAL_PRO} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Indicator, Toast } from 'mint-ui';

    export default {
        data() {
            return {
                fromUrl:'',
                searchValue: '',
                startTime: '请选择开始时间(必填)',
                endTime: '请选择结束时间(必填)',
                pickerValue: new Date(),
                token:'',
                officeId:'',
                officeId2:'',
                processDefId:'',
                Reason:'',
                money:'',
                Cashier:'',
                Remarks:'',

                Deps:[],
                popupVisible: false,
                leaveValue:'请选择部门（必选）',
                values: [],
                slots: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],

                Pros:[],
                popupVisiblePro: false,
                leaveValuePro:'请选择审批流程（必选）',
                valuesPro: [],
                slotsPro: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        computed: {},
        created(){
            this.token=this.$login.getToken();
            this.officeId=this.$route.params.id;
            this._requestDep();
            this._requestPro();
        },
        components: {VHeader},
        methods: {
            showDatePicker(bOk) {
                this.$refs.picker.open();
                this.bOk = bOk
            },
            _dateFormat(date) {
                var month;var num;var date2;var num2;
                if(date.getMonth()+1<10&&date.getDate()>9){
                    num=date.getMonth()+1;
                    month='0'+num;
                    return `${date.getFullYear()}-${month}-${date.getDate()}`;
                }else if(date.getMonth()+1>=10&&date.getDate()<10){
                    num2=date.getDate();
                    date2='0'+num2;
                    return `${date.getFullYear()}-${date.getMonth()+1}-${date2}`;
                }
                else if(date.getMonth()+1<10&&date.getDate()<10){
                    num=date.getMonth()+1;
                    month='0'+num;
                    num2=date.getDate();
                    date2='0'+num2;
                    return `${date.getFullYear()}-${month}-${date2}`;
                }else if(date.getMonth()+1>=10&&date.getDate()>9){
                    return `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
                }
                // return `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
            },
            confirmHandler(value) {
                if(this.bOk) {
                    this.startTime = this._dateFormat(value);
                }else {
                    this.endTime = this._dateFormat(value);
                }
            },
            //部门
            async _requestDep(){
                let res=await axios.post(API_ALL_DEP,qs.stringify({
                    utoken:this.token,
                    type:'1',
                    officeId:this.officeId
                }));
                let data=res.data;
                console.log(data.list);
                this.Deps=data.list;
                let arr=[];
                for(var i=0;i<this.Deps.length;i++){
                    arr.push(this.Deps[i].name)
                }
                this.slots[0].values=arr;
            },
            async _requestPro(){
                let res=await axios.post(API_APPROVAL_PRO,qs.stringify({
                    utoken:this.token,
                    type:'9',
                    officeId:this.officeId
                }));
                let data=res.data;
                console.log(data.list);

                this.Pros=data.list;
                let arr=[];
                for(var i=0;i<this.Pros.length;i++){
                    arr.push(this.Pros[i].name)
                }
                this.slotsPro[0].values=arr;
            },
            onValuesChange(a) {
                this.leaveValue = a;
                this.values = [a];
                for(var i=0;i<this.Deps.length;i++){
                    if(this.values[0]===this.Deps[i].name){
                        this.officeId2=this.Deps[i].id;
                    }
                }
                console.log(this.officeId2);
            },
            onValuesChangePro(b) {
                this.leaveValuePro = b;
                this.valuesPro = [b];
                for(var i=0;i<this.Pros.length;i++){
                    if(this.valuesPro[0]===this.Pros[i].name){
                        this.processDefId=this.Pros[i].value;
                    }
                }
                console.log(this.processDefId);
            },
            Sub:function () {
                if(this.officeId===''||this.officeId2===''||this.Reason===''||this.money===''||this.startTime===''||this.endTime===''||this.Cashier===''||this.processDefId===''||this.Remarks===''){
                    alert('请把数据补充完整');
                }else{
                    axios.post(API_ADD_IOU,qs.stringify({
                        utoken:this.token,
                        companyId:this.officeId,
                        officeId:this.officeId2,
                        reason:this.Reason,
                        Reason:this.money,
                        useLoanTime:this.startTime,
                        returnLoanTime:this.endTime,
                        cashier:this.Cashier,
                        processDefId:this.processDefId,
                        remarks:this.Remarks
                    })).then(res=>{
                        let data=res.data;
                        if(data.result===0){
                            Toast({
                                message: '提交成功',
                                duration: 2000
                            });
                            this.$router.push('subSuccess');
                        }else if(data.result===2){
                            window.localStorage.clear();
                            this.$router.push('/login');
                            Toast({
                                message: '用户已失效，请重新登陆',
                                duration: 2000
                            });
                        }
                        console.log(res);
                    }).catch(err=>{
                        console.log(err);
                    })
                }
            }
            }

    }
</script>

<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"
    .iou-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        .bottom
            margin-bottom 10px
        .pi-wrapper
            position: absolute
            top: 50px
            bottom: 50px
            left: 0
            right: 0
        .purchase-btn
            width: 100%
            height: 50px
            font-size: 16px
            text-align: center
            line-height: 50px
            position: absolute
            left: 0
            bottom: 0
            color #fff
            background-color: $color-orange
</style>



