import Vue from 'vue';
import Router from 'vue-router';
import Account from './account';

import IndexRoute from './index-route';

import Team from './team';

import Message from './message';

import Mine from './mine';

import AppStore from 'components/appStore/appStore';

import Cart from 'components/cart/cart';

import Purse from 'components/purse/purse';
import AddCard from 'components/purse/subpage/add-card/add-card';
import Extract from 'components/purse/subpage/extract/extract';
import Recharge from 'components/purse/subpage/Recharge/Recharge';

import Register from 'components/register/register';

import Login from 'components/login/login';

import { LOGIN } from 'common/js/isLogin';
import PayApp from 'components/pay-app/pay-app'
import Repurchase from 'components/repurchase-app/repurchase-app'

import axios from 'axios';

Vue.use(Router);

const router = new Router({
    routes: [
        {
            path: '/',
            redirect: '/account'
        },
        {
            path: '/login',
            component: Login
        },
        {
            path: '/register',
            component: Register
        },
        Account,
        IndexRoute,
        Team,
        Mine,
        Message,
        {
            path: '/app',
            component: AppStore,
            children: [
                {
                    path: 'cart',
                    component: Cart
                },
                {
                    path: 'payapp/:id',
                    component: PayApp
                },
                {
                    path: 'repurchase/:id',
                    component: Repurchase
                }
            ]
        },
        {
            path: '/purse',
            component: Purse,
            meta: {
                requireAuth: true
            },
            children: [
                {
                    path: 'addcard',
                    component: AddCard
                },
                {
                    path: 'extract',
                    component: Extract
                },
                {
                    path: 'recharge',
                    component: Recharge
                }
            ]
        }
    ]
});
router.beforeEach((to, from, next) => {
    let login = new LOGIN();

    if(to.meta.requireAuth) {
        if(login.isLogin()) {
            next()
        }else{
            next({
                path: '/login',
                query: {
                    redirect:to.fullPath
                }
            })
        }
    }else{
        next()
    }
});

export default router;



