<template>
    <div class="sign-wrapper">
        <v-header :path="fromUrl" title="退出群组 (不用了)"></v-header>
        <div class="btn-group">
            <div class="del-friend" @click="delGroup">退出群组</div>
        </div>
    </div>
</template>
<script>
    import VHeader from 'components/header/header';
    import {API_SIGN_GROUP} from 'common/js/api';
    import  axios from 'axios';
    import { Indicator, Toast } from 'mint-ui';
    import qs from 'qs';


    export default {
        data() {
            return {
                fromUrl:'',
                id:'',
                token:''
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        created(){
            this.id=this.$route.params.id;
            this.token=this.$login.getToken();
            console.log(this.id);
            console.log(this.token);
        },
        computed: {},
        components: {VHeader},
        methods: {
            delGroup(){
                axios.post(API_SIGN_GROUP,qs.stringify({
                    utoken:this.token,
                    target:this.id
                })).then((res)=> {
                    console.log(res.data.msg);
                    // this.$router.go(-3);
                    // Toast({
                    //     message: res.data.msg,
                    //     duration: 1000
                    // });
                }).then((err)=>{
                    console.log(err);
                    // alert(err.data.msg);
                })
            }
        }
    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"
    .sign-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index: 1000
        background-color: $color-bg
        .btn-group
            div
                margin: 0 auto
                text-align: center
                width: 90%
                line-height: 40px
                margin-top: 20px
                color #fff
                border-radius 4px
                font-size: 14px
                background-color: $color-orange
            .del-friend
                background-color: rgb(255, 73, 72)
</style>



