<template>
    <div class="charts-wrapper" style="text-a">
        <p style="text-align: center;position: relative;top: 70px;">暂无数据...</p>
        <div class="charts" ref="chart">

        </div>
        <div class="tab-btn">
            <div :class="{ on: show === 0 }" @click="showChart(0)">收</div>
            <div :class="{ on: show === 1}" @click="showChart(1)">支</div>
        </div>
    </div>
</template>

<script>
    import echarts from 'echarts';

	export default {
	    props: {
	        chartArr: {
	            type: Array,
                default() {
	                return []
                }
            }
        },
        computed: {
	        income() {
	            let arr = [];
	            for(let i = 0; i < this.chartArr.length; i++) {
	                if(this.chartArr[i].t === 1) {
	                    arr.push({
                            name: this.chartArr[i].soure,
                            value: this.chartArr[i].money
                        })
                    }
                }
                for(let i = 0; i < arr.length; i++) {
	                if(i !== arr.length-1) {
                        for(let i = 0; i < arr.length; i++) {
                            if(i !== arr.length-1) {
                                for(let j = i; j < arr.length; j++) {
                                    if( i !== j) {
                                        if(arr[i].name === arr[j].name) {
                                            arr[i] = {
                                                name: arr[i].name,
                                                value: parseFloat(arr[i].value) + parseFloat(arr[j].value)
                                            };
                                            arr.splice(j, 1)
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                return arr;
            },
            pay() {
                let arr = [];
                for(let i = 0; i < this.chartArr.length; i++) {
                    if(this.chartArr[i].t === 2) {
                        arr.push({
                            name: this.chartArr[i].soure,
                            value: this.chartArr[i].money
                        })
                    }
                }
                for(let i = 0; i < arr.length; i++) {
                    if(i !== arr.length-1) {
                        for(let j = i; j < arr.length; j++) {
                            if( i !== j) {
                                if(arr[i].name === arr[j].name) {
                                    arr[i] = {
                                        name: arr[i].name,
                                        value: parseFloat(arr[i].value) + parseFloat(arr[j].value)
                                    };
                                    arr.splice(j, 1)
                                }
                            }
                        }
                    }
                }
                return arr;
            }
        },
	    data() {
	        return {
	            charts: null,
	            show: 0,
	            takeInOption: {
                    tooltip : {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    series : [
                        {
                            name: '收入来源',
                            type: 'pie',
                            radius : '60%',
                            center: ['50%', '40%'],
                            data: [],
                            itemStyle: {
                                emphasis: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            },
                            labelLine: {
                                normal: {
                                    length: 5,
                                   length2: 60
                                }
                            }
                        }
                    ],
                    color: ['#f6a933','#65c9eb','#f9cd38','#fe8189','#cca1f0']
                },
                takeOutOption: {
                    tooltip : {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    series : [
                        {
                            name: '支出',
                            type: 'pie',
                            radius : '60%',
                            center: ['50%', '40%'],
                            data: this.pay,
                            itemStyle: {
                                emphasis: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            },
                            labelLine: {
                                normal: {
                                    length: 5,
                                    length2: 60
                                }
                            }
                        }
                    ],
                    color: ['#f6a933','#65c9eb','#f9cd38','#fe8189','#cca1f0']
                }
            }
        },
	    mounted() {
            this.charts = echarts.init(this.$refs.chart);

        },
        methods: {
            showChart(wh) {
                this.show = wh;
                if(wh === 0) {
                    this.charts.setOption(this.takeInOption);
                }else{
                    this.charts.setOption(this.takeOutOption);
                }
            }
        },
        watch: {
            chartArr() {
                this.takeInOption.series[0].data = this.income;
                this.takeOutOption.series[0].data = this.pay;
                this.charts.setOption(this.takeInOption);
                setTimeout(() => {
                    this.charts.resize()
                },20)
            }
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    .charts-wrapper
        width: 100%
        /*height: 112px*/
        height: 3.24rem
        position: relative
        .tab-btn
            width: 110px
            height: 19px
            position: absolute
            left: 50%
            bottom: 6px
            margin-left: -55px
            display: flex
            justify-content space-between
            div
                flex 0 0 53px
                text-align: center
                background-color #a0a0a0
                color #fff
                &.on
                    background-color $color-orange
        .charts
            width: 100%
            height: 100%
</style>



