<template>
    <div class="record-detail-wrapper">
        <v-header path="/mine/record" title="订单详情"></v-header>
        <div class="record-detail-item" >
            <div class="title">订单信息</div>
            <ul class="cardlist">
                <li class="listitem">
                    <span data-v-8d3b19b8="">订单号：</span>
                    {{code}}
                </li>
                <li class="listitem">
                    <span data-v-8d3b19b8="">支付方式：</span>
                    在线支付
                </li>
                <li class="listitem">
                    <span data-v-8d3b19b8="">下单时间：</span>
                    {{payDate}}
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import axios from 'axios';
    import {API_CONSUMPTION} from 'common/js/api';
        import qs from 'qs';

	export default {
        data(){
            return {
                id:'',
                payDate:'',
                name:'',
                code:''
            }
        },
        created(){
            let token = this.$login.getToken();
            this._request(token);
        },
        methods:{
            async _request(token){
	            let res=await axios.post(API_CONSUMPTION,qs.stringify({
                    utoken:token
                }));
                let data=res.data;
                this.list=data.list;
                console.log(data);
            },
        },
        created(){
             this.id = this.$route.query.id;
             this.name = this.$route.query.name;
             this.payDate = this.$route.query.payDate;
             this.code = this.$route.query.code;
        },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .record-detail-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        .record-detail-item
            background-color: #fff
            .title
                font-size: 14px;
                color: #333;
                border-bottom: 1px solid #eee;
                padding-left: 12px;
                line-height: 35px;
            .cardlist
                color: #6e6e6e;
                padding-left: 15px
                border-bottom: 1px solid #f2f2f2;
                .listitem
                    display: flex
                    align-items baseline
                    padding:12px 30px 10px 0
                    border-bottom: 1px solid #f2f2f2;
</style>



