<template>
    <div class="files-wrapper">
        <v-header :path="fromUrl" title="档案"></v-header>
        <!--<mt-search v-model="value"></mt-search>-->
        <div class="scroller-wrapper">
            <scroller>
                <div class="files-item">
                    <div class="name">营业执照</div>
                    <p class="detail border-1px">{{detail}}</p>
                    <div class="img-group">
                        <div class="img-item" v-for="item in imgarr">
                            <img :src="item.url" alt="">
                        </div>
                        <!--<div class="img-item">
                            <img src="//fuss10.elemecdn.com/a/2a/5fd158b2803f19742462cd5b786afjpeg.jpeg?imageMogr/format/webp/thumbnail/!130x130r/gravity/Center/crop/130x130/" alt="">
                        </div>-->
                    </div>
                </div>
            </scroller>
        </div>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import {API_ARCHIVES} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
	export default {
	    data() {
	        return {
                detail:'',
                imgarr:[],
	            value: '',
                fromUrl: '',
                id:'',
                token:''
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
               vm.fromUrl = from.path;
            })
        },
        created() {
            this.token = this.$login.getToken();
            this.id=this.$route.params.id;
            console.log(this.id);
            this._request();
        },
        methods:{
            async _request(){
                let res=await axios.post(API_ARCHIVES,qs.stringify({
                    utoken:this.token,
                    officeId:this.id
                }));
                let data=res.data;
                if(data.result===0){
                    this.imgarr=data.list;
                    this.detail=data.remarks;
                    console.log(data);
                }else if(data.result===2){
                    window.localStorage.clear();
                    this.$router.push('/login');
                    Toast({
                        message: '用户已失效,请重新登陆',
                        duration: 2000
                    });
                }
            }
        },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"

    .files-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        .files-item
            width: 100%
            padding: 10px
            margin-top: 10px
            background-color: #fff
            .name
                font-size: 16px
            .detail
                padding: 5px 0
                color #777
                border-b(#ccc)
            .img-group
                display: flex
                justify-content space-between
                flex-wrap wrap
                padding-top: 5px
                .img-item
                    width: 25%
                    img
                        width: 100%
        .scroller-wrapper
            width: 100%
            position: absolute
            top: 44px
            bottom: 0
            z-index 99999
</style>



