export default {
    install(Vue, opt) {
        Vue.prototype.$login = new LOGIN();
    }
};


export class LOGIN {
    isLogin() {
        return Boolean(window.localStorage.getItem('u_token'));
    }
    getUserName() {
        return this.isLogin() ? JSON.parse(window.localStorage.getItem('u_token')).username : '未登录'
    }
    getToken() {
        return this.isLogin() ? JSON.parse(window.localStorage.getItem('u_token')).token : ''
    }
    getAvatar() {
        return this.isLogin() ? JSON.parse(window.localStorage.getItem('u_token')).avatar : ''
    }
    resetLogin(vue) {
        window.localStorage.clear();
        vue.$router.push({
            path: '/login'
        })
    }
}



