<template>
    <div class="goods-wrapper">
        <scroller v-if="hasGoods">
            <ul>
                <li v-for="(i,index) in goodsList" :key="index" class="g-item border-1px">
                    <div class="img">
                        <img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1512475529260&di=55bfd2e930b547d77a999eff2c910b9f&imgtype=0&src=http%3A%2F%2Ff.hiphotos.baidu.com%2Fzhidao%2Fpic%2Fitem%2Fb3b7d0a20cf431adeef1310e4d36acaf2edd98e1.jpg" alt="">
                    </div>
                    <div class="text">
                        <div>{{i.name}}</div>
                        <div class="money">￥<span class="big">{{i.price}}</span></div>
                        <div>已售{{i.saleCount}}份</div>
                    </div>
                    
                    <cartcontral @addCart="addCart" :goodMsg="{ price: 30, id: i.id ,name:i.name}"></cartcontral>
                </li>
            </ul>
        </scroller>
        <div v-if="requestOk&&hasGoods==0" >店铺木有商品</div>
        <div class="cart">
            <div v-show="sumPrice == 0">您还没有选购商品</div>
            <div v-show="sumPrice > 0">总计：<span class="money">{{sumPrice}}</span>元</div>
            <!-- <span>收藏店铺 <i><img width="20px" src="https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=1597601509,1590985048&fm=27&gp=0.jpg" alt=""></i></span> -->
            <!--<router-link append to="cart" tag="div" v-show="sumPrice > 0" class="go-cart">查看购物车</router-link>-->
            <div class="go-cart" @click="lookcar">
                查看购物车
            </div>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
    import { API_GOODS,API_ADD_CAR } from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Toast, Indicator } from 'mint-ui';
    import Cartcontral from '../comp/cartcontral/cartcontral';
    import {mapMutations} from 'vuex';
	export default {
	    data() {
	        return {
	            goodsList: [],
                cart: [],
                sumPrice: 0,
                requestOk: false
            }
        },
        computed: {
	        hasGoods() {
	            return this.goodsList.length;
            },
        },
        created() {
            this._request();
        },
        methods: {
	        async _request() {
                Indicator.open();
                let res = await axios.post(API_GOODS, qs.stringify({
                    officeId: this.$route.params.id,
                    utoken: this.$login.getToken()
                }));
                Indicator.close();
                let data = res.data;
                if(data.result === 0) {
                    this.goodsList = data.list;
                    console.log(data.list);
                }else{
                    window.localStorage.clear();
                    Toast({
                        message: data.msg,
                        duration: 2000
                    });
                }
                this.requestOk = true;
            },
            addCart(good) {
	            let sum = 0;
	            if(this.cart.length === 0) {
                    this.cart.push(good);
                    console.log(this.cart);
                    sum += good.sumPrice;
                }else{
	                let searchRes = this._searchGoods(this.cart, good);
	                if(searchRes.hasGood) {
	                   this.cart.splice(searchRes.index, 1, good);
                    }else{
	                    this.cart.push(good);
                    }
                    for(let i = 0; i < this.cart.length; i++) {
	                    sum += this.cart[i].sumPrice;
                    }
                }
                console.log(this.cart);
                this.sumPrice = sum;
                // this.setShopCart(this.cart);
            },
            lookcar(){
	            var arr=[];
	            for(let i=0;i<this.cart.length;i++){
	                arr.push({
                        goodsNum:this.cart[i].count,
                        goodsPrice:this.cart[i].sumPrice,
                        goodsId:this.cart[i].id,
                    })
                }
	            var car=JSON.stringify(arr);
	            axios.post(API_ADD_CAR,qs.stringify({
                    utoken:this.$login.getToken(),
                    goodsInfo:car,
                    officeId:this.$route.params.id
                })).then(res=>{
                    console.log(res);
                    let data=res.data;
                    if(data.result===0){
                        this.$router.push('goods/cart')
                    }else if(data.result===2){
                        window.localStorage.clear();
                        Toast({
                            message: data.msg,
                            duration: 2000
                        });
                    }

                }).catch(err=>{
                    console.log(err);
                })
            },
           /* ...mapMutations({
                'setShopCart':"SET_SHOP_CART"
            }),*/
            _searchGoods(originArr, item) {
                for(let i = 0; i < originArr.length; i++) {
                    if(item.id === originArr[i].id) {
                        return {
                            hasGood: true,
                            index: i
                        }
                    }
                }
                return {
                    hasGood: false
                }
            }

        },
        components: {
            Cartcontral
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/mixin"
    @import "~common/stylus/variable"

    .goods-wrapper
        width: 100%
        height: 100%
        box-sizing border-box
        ul
            padding:0 12px 45px
        .g-item
            padding: 10px 6px
            box-sizing border-box
            height: 92px
            border-b(#eee)
            font-size: 0
            display: flex
            div
                display: inline-block
                vertical-align: top
            .img
                flex 0 0 73px
                width: 73px
                height: 73px
                img
                    width: 100%
                    height: 100%
                    border-radius 5px
            .text
                flex 1
                box-sizing border-box
                padding-left: 15px
                font-size: 12px
                color #6a6969
                .money
                    color $color-orange
                    .big
                        font-size: 18px
                    .del
                        text-decoration line-through
                        color #6a6969
                div
                    display: block

        .cart
            position: fixed
            bottom: 0
            left: 0
            right: 0
            height: 45px
            color #fff
            display: flex
            justify-content space-between
            align-items center
            padding-left: 20px
            background-color $color-black
            .money
                color $color-orange
                font-size: 18px
            .go-cart
                width: 96px
                line-height: 45px
                text-align: center
                color #fff
                background-color: $color-orange
</style>



