import Vue from 'vue'
import Vuex from 'vuex'
import createLogger from './logger';

import purse from './modules/purse';
import company from './modules/company';
import login from './modules/login';
import billList from './modules/billList';
import bxList from './modules/bxList';
import setBankcard from './modules/setbankcard'
import moneyTotal from './modules/moneytotal'
import setPayApp from './modules/setpayapp'
import setPayedApp from './modules/setpayedapp'
import setListPeople from './modules/complete'
import setPeopleId from './modules/setpeopleid'
import setAssetDetails from './modules/assetdetails'
import setShopList from './modules/shoplist'
import setMyCus from './modules/setmycus'
import setNoCus from './modules/setnocus'
import setAllCus from './modules/setallcus'
import setShopCart from './modules/setshopcart'
import setChart from './modules/setchart'
import setExpendmsg from './modules/setexpendmsg'
import setprofitmsg from './modules/setprofitMsg'
import setFriendList from './modules/setfriendlist'

Vue.use(Vuex);

const debug = process.env.NODE_ENV !== 'production';

export default new Vuex.Store({
    actions: {
        getFriendList(context) {
            return new Promise((resolve, reject) => {
                Vue.prototype.$im.getRoster({
                    success(data) {
                        console.log(1);
                        context.commit('SET_FRIEND_LIST', data);
                        resolve();
                    }
                })
            })
        }
    },
    state: {
        friendList: [],
        connected: false
    },
    mutations: {
        ['SET_FRIEND_LIST'](state, friendList) {
            state.friendList = friendList;
        },
        ['SET_CONN_STATE'](state, status) {
            state.connected = status
        }
    },
    getters: {
        getFriendList: state => state.friendList,
        getConnState: state => state.connected
    },
    modules: {
        purse,
        company,
        login,
        billList,
        bxList,
        setBankcard,
        moneyTotal,
        setPayApp,
        setPayedApp,
        setListPeople,
        setPeopleId,
        setAssetDetails,
        setShopList,
        setMyCus,
        setNoCus,
        setAllCus,
        setShopCart,
        setChart,
        setExpendmsg,
        setprofitmsg,
        setFriendList
    },
    strict: debug,
    plugins: debug ? [createLogger()] : []
});



