<template>
    <div class="red-wrapper">
        <div class="red-top">
            <mu-avatar class="red-avatar" :size="57" src="http://avatar.zbjimg.com/016/72/27/200x200_avatar_08.jpg!big"/>
            <div class="red-name">
                来自{{name}}
            </div>
            <div class="red-money">
                ￥<span class="fz26">6.66</span>元
            </div>
        </div>
        <div class="red-bot">
            <div class="red-btn" @click="receiveBonus">点击领取</div>
            <div class="red-msg">恭喜发财，大吉大利</div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
	export default {
	    props: {
	        name: {
	            type: String,
                default: "北京睿信毅城科技有限公司"
            }
        },
        methods: {
            receiveBonus(){
                console.log(123)
                this.$emit('receiveBonus')
            }
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    .red-wrapper
        width: 242px
        height: 319px
        background-color #f36750
        .red-top
            width: 100%
            height: 176px
            background-image url("./red-bag.png")
            background-size 100%
            background-position center top
            background-repeat no-repeat
            text-align: center
            .red-avatar
                margin-top: 18px
            .red-name
                font-size: 12px
                color #6f4417
            .red-money
                font-size: 16px
                color #f36750
                .fz26
                    font-size: 26px
        .red-bot
            .red-btn
                width: 95px
                height: 23px
                line-height: 23px
                margin: 5px auto 0
                border-radius 3px
                color #a34636
                background-color #fbd951
                text-align: center
            .red-msg
                color #fbd951
                margin-top: 20px
                text-align: center
</style>



