<template>
    <div class="bill-wrapper">
        <div class="bill-header">
            <span>我的账单</span>
            <router-link tag="span" to="account/billlist">更多&gt;</router-link>
        </div>
        <div class="bill-content">
            <div v-for="item in billArr" class="item border-1px">
                <div class="icon">
                    <div class="ball">
                        <i class="icon-dianhua"></i>
                    </div>
                </div>
                <div class="bill-text">
                    <div class="top">
                        <span>{{item.soure}}</span>
                        <span class="y" v-if="item.t == 2">可报销</span>
                    </div>
                    <div class="bot">
                        <span class="bot-money green" v-if="item.t == 2">-{{item.money}}</span>
                        <span class="bot-money red" v-else-if="item.t == 1">+{{item.money}}</span>
                        <span>来自{{item.mode}} {{item.date}}</span>
                    </div>
                </div>
            </div>
        </div>
        <div class="set-one">
            <div class="btn-write" @click="goWrite">记一笔</div>
        </div>
    </div>
</template>

<script>
	export default {
	    props: {
	        billArr: {
	            type: Array,
                default() {
	                return []
                }
            }
        },
	    methods: {
	        goWrite() {
	            this.$router.push({
                    path: '/account/writeone'
                })
            }
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/mixin"
    @import "~common/stylus/variable"
    .bill-header
        display: flex
        justify-content space-between
        height: 29px
        background-color: #f0f0f0
        box-sizing border-box
        padding:0 15px
        line-height: 29px
        font-size: 12px
        color #555554
    .bill-content
        padding: 0 16px
        .item
            height: 38px
            border-b(#ccc)
            font-size: 0
            box-sizing border-box
            padding: 0 15px
            display: flex
            .icon
                width: 21px
                height: 100%
                line-height: 38px
                display: inline-block
                vertical-align: top
                .ball
                    display: inline-block
                    vertical-align: middle
                    width: 21px
                    height: 21px
                    border-radius 50%
                    background-color $color-orange
                    color #fff
                    text-align: center
                    line-height: 21px
                    i
                        font-size: 12px
            .bill-text
                padding: 5px 0 0 11px
                box-sizing border-box
                flex 1
                display: flex
                justify-content space-between
                font-size: 12px
                .bot,.top
                    display: flex
                    flex-direction column
                    justify-content center
                .bot
                    font-weight normal
                    font-size: 10px
                    color #929292
                    text-align: right
                    .bot-money
                        font-size: 12px
                        &.red
                            color red
                        &.green
                            color green
                .y
                    font-size: 10px
                    color $color-orange
    .set-one
        padding: 0 15px
        box-sizing border-box
        position: absolute
        left: 0
        right: 0
        bottom: 3%
        .btn-write
            height: 32px
            background-color $color-orange
            color #fff
            font-size: 14px
            border-radius 5px
            margin-top: 12px
            text-align: center
            line-height: 32px
</style>



