<template>
    <div class="join-field-wrapper">
        <v-header :path="fromUrl" title="填写申请信息"></v-header>
        <!--<mt-field label="姓名" placeholder="请输入姓名"></mt-field>-->
        <mt-cell title="岗位" :value="leaveValue" @click.native="popupVisible=true" is-link></mt-cell>

        <mt-cell title="流程" :value="leaveValueFlow" @click.native="popupVisibleFlow=true" is-link></mt-cell>

        <!--<mt-field label="所属岗位" placeholder="请输入所属岗位"></mt-field>-->
        <div class="sub" @click="Sub">发送</div>

        <mt-popup
            v-model="popupVisible"
            position="bottom">
            <mu-picker :slots="slots" :visible-item-count="3" @change="onValuesChange" :values="values"/>
        </mt-popup>

        <mt-popup
            v-model="popupVisibleFlow"
            position="bottom">
            <mu-picker :slots="slotsFlow" :visible-item-count="3" @change="onValuesChangeFlow" :values="valuesFlow"/>
        </mt-popup>


    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import {API_ALL_STATION,API_APPROVAL_PRO,API_JOIN_TRAM} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
	export default {
	    data() {
	        return {
	            fromUrl: '',
                officeid:'',
                comId:'',

                off:[],
                offId:[],
                popupVisible: false,
                leaveValue:'请选择（必选）',
                values: [],
                slots: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],

                FlowId:'',
                Flow:[],
                popupVisibleFlow: false,
                leaveValueFlow:'请选择（必选）',
                valuesFlow: [],
                slotsFlow: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        created(){
	        this.officeid=this.$route.params.id;
	        this.comId=this.$route.params.comid;
	        this._request();
	        this._requestPro();
        },
        methods:{
            onValuesChange(a) {
                this.leaveValue = a;
                this.values = [a];
                for(var i=0;i<this.off.length;i++){
                    if(this.values[0]===this.off[i].name){
                        this.offId=this.off[i].id
                    }
                }
                console.log(this.offId);
            },
            onValuesChangeFlow(b) {
                this.leaveValueFlow = b;
                this.valuesFlow = [b];
                for(var i=0;i<this.Flow.length;i++){
                    if(this.valuesFlow[0]===this.Flow[i].name){
                        this.FlowId=this.Flow[i].value
                    }
                }
                console.log(this.FlowId);
            },
	        async _request(){
	            let res =await axios.post(API_ALL_STATION,qs.stringify({
                    utoken:this.$login.getToken(),
                    type:'2',
                    officeId:this.officeid
                }));
                let data=res.data;
                console.log(data);
                this.off=data.list;
                let arr=[];
                for(let i=0;i<this.off.length;i++){
                    arr.push(this.off[i].name)
                }
                this.slots[0].values=arr;
            },
            async _requestPro(){
                let res =await axios.post(API_APPROVAL_PRO,qs.stringify({
                    utoken:this.$login.getToken(),
                    type:'7',
                    officeId:this.comId
                }));
                let data=res.data;
                console.log(data);
                this.Flow=data.list;
                let arr=[];
                for(let i=0;i<this.Flow.length;i++){
                    arr.push(this.Flow[i].name);
                }
                this.slotsFlow[0].values=arr;
            },
            Sub(){
                axios.post(API_JOIN_TRAM,qs.stringify({
                    utoken:this.$login.getToken(),
                    teamId:this.officeid,
                    roleId:this.offId,
                    processId:this.FlowId
                })).then(res=>{
                    let data=res.data;
                    console.log(res);
                    if(data.result===0){
                        console.log(res);
                        this.$router.go(-3);
                    }else if(data.result===2){

                        window.localStorage.clear();
                        this.$router.push('/login');
                        Toast({
                            message: '用户已失效,请重新登陆',
                            duration: 2000
                        });
                    }
                }).catch(err=>{
                    console.log(err);
                })
            }

        },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .join-field-wrapper
        position: fixed
        left: 0
        top: 0
        bottom: 0
        right: 0
        background-color: $color-bg
        .sub
            position absolute
            top:15px
            right 10px
            color #FFFFFF
</style>



