<template>
    <div class="customer-wrapper">
        <v-header  :path="fromUrl" title="客户"></v-header>

        <mt-navbar v-model="selected">
            <mt-tab-item id="1">我负责的</mt-tab-item>
            <!--<mt-tab-item id="2">共享给我的</mt-tab-item>-->
            <mt-tab-item id="3">全公司的</mt-tab-item>
            <mt-tab-item id="4">无人负责的</mt-tab-item>
        </mt-navbar>
        <div style="position: absolute;top:100px;bottom: 0;left: 0;right: 0">
        <scroller>
        <!--<div class="peoplesLength">共{{2}}人</div>-->
        <mt-tab-container v-model="selected">
            <mt-tab-container-item id="1">
                <mt-search v-model="searchValue" @input.native="inputHandler">
                    <ul class="lists">
                        <li class="list" v-for="item in mypeople" :key="item.id" @click="ToMyDetails(item.id)">
                            <span class="name">{{item.name}}</span>
                        </li>
                    </ul>
                </mt-search>

                <div class="lists-people"  v-for="item in mys" :key="item.id" @click.capture="ToMyDetails(item.id)">
                    <img :src="item.pics" class="lists-img">
                    <span class="lists-name">{{item.name}}</span>
                </div>
            </mt-tab-container-item>


            <mt-tab-container-item id="3">
                <mt-search v-model="searchValue" @input.native="inputHandlerall">
                    <ul class="lists">
                        <li class="list" v-for="item in myallpeople" :key="item.id" @click="ToAllDetails(item.id)">
                            <span class="name">{{item.name}}</span>
                        </li>
                    </ul>
                </mt-search>

                <div class="lists-people" v-for="item in Alls" :key="item.id" @click.capture="ToAllDetails(item.id)">
                    <img :src="item.pics" class="lists-img">
                    <span class="lists-name">{{item.name}}</span>
                </div>
            </mt-tab-container-item>


            <mt-tab-container-item id="4">
                <mt-search v-model="searchValue" @input.native="inputHandlerno">
                    <ul class="lists">
                        <li class="list" v-for="item in mynopeople" :key="item.id" @click="ToNoDetails(item.id)">
                            <span class="name">{{item.name}}</span>
                        </li>
                    </ul>
                </mt-search>
                <div class="lists-people"  v-for="item in Nos" :key="item.id" @click.capture="ToNoDetails(item.id)">
                    <img :src="item.pics" class="lists-img">
                    <span class="lists-name">{{item.name}}</span>
                </div>
            </mt-tab-container-item>
        </mt-tab-container>
        </scroller>
        </div>
        <router-link tag="div" append to="createpeople" class="create-people">新建</router-link>
        <transition name="slide">
            <router-view></router-view>
        </transition>
    </div>

</template>
<script>
    import VHeader from 'components/header/header';
    import {API_CUSTOMER} from 'common/js/api';
    import _ from 'lodash';
    import { mapMutations } from 'vuex';
    import qs from 'qs';
    import axios from 'axios';
    import { Indicator, Toast } from 'mint-ui';

    export default {
        name:'customer',
        data() {
            return {
                selected:'1',
                fromUrl: '',
                searchValue:'',
                token:'',
                mys:[],
                Alls:[],
                Nos:[],
                mypeople:[],
                myallpeople:[],
                mynopeople:[]
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        created(){
            this.token=this.$login.getToken();
            this._requestMy();
            this._requestAll();
            this._requestNo();
        },
        computed: {},
        beforeRouteUpdate(to, from, next){
            if(from.path.substr(from.path.length-12,from.path.length)==='createpeople'){
                this._requestMy();
                this._requestAll();
            }
            next();
        },
        components: {VHeader},
        methods: {
            async _requestMy(){
                let res=await axios.post(API_CUSTOMER,qs.stringify({
                    utoken:this.token,
                    customerType:'1',
                    customerName:this.searchValue
                }));
                let data=res.data;
                if(data.result===0){
                    this.mys=data.list;
                    this.setMyCus(data.list)
                }else if(data.result===2){
                    window.localStorage.clear();
                    this.$router.push('/login');
                    Toast({
                        message: '用户已失效，请重新登陆',
                        duration: 2000
                    });
                }
            },
            async _requestAll(){
                let res=await axios.post(API_CUSTOMER,qs.stringify({
                    utoken:this.token,
                    customerType:'3',
                    customerName:this.searchValue
                }));
                let data=res.data;
                console.log(data);
                if(data.result===0){
                    this.Alls=data.list;
                    this.setAllCus(data.list);
                }else if(data.result===2){
                    window.localStorage.clear();
                    this.$router.push('/login');
                    Toast({
                        message: '用户已失效，请重新登陆',
                        duration: 2000
                    });
                }

            },
            async _requestNo(){
                let res=await axios.post(API_CUSTOMER,qs.stringify({
                    utoken:this.token,
                    customerType:'2',
                    customerName:this.searchValue
                }));
                let data=res.data;
                console.log(data);
                if(data.result===0){
                    this.Nos=data.list;
                    this.setNoCus(data.list);
                }else if(data.result===2){
                    window.localStorage.clear();
                    this.$router.push('/login');
                    Toast({
                        message: '用户已失效，请重新登陆',
                        duration: 2000
                    });
                }
            },
            ToMyDetails(id){
                console.log(id);
                this.$router.push({
                    path:`customer/details/${id}`
                })
            },
            ToAllDetails(id){
                this.$router.push({
                    path:`customer/cusall/${id}`
                })
            },
            ToNoDetails(id){
                this.$router.push({
                    path:`customer/cusno/${id}`
                })
            },
            inputHandler: _.debounce(function () {
                axios.post(API_CUSTOMER, qs.stringify({
                    utoken: this.token,
                    customerType:'1',
                    customerName: this.searchValue
                })).then((res) => {
                    let data = res.data;
                    console.log(data.list);
                    if(data.result===0){
                        this.mypeople=data.list;
                    }else if(data.result===2){
                        window.localStorage.clear();
                        this.$router.push('/login');
                        Toast({
                            message: '用户已失效，请重新登陆',
                            duration: 2000
                        });
                    }

                    /*console.log(this.$refs.contentMy);
                    for(var i=0;i<this.$refs.contentMy.length;i++){
                        this.$refs.contentMy[i].style.display='none';
                    }*/

                }).catch((err) => {
                    console.log(err);
                });
            }, 1000),
            inputHandlerall: _.debounce(function () {
                axios.post(API_CUSTOMER, qs.stringify({
                    utoken: this.token,
                    customerType:'3',
                    customerName: this.searchValue
                })).then((res) => {
                    let data = res.data;
                    console.log(data.list);
                    if(data.result===0){
                        this.myallpeople=data.list;
                    }else if(data.result===2){
                        window.localStorage.clear();
                        this.$router.push('/login');
                        Toast({
                            message: '用户已失效，请重新登陆',
                            duration: 2000
                        });
                    }


                    /*console.log(this.$refs.contentMy);
                    for(var i=0;i<this.$refs.contentMy.length;i++){
                        this.$refs.contentMy[i].style.display='none';
                    }*/

                }).catch((err) => {
                    console.log(err);
                });
            }, 1000),
            inputHandlerno: _.debounce(function () {
                axios.post(API_CUSTOMER, qs.stringify({
                    utoken: this.token,
                    customerType:'2',
                    customerName: this.searchValue
                })).then((res) => {
                    let data = res.data;
                    console.log(data.list);
                    if(data.result===0){
                        this.mynopeople=data.list;
                    }else if(data.result===2){
                        window.localStorage.clear();
                        this.$router.push('/login');
                        Toast({
                            message: '用户已失效，请重新登陆',
                            duration: 2000
                        });
                    }
                    /*console.log(this.$refs.contentMy);
                    for(var i=0;i<this.$refs.contentMy.length;i++){
                        this.$refs.contentMy[i].style.display='none';
                    }*/

                }).catch((err) => {
                    console.log(err);
                });
            }, 1000),
            ...mapMutations({
                'setMyCus':"SET_MY_CUS",
                'setAllCus':"SET_ALL_CUS",
                'setNoCus':"SET_NO_CUS",
            })
        }
    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"
    .customer-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        .create-people
            width: 80px
            padding: 0 5px
            text-align: center
            color #fff
            position: absolute
            right: 0
            top: 0
            line-height: 50px

    .lists-people
        height: 60px
        padding: 6px 5px 5px 10px
        border-bottom: 1px solid #CCC
        display flex
        align-items center
        background-color #FFFFFF

        .lists-img
            border-radius :50%
            width: 50px;
            height: 50px;
        .lists-name
            font-size 20px
            margin-left :30%
    .peoplesLength
        font-size 16px
        height 25px
        line-height 25px
        margin-left 10px

    .mint-navbar
        margin-bottom 2px

    .lists
        height 700px
        background-color #FFFFFF

    .list
        width 100%
        height 40px
        background-color #FFFFFF
        line-height 40px
        border-bottom 1px solid #CCCCCC
        z-index 999

        .name
            font-size 20px
            color #000000
            margin-left 20px
            margin-right 20px
</style>



