<template>
    <div class="star-wrapper">
        <div class="star" :class="{on: star>=1}"></div>
        <div class="star" :class="{on: star>=2}"></div>
        <div class="star" :class="{on: star>=3}"></div>
        <div class="star" :class="{on: star>=4}"></div>
        <div class="star" :class="{on: star>=5}"></div>
    </div>
</template>

<script>
	export default {
	    props: {
	        star: {
	            type: Number,
                default: 5
            }
        },
        mounted() {

        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    .star-wrapper
        display: inline-block
        vertical-align: top
        width: 71px
        height: 11px
        font-size: 0
        .star
            display: inline-block
            vertical-align: top
            font-size: 12px
            width: 12px
            height: 100%
            background url("./star.png") center no-repeat / 100%
            &.on
                background-image url("./star-on.png")
</style>



