import Message from 'page/message/message';
import Msg from 'page/message/subpage/message/message';
import Group from 'page/message/subpage/group/group';
import MailList from 'page/message/subpage/mail-list/mail-list';
import SearchFriend from 'page/message/subpage/search-friend/search-friend';
import Talk from 'page/message/subpage/talk/talk';
import FriendDetail from 'page/message/subpage/friend-detail/friend-detail';
import Friend from 'page/message/subpage/friend/friend';

import AddFriend from 'page/message/subpage/add-friend/add-friend';
import GroupTalk from 'page/message/subpage/group-talk/group-talk'
import SignGroup from 'page/message/subpage/sign-group/sign-group'
export default {
    path: '/message',
    component: Message,
    redirect: '/message/msg',
    children: [
        {
            path: 'msg',
            component: Msg
        },
        {
            path: 'group',
            component: Group
        },
        {
            path: 'friend',
            component: Friend
        },
        {
            path: 'maillist',
            component: MailList,
            children: [
                {
                    path: 'searchfriend',
                    component: SearchFriend
                },
                {
                    path: 'friend/:id/:login',
                    component: FriendDetail
                },
                {
                    path: 'friend/:id',
                    // path: 'friend',
                    component: FriendDetail
                },
                {
                    path: 'addfriend/:id/:login',
                    // path: 'addfriend',
                    component: AddFriend
                },
            ]
        },
        {
            // path: 'talk/:id',
            path: 'talk',
            component: Talk
        },
        {
            path: 'grouptalk/:id/:name',
            // path: 'talk',
            component: GroupTalk
        },
        {
            path: 'signgroup/:id',
            component: SignGroup
        }

    ]
}



