<template>
    <div class="create-peoples">
        <v-header title="新建联系人" :path="fromUrl"></v-header>
        <span class="file" @click="ToCustomer">保存</span>
        <mt-field label="姓名" placeholder="请输入姓名(必填)" v-model="username" class="name"></mt-field>
        <img-upload title="上传头像" style="margin-top: 10px;" ref="SrcBase"></img-upload>
        <mt-field label="电话" placeholder="请输入电话(必填)" type="number" v-model="phone"></mt-field>
        <mt-field label="年龄" placeholder="请输入年龄(必填)" type="number" v-model="company"></mt-field>
        <mt-cell title="性别" :value="leaveValue" @click.native="popupVisible=true" is-link></mt-cell>
        <mt-field label="客户意向" placeholder="选填" type="text" v-model="job"></mt-field>
        <mt-field label="备注信息" placeholder="选填" type="text" v-model="remarks"></mt-field>
        <mt-cell title="审批流程" :value="leaveValuePro" @click.native="popupVisiblePro=true" is-link></mt-cell>

        <mt-popup
            v-model="popupVisible"
            position="bottom">
            <mu-picker :slots="slots" :visible-item-count="3" @change="onValuesChange" :values="values"/>
        </mt-popup>

        <mt-popup
            v-model="popupVisiblePro"
            position="bottom">
            <mu-picker :slots="slotsPro" :visible-item-count="3" @change="onValuesChangePro" :values="valuesPro"/>
        </mt-popup>
    </div>

</template>
<script>
    import VHeader from 'components/header/header';
    import {API_APPROVAL_PRO,API_ADD_CUS} from 'common/js/api';
    import ImgUpload from 'components/img-upload/img-upload';
    import { Indicator, Toast } from 'mint-ui';
    import qs from 'qs';
    import axios from 'axios';

    export default {
        data() {
            return {
                fromUrl:'',
                username:'',
                phone:'',
                company:'',
                sex:'',
                job:'',
                address:'',
                remarks:'',
                token:'',
                officeId:'',
                imglist:'',

                popupVisible: false,
                leaveValue:'请选择性别（必选）',
                values: [],
                slots: [
                    {
                        flex: 1,
                        values: ['男','女'],
                        textAlign: 'center'
                    }
                ],

                Pros:[],
                popupVisiblePro: false,
                leaveValuePro:'请选择审批流程（必选）',
                valuesPro: [],
                slotsPro: [
                    {
                        flex: 1,
                        values: [],
                        textAlign: 'center'
                    }
                ],
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        created(){
            this.token=this.$login.getToken();
            this.officeId=this.$route.params.id;
            this._requestPro();
        },
        computed: {},
        components: {VHeader,ImgUpload},
        methods: {
            async _requestPro(){
                let res=await axios.post(API_APPROVAL_PRO,qs.stringify({
                    utoken:this.token,
                    type:'5',
                    officeId:this.officeId
                }));
                let data=res.data;
                console.log(data.list);

                this.Pros=data.list;
                let arr=[];
                for(var i=0;i<this.Pros.length;i++){
                    arr.push(this.Pros[i].name)
                }
                this.slotsPro[0].values=arr;
            },
            onValuesChange(a) {
                this.leaveValue = a;
                this.values = [a];
            },
            onValuesChangePro(b) {
                this.leaveValuePro = b;
                this.valuesPro = [b];
                for(var i=0;i<this.Pros.length;i++){
                    if(this.valuesPro[0]===this.Pros[i].name){
                        this.processDefId=this.Pros[i].value;
                    }
                }
                console.log(this.processDefId);
            },
            ToCustomer:function () {
                if(this.values[0]==='男'){
                    this.sex=1
                }else if(this.values[0]==='女'){
                    this.sex=2
                }
                console.log(this.sex);
                this.imglist=this.$refs.SrcBase.$data.imgArr;
                var file=[];
                for(var i=0;i<this.imglist.length;i++){
                    file.push(this.imglist[i])
                }
                if(this.username===''||this.phone===''||this.sex===''||this.company===''||this.processDefId===''){
                    alert('请补充完整信息');
                }else {
                axios.post(API_ADD_CUS,qs.stringify({
                    utoken:this.token,
                    file64Base:file,
                    name:this.username,
                    phone:this.phone,
                    intention:this.job,
                    sex:this.sex,
                    age:this.company,
                    processDefId:this.processDefId,
                    remarks:this.remarks
                })).then(res=>{
                    console.log(res);
                    let data=res.data;
                    if(data.result===0){
                        window.history.back(-1);
                    }else if(data.result===2){
                        window.localStorage.clear();
                        this.$router.push('/login');
                        Toast({
                            message: '用户已失效，请重新登陆',
                            duration: 2000
                        });
                    }
                }).catch(err=>{
                    console.log(err);
                });
                }
            }
        }
    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"
    .create-peoples
        position: fixed
        width: 100%
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        z-index 1
        .file{
            width: 80px
            padding: 0 5px
            text-align: center
            color #fff
            position: absolute
            right: 0
            top: 0
            line-height: 50px
        }
        .name{
            margin-top 10px
        }
        .mint-cell-wrapper{
            background-image: none
        }
</style>



