<template>
        <div class="scroller-wrapper">
            <scroller>
                <div class="files-item">
                    <div class="name">营业执照</div>
                    <p class="detail border-1px">{{msg.address}}</p>
                    <div class="img-group">
                       <!-- <div class="img-item">
                            <img :src="msg.license" alt="">
                        </div>-->
                        <div class="img-item">
                            <img src="//fuss10.elemecdn.com/a/2a/5fd158b2803f19742462cd5b786afjpeg.jpeg?imageMogr/format/webp/thumbnail/!130x130r/gravity/Center/crop/130x130/" alt="">
                        </div>
                    </div>
                </div>
            </scroller>
        </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import { mapGetters } from 'vuex';

    export default {
        data() {
            return {
                msg:{},
            }
        },
        computed:{
            ...mapGetters([
                'getCompanyMsg'
            ])
        },
        created() {
            this.msg=this.getCompanyMsg;
            console.log(this.msg);
        },
        components: {
            VHeader
        }

    }
</script>

<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/mixin"
    @import "~common/stylus/variable"
        .files-item
            width: 100%
            padding: 10px
            margin-top: 10px
            background-color: #fff
            .name
                font-size: 16px
            .detail
                padding: 5px 0
                color #777
                border-b(#ccc)
            .img-group
                display: flex
                justify-content space-between
                flex-wrap wrap
                padding-top: 5px
                .img-item
                    width: 25%
                    img
                        width: 100%
        .scroller-wrapper
            width: 100%
            position: absolute
            top: 0px
            bottom: 0
            z-index 99999
</style>



