<template>
    <div class="asset-detail-wrapper">
        <v-header :path="fromUrl" title="资产详情"></v-header>

        <div class="asset-detail-msg">
            <img :src="obj.picPathList[1].picPath" alt="">
            <div class="msg">
                <div class="name">{{obj.name}}</div>
                <div class="state">资产状态：{{obj.useStatus}}</div>
                <div class="date">创建时间：{{obj.addTime}}</div>
            </div>
        </div>
        <div class="detail">
            <div class="title">资产详情</div>
            <mt-cell title="资产名称" :value="obj.name"></mt-cell>
            <mt-cell title="价格" :value="'￥'+obj.price"></mt-cell>
            <mt-cell title="资产状态" :value="obj.useStatus"></mt-cell>
            <mt-cell title="添加人" :value="obj.addName"></mt-cell>
        </div>
        <div class="detail">
            <div class="title">凭证</div>
            <div class="img-group">
                <div class="img-item" v-for="item in obj.picPathList">
                    <img :src="item.picPath" alt="">
                </div>

            </div>
        </div>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import { mapGetters } from 'vuex';

    export default {
        data() {
            return {
                fromUrl: '',
                id:'',
                obj:{}
            }
        },
        beforeRouteEnter (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        created(){
            this.id=this.$route.params.assetid;
            // console.log(this.id);
            // console.log(this.getAssetDetails);
            for(var i=0;i<this.getAssetDetails.length;i++){
                if(this.id===this.getAssetDetails[i].id){
                    this.obj=this.getAssetDetails[i];
                }
            }
            console.log(this.obj);
        },
        computed:{
            ...mapGetters([
                'getAssetDetails'
            ])
        },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"

    .asset-detail-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        z-index: 1
        .asset-detail-msg
            width: 100%
            height: 125px
            padding: 15px
            display: flex
            background-color: $color-black
            img
                width: 95px
                height: 95px
            .msg
                display: flex
                flex-direction column
                justify-content space-around
                flex: 1
                padding-left: 15px
                height: 100%
                color #fff
                .name
                    font-size: 16px
        .detail
            .title
                padding-left: 15px
                line-height: 30px
                background-color: #f8f8f9
            .img-group
                background-color: #fff
                padding:10px
                display: flex
                flex-wrap wrap
                .img-item
                    width: 25%
                    text-align: center
                    img
                        width: 80px
                        height: 80px
                        border-radius 2px
</style>



