<template>
    <div class="bill-detail-wrapper">
        <v-header path="1" :goback="true" title="账单报销"></v-header>
        <div class="bill-detail-msg">
            <div>话费</div>
            <div>2017-10-01</div>
            <div>-￥100.00</div>
        </div>
        <div class="bx">
            <mt-cell title="报销单位" is-link>
                <span>请选择报销单位</span>
            </mt-cell>
            <mt-cell title="账目类别" is-link>
                <span>请选择账目类别</span>
            </mt-cell>
            <mt-field label="商品名" placeholder="请输入商品名" v-model="goodsname"></mt-field>
            <mt-cell title="商品状态" is-link>
                <span>请选择商品状态</span>
            </mt-cell>
            <img-upload style="margin-top: 5px;" title="上传凭证"></img-upload>
        </div>
        <div class="bill-detail-btn">提交</div>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import ImgUpload from 'components/img-upload/img-upload';

	export default {
	    data() {
	        return {
                goodsname: ''
            }
        },
	    components: {
	        VHeader,
            ImgUpload
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    .bill-detail-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index: 1000
        background-color: $color-bg
        .bill-detail-msg
            height: 150px
            color #fff
            background-color: $color-black
        .bx
            width: 100%
            margin-top: 5px
        .bill-detail-btn
            position: absolute
            bottom: 0
            left: 0
            right: 0
            height: 42px
            line-height: 42px
            text-align: center
            color #fff
            background-color: $color-orange
        .mint-cell-text
            font-size: 12px
            color #8e8d8d
</style>



