<template>
    <div class="create-wrapper">
        <v-header :path="fromUrl" title="创建团队"></v-header>
        <mt-field label="团队名称" placeholder="请输入团队真实名称"></mt-field>
        <mt-field label="地区" placeholder="请输入"></mt-field>
        <mt-field label="所在行业" placeholder="请输入"></mt-field>

        <mt-cell title="人员规模" :value="scale" @click.native="popupVisible=true" is-link></mt-cell>
        <mt-popup
            v-model="popupVisible"
            position="bottom">
            <mu-picker :slots="slots" :visible-item-count="5" @change="onValuesChange" :values="values"/>
        </mt-popup>

        <mt-cell title="我的职务" :value="zhiwu" @click.native="propleNum=true" is-link></mt-cell>
        <mt-popup
            v-model="propleNum"
            position="bottom">
            <mu-picker :slots="slotsNum" :visible-item-count="5" @change="onNumChange" :values="MyNum"/>
        </mt-popup>

        <span class="btn-create" @click="BtnCreate">确定创建</span>

    </div>
</template>
<script>
    import VHeader from 'components/header/header';

    export default {
        data() {
            return {
                fromUrl:'',
                popupVisible:false,
                scale:'请选择',
                values: ['1-9'],
                slots: [
                    {
                        flex: 1,
                        values: ['10-20','21-30','31-40','41-50'],
                        textAlign: 'center'
                    }
                ],
                zhiwu:'请选择',
                propleNum:false,
                MyNum:['负责人'],
                slotsNum:[
                    {
                        flex: 1,
                        values: ['负责人','管理层','人事','财务','行政','销售','技术','员工','技术'],
                        textAlign: 'center'
                    }
                ]
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
        computed: {},
        components: {VHeader},
        methods: {
            onValuesChange(a) {
                this.scale = a;
                this.values = [a]
            },
            onNumChange(a){
                this.zhiwu=a;
                this.values=[a];
            },
            BtnCreate:function () {
                this.$router.go(-1);
            },
            change:function () {

            }
        }
    }
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"
    .create-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background-color: $color-bg
        .btn-create
            display block
            width 100%
            text-align center
            background-color #F08619
            position fixed
            bottom:0
            left 0
            color #FFFFFF
            font-size 16px
            padding 5px 0


</style>



