const state = {
    msg: {

    }
};

const getters = {
    getCompanyMsg: state => state.msg
};

const mutations = {
    ['SET_COMPANY_MSG'](state, msg) {
        state.msg = msg;
        // console.log(state.msg);
        // console.log(state.msg.id);
    }
};

export default {
    state,
    getters,
    mutations
}



