const state = {
    bxList: {

    }
};

const getters = {
    getBxList: state => state.bxList
};

const mutations = {
    ['SET_BX_LIST'](state, bxList) {
        state.bxList = bxList;
    }
};

export default {
    state,
    getters,
    mutations
}



