<template>
    <scroller>
    <div class="detail-msg-wrapper">
        <div class="top-msg">

            <div class="detail-date">
                <mu-date-picker class="top-date"
                                :fullWidth="false"
                                :underlineShow="false"
                                v-model="datestart"
                                :maxDate="new Date()"
                                format="YYYY-MM-DD"
                                inputClass="date-i"
                                labelClass="top-date"
                                hintTextClass="top-text"
                                :hintText="dateText"
                                @change="change">
                </mu-date-picker>
                <span>到</span>
                <mu-date-picker class="top-date"
                                :fullWidth="false"
                                :underlineShow="false"
                                v-model="date"
                                :maxDate="new Date()"
                                format="YYYY-MM-DD"
                                inputClass="date-i"
                                labelClass="top-date"
                                hintTextClass="top-text"
                                :hintText="dateTextend"
                                @change="changeEnd">
                </mu-date-picker>
            </div>
            <div class="detail-msg">
                <div>收入总额：<span class="fw">￥{{money.incomeMoney}}</span></div>
            </div>
        </div>
        <div class="detail-chart" ref="detailChart"></div>

        <div class="detail-list">
            <Table :columns="tableData.columns1" :data="tableData.data1"></Table>
        </div>
    </div>
    </scroller>
</template>

<script>
    import echarts from 'echarts';
    import {API_MONEY_INFO} from 'common/js/api';
    import qs from 'qs';
    import axios from 'axios';
    import { Indicator, Toast } from 'mint-ui';

    let getT = () => {
        let date = new Date();
        var month;var num;var date2;var num2;
        if(date.getMonth()+1<10&&date.getDate()>9){
            num=date.getMonth()+1;
            month='0'+num;
            return `${date.getFullYear()}-${month}-${date.getDate()}`;
        }else if(date.getMonth()+1>=10&&date.getDate()<10){
            num2=date.getDate();
            date2='0'+num2;
            return `${date.getFullYear()}-${date.getMonth()+1}-${date2}`;
        }
        else if(date.getMonth()+1<10&&date.getDate()<10){
            num=date.getMonth()+1;
            month='0'+num;
            num2=date.getDate();
            date2='0'+num2;
            return `${date.getFullYear()}-${month}-${date2}`;
        }else if(date.getMonth()+1>=10&&date.getDate()>9){
            return `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
        }
    };
    let getTend=()=>{
        let date = new Date();
        var month;var num;var date2;var num2;
        if(date.getMonth()+1<10&&date.getDate()>9){
            num=date.getMonth()+1;
            month='0'+num;
            return `${date.getFullYear()}-${month}-${date.getDate()}`;
        }else if(date.getMonth()+1>=10&&date.getDate()<10){
            num2=date.getDate();
            date2='0'+num2;
            return `${date.getFullYear()}-${date.getMonth()+1}-${date2}`;
        }
        else if(date.getMonth()+1<10&&date.getDate()<10){
            num=date.getMonth()+1;
            month='0'+num;
            num2=date.getDate();
            date2='0'+num2;
            return `${date.getFullYear()}-${month}-${date2}`;
        }else if(date.getMonth()+1>=10&&date.getDate()>9){
            return `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`;
        }
    };
	export default {
            data() {
            return {
                list:[],
                seriesList:[],
                values:[],
                startDate: getT(),
                endDate: getTend(),
                charts: null,
                date:'',
                datestart:'',
                dateText:getT(),
                dateTextend:getTend(),
                money:{},
                chartOption: {
                            tooltip : {
                                trigger: 'item',
                                formatter: "{a} <br/>{b} : {c} ({d}%)"
                            },
                            series : [
                                {
                                    name: '收入来源',
                                    type: 'pie',
                                    radius : '60%',
                                    center: ['50%', '50%'],
                                    data:this.values,
                                    /*[
                                        /!*{value:0, name:'红包'},
                                        {value:0, name:'彩票'},
                                        {value:0, name:'收彩礼'},
                                        {value:0, name:'股东分红'},
                                        {value:0, name:'工资'}*!/
                                    ],*/
                                    itemStyle: {
                                        emphasis: {
                                            shadowBlur: 10,
                                            shadowOffsetX: 0,
                                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                                        }
                                    },
                                }
                            ],
                            color: ['#f6a933','#65c9eb','#f9cd38','#fe8189','#cca1f0']
                        },
                tableData: {
                            columns1: [
                                {
                                    title: '收入类型',
                                    key: 'inType'
                                },
                                {
                                    title: '金额',
                                    key: 'num'
                                }
                            ],
                            data1: []
                        }
                    }
                },
        created(){
            console.log('进来');
            /* this.chartOption.series[0].data=this.getChart;
             let arr=[];
             for(let i=0;i<this.getChart.length;i++){
                 arr.push({
                     inType:this.getChart[i].name,
                     num:this.getChart[i].value
                 })
             }
             this.tableData.data1=arr;*/
        },
        methods:{
            change(a,b){
                this.datestart=a;
                this.income(this.datestart,this.date);
            },
            changeEnd(c,d){
                this.date=c;
                this.income(this.datestart,this.date);
            },
            judge(){
                if(this.datestart===''&&this.date===''){
                    this.datestart=getT();
                    this.date=getTend();
                    this.income(this.datestart,this.date);
                }else if(this.datestart===''&&this.date!==''){
                    this.datestart=getT();
                    this.income(this.datestart,this.date);
                }else if(this.datestart!==''&&this.date===''){
                    this.date=getTend();
                    this.income(this.datestart,this.date);
                }else if(this.datestart!==''&&this.date!==''){
                    this.income(this.datestart,this.date);
                }
            },
            income(a,b){
                let res=axios.post(API_MONEY_INFO,qs.stringify({
                    utoken:this.$login.getToken(),
                    officeId:this.$route.params.id,
                    type:'1',
                    startDate:a,
                    endDate:b
                })).then(res=>{
                    let data=res.data;
                    if(data.result===0){
                        console.log(res.data);
                        this.money=res.data.data;
                        this.seriesList=res.data.reportList;
                        var list=[];
                        if(this.seriesList.length>0){
                            for(var i=0;i<this.seriesList.length;i++){
                                list.push({
                                    value:this.seriesList[i].value,
                                    name:this.seriesList[i].soure
                                })
                            }
                        }
                        this.values=list;
                        this.chartOption.series[0].data=list;
                        console.log(this.chartOption.series[0].data);
                        let arr=[];
                        console.log(this.seriesList);
                        if(this.seriesList.length>0){
                            for(let i=0;i<this.seriesList.length;i++){
                                arr.push({
                                    inType:this.seriesList[i].soure,
                                    num:this.seriesList[i].value
                                })
                            }
                            this.tableData.data1=arr;
                        }
                    }else if(data.result===2){
                        window.localStorage.clear();
                        this.$router.push('/login');
                        Toast({
                            message: '用户已失效,请重新登陆',
                            duration: 2000
                        });
                    }
                }).catch(err=>{
                    console.log(err);
                })
            },
        },
        mounted() {
            console.log("执行2");
        },
        watch: {
            chartOption:{
                handler:function(val,oldval){
                    console.log('开始');
                    this.charts = echarts.init(this.$refs.detailChart);
                    this.chartOption.series[0].data=this.values;
                    this.charts.setOption(this.chartOption);
                    setTimeout(() => {
                        this.charts.resize()
                    },20);
                },
                deep:true//对象内部的属性监听，也叫深度监听
            },
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    .detail-msg-wrapper
        width: 100%
        height: 100%
        .top-msg
            background-color: #fff
        .detail-date
            height: 16px
            box-sizing content-box
            padding: 10px

        .detail-msg
            padding: 5px 10px 10px
            .fw
                /*font-size: 30px*/
                color $color-orange


        .detail-chart
            margin-top: 10px
            background-color: #fff!important
            height: 155px

    .mu-text-field
        width: 100%
    .mu-text-field-content
        padding: 0
    .mu-text-field
        margin: 0
        line-height: 1
        min-height 100%
    .top-date
        display: inline-block
        vertical-align: top
        width: 125px
        text-align: center
        background-color #e5e5e5
        /*height: 170%*/
        font-size: 0
    .date-i
        width: 100%
        height: 100%
        color #787777
        font-size: 12px
        text-align: center
        line-height: 1

   .mu-text-field-hint
        font-size:12px;
        line-height 14px
</style>



