<template>
    <div class="map-wrapper" id="map">
        <slot></slot>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui';
	export default {
	    data() {
	        return {
                map: null
            }
        },
        props: {
            userStatus: {
                type: String
            },
        },
	    mounted() {
            let that = this
            this.map = new AMap.Map('map',{
                resizeEnable: true,
                zoom: 10,//地图显示的缩放级别
                keyboardEnable: true,
                center: [116.397428, 39.90923]
            });
            AMap.plugin(['AMap.Geolocation','AMap.Autocomplete','AMap.PlaceSearch',"AMap.Driving"],() => {
                // this._getUserLocation();
                this._completeHandler();
            })
        
        },
        methods: {
	        _getUserLocation() {
                let map = this.map;
                let geolocation = new AMap.Geolocation({
                    enableHighAccuracy: true,
                    timeout: 10000,
                    maximumAge: 0,
                    convert: true,
                    showButton: true,
                    buttonPosition: 'LB',
                    buttonOffset: new AMap.Pixel(10, 20),
                    showMarker: true,
                    showCircle: true,
                    panToLocation: true,
                    zoomToAccuracy:true
                });

                let auto = new AMap.Autocomplete({
                    input: 'input'
                });

                let placeSearch = new AMap.PlaceSearch({
                    map: this.map
                })

                map.addControl(geolocation);
                geolocation.getCurrentPosition();
                geolocation.getCityInfo((status, res) => {
                    auto.setCity(res.city)
                });
                AMap.event.addListener(geolocation, 'complete', this._completeHandler);
                AMap.event.addListener(geolocation, 'error', this._errorHandler);
                AMap.event.addListener(auto, 'select', (res) => {
                    placeSearch.setCity(res.poi.adcode);
                    placeSearch.search(res.poi.name);
                })
            },
            _completeHandler() {
                let that = this
                that.$emit('map',{
                    map: that.map
                })

                this.map.on('click', (e)=> {
                    that.$emit('clickMap',{
                        posx: e.lnglat.getLng(),
                        posy: e.lnglat.getLat()
                    })
                })
                
            },
            _errorHandler(data) {
	            console.log(data)
            },
            _search(res) {
                console.log(res)
            },
            //获取到指定商家的位置
            goTo(merchant){
                let that = this
                let geolocation = new AMap.Geolocation({
                    enableHighAccuracy: true,//是否使用高精度定位，默认:true
                    timeout: 10000,          //超过10秒后停止定位，默认：无穷大
                    buttonOffset: new AMap.Pixel(10, 20),//定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
                    zoomToAccuracy: true,  //定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
                    buttonPosition:'RB'
                });
                that.map.addControl(geolocation);
                geolocation.getCurrentPosition();
                //返回定位信息
                AMap.event.addListener(geolocation, 'complete', function(data){
                    console.log(data)
                    let driving = new AMap.Driving({
                        map: that.map
                    })
                    driving.search(
                        data.position,
                        new AMap.LngLat(merchant.locationX,merchant.locationY),
                    );
                        console.log(merchant.locationX,merchant.locationY)
                });
                //返回定位出错信息
                AMap.event.addListener(geolocation, 'error', function(error){
                    Toast('获取您的定位信息失败')
                }); 
            }
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">

.map-wrapper {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 55px;
}
#map {
    height 100%
    width 100%
}
</style>



